import {
  PaddingOption,
  calcBasePosition,
  calcStartPosition,
  getFontSize,
} from '../internal/canvases'
import { calcElapsedColor } from '../internal/color'
import { ColorDef } from '../ts-types'

const { ceil, PI } = Math

function strokeColorsRect(
  ctx: CanvasRenderingContext2D,
  borderColors: [
    ColorDef | null,
    ColorDef | null,
    ColorDef | null,
    ColorDef | null
  ],
  left: number,
  top: number,
  width: number,
  height: number
): void {
  type Position = { x: number; y: number }
  function strokeRectLines(
    positions: [Position, Position, Position, Position, Position]
  ): void {
    for (let i = 0; i < borderColors.length; i++) {
      const color = borderColors[i]
      const preColor = borderColors[i - 1]
      if (color) {
        if (preColor !== color) {
          if (preColor) {
            ctx.strokeStyle = preColor
            ctx.stroke()
          }
          const pos1 = positions[i]
          ctx.beginPath()
          ctx.moveTo(pos1.x, pos1.y)
        }
        const pos2 = positions[i + 1]
        ctx.lineTo(pos2.x, pos2.y)
      } else {
        if (preColor) {
          ctx.strokeStyle = preColor
          ctx.stroke()
        }
      }
    }
    const pColor = borderColors[borderColors.length - 1]
    if (pColor) {
      ctx.strokeStyle = pColor
      ctx.stroke()
    }
  }
  if (
    borderColors[0] === borderColors[1] &&
    borderColors[0] === borderColors[2] &&
    borderColors[0] === borderColors[3]
  ) {
    if (borderColors[0]) {
      ctx.strokeStyle = borderColors[0]
      ctx.strokeRect(left, top, width, height)
    }
  } else {
    strokeRectLines([
      { x: left, y: top },
      { x: left + width, y: top },
      { x: left + width, y: top + height },
      { x: left, y: top + height },
      { x: left, y: top },
    ])
  }
}
function roundRect(
  ctx: CanvasRenderingContext2D,
  left: number,
  top: number,
  width: number,
  height: number,
  radius: number
): void {
  ctx.beginPath()
  ctx.arc(left + radius, top + radius, radius, -PI, -0.5 * PI, false)
  ctx.arc(left + width - radius, top + radius, radius, -0.5 * PI, 0, false)
  ctx.arc(
    left + width - radius,
    top + height - radius,
    radius,
    0,
    0.5 * PI,
    false
  )
  ctx.arc(left + radius, top + height - radius, radius, 0.5 * PI, PI, false)
  ctx.closePath()
}
function fillRoundRect(
  ctx: CanvasRenderingContext2D,
  left: number,
  top: number,
  width: number,
  height: number,
  radius: number
) {
  roundRect(ctx, left, top, width, height, radius)
  ctx.fill()
}
function strokeRoundRect(
  ctx: CanvasRenderingContext2D,
  left: number,
  top: number,
  width: number,
  height: number,
  radius: number
) {
  roundRect(ctx, left, top, width, height, radius)
  ctx.stroke()
}
function fillCircle(
  ctx: CanvasRenderingContext2D,
  left: number,
  top: number,
  width: number,
  height: number
): void {
  const min = Math.min(width, height) / 2
  ctx.beginPath()
  ctx.arc(left + min, top + min, min, 0, 2 * Math.PI)
  ctx.closePath()
  ctx.fill()
}
function strokeCircle(
  ctx: CanvasRenderingContext2D,
  left: number,
  top: number,
  width: number,
  height: number
): void {
  const min = Math.min(width, height) / 2
  ctx.beginPath()
  ctx.arc(left + min, top + min, min, 0, 2 * Math.PI)
  ctx.closePath()
  ctx.stroke()
}

export type FillTextRectOption = {
  offset?: number
  padding?: PaddingOption
}
function fillTextRect(
  ctx: CanvasRenderingContext2D,
  text: string,
  left: number,
  top: number,
  width: number,
  height: number,
  { offset = 2, padding }: FillTextRectOption = {}
): void {
  const rect = {
    bottom: top + height,
    height,
    left,
    right: left + width,
    top,
    width,
  }
  ctx.save()
  try {
    ctx.beginPath()
    ctx.rect(rect.left, rect.top, rect.width, rect.height)
    // clip
    ctx.clip()

    // 文字
    const pos = calcBasePosition(ctx, rect, {
      offset,
      padding,
    })

    ctx.fillText(text, pos.x, pos.y)
  } finally {
    ctx.restore()
  }
}

export type DrawInlineImageRectOption = {
  offset?: number
  padding?: PaddingOption
}
function drawInlineImageRect(
  ctx: CanvasRenderingContext2D,
  image: HTMLImageElement,
  srcLeft: number,
  srcTop: number,
  srcWidth: number,
  srcHeight: number,
  destWidth: number,
  destHeight: number,
  left: number,
  top: number,
  width: number,
  height: number,
  { offset = 2, padding }: DrawInlineImageRectOption = {}
): void {
  const rect = {
    bottom: top + height,
    height,
    left,
    right: left + width,
    top,
    width,
  }
  ctx.save()
  try {
    ctx.beginPath()
    ctx.rect(rect.left, rect.top, rect.width, rect.height)
    // clip
    ctx.clip()

    // 文字
    const pos = calcStartPosition(ctx, rect, destWidth, destHeight, {
      offset,
      padding,
    })

    ctx.drawImage(
      image,
      srcLeft,
      srcTop,
      srcWidth || destWidth,
      srcHeight || destHeight,
      pos.x,
      pos.y,
      destWidth,
      destHeight
    )
  } finally {
    ctx.restore()
  }
}

/**
 * Returns an object containing the width of the checkbox.
 * @param ctx - canvas context
 * @returns Object containing the width of the checkbox
 */
function measureCheckbox(ctx: CanvasRenderingContext2D): { width: number } {
  return {
    width: getFontSize(ctx, null).width,
  }
}

/**
 * Returns an object containing the width of the radio button.
 * @param ctx - canvas context
 * @returns Object containing the width of the radio button
 */
function measureRadioButton(ctx: CanvasRenderingContext2D): { width: number } {
  return {
    width: getFontSize(ctx, null).width,
  }
}

export type DrawCheckboxOption = {
  uncheckBgColor?: ColorDef
  checkBgColor?: ColorDef
  borderColor?: ColorDef
  boxSize?: number
}
/**
 * draw Checkbox
 * @param ctx - canvas context
 * @param x - The x coordinate where to start drawing the checkbox (relative to the canvas)
 * @param y - The y coordinate where to start drawing the checkbox (relative to the canvas)
 * @param check - checkbox check status
 * @param option - option
 * @returns
 */
function drawCheckbox(
  ctx: CanvasRenderingContext2D,
  x: number,
  y: number,
  check: number | boolean,
  {
    uncheckBgColor = '#FFF',
    checkBgColor = 'rgb(76, 73, 72)',
    borderColor = '#000',
    boxSize = measureCheckbox(ctx).width,
  }: DrawCheckboxOption = {}
): void {
  const checkPoint = typeof check === 'number' ? (check > 1 ? 1 : check) : 1

  ctx.save()
  try {
    ctx.fillStyle = check ? checkBgColor : uncheckBgColor
    const leftX = ceil(x)
    const topY = ceil(y)
    const size = ceil(boxSize)

    fillRoundRect(ctx, leftX - 1, topY - 1, size + 1, size + 1, boxSize / 5)
    ctx.lineWidth = 1
    ctx.strokeStyle = borderColor
    strokeRoundRect(ctx, leftX - 0.5, topY - 0.5, size, size, boxSize / 5)

    if (check) {
      ctx.lineWidth = ceil(boxSize / 10)
      ctx.strokeStyle = uncheckBgColor
      let leftWidth = boxSize / 4
      let rightWidth = (boxSize / 2) * 0.9
      const leftLeftPos = x + boxSize * 0.2
      const leftTopPos = y + boxSize / 2

      if (checkPoint < 0.5) {
        leftWidth *= checkPoint * 2
      }

      ctx.beginPath()
      ctx.moveTo(leftLeftPos, leftTopPos)
      ctx.lineTo(leftLeftPos + leftWidth, leftTopPos + leftWidth)
      if (checkPoint > 0.5) {
        if (checkPoint < 1) {
          rightWidth *= (checkPoint - 0.5) * 2
        }
        ctx.lineTo(
          leftLeftPos + leftWidth + rightWidth,
          leftTopPos + leftWidth - rightWidth
        )
      }
      ctx.stroke()
    }
  } finally {
    ctx.restore()
  }
}

export type DrawRadioButtonOption = {
  checkColor?: ColorDef
  borderColor?: ColorDef
  bgColor?: ColorDef
  boxSize?: number
}
/**
 * draw Radio button
 * @param ctx - canvas context
 * @param x - The x coordinate where to start drawing the radio button (relative to the canvas)
 * @param y - The y coordinate where to start drawing the radio button (relative to the canvas)
 * @param check - radio button check status
 * @param option - option
 * @returns
 */
function drawRadioButton(
  ctx: CanvasRenderingContext2D,
  x: number,
  y: number,
  check: number | boolean,
  {
    checkColor = 'rgb(76, 73, 72)',
    borderColor = '#000',
    bgColor = '#FFF',
    boxSize = measureRadioButton(ctx).width,
  }: DrawRadioButtonOption = {}
): void {
  const ratio = typeof check === 'number' ? (check > 1 ? 1 : check) : 1

  ctx.save()
  try {
    ctx.fillStyle = bgColor
    const leftX = ceil(x)
    const topY = ceil(y)
    const size = ceil(boxSize)
    fillCircle(ctx, leftX - 1, topY - 1, size + 1, size + 1)

    ctx.lineWidth = 1
    ctx.strokeStyle = borderColor
    strokeCircle(ctx, leftX - 0.5, topY - 0.5, size, size)
    if (check) {
      const checkSize = (size * ratio) / 2
      const padding = (size - checkSize) / 2
      ctx.fillStyle = checkColor
      fillCircle(
        ctx,
        ceil((leftX - 0.5 + padding) * 100) / 100,
        ceil((topY - 0.5 + padding) * 100) / 100,
        ceil(checkSize * 100) / 100,
        ceil(checkSize * 100) / 100
      )
    }
  } finally {
    ctx.restore()
  }
}

export type DrawSwitchOption = {
  uncheckBgColor?: ColorDef
  checkBgColor?: ColorDef
  borderColor?: ColorDef
  btnSize?: { width: number; height: number }
}
/**
 * draw Switch Button
 * @param ctx - canvas context
 * @param x - The x coordinate where to start drawing the switch (relative to the canvas)
 * @param y - The y coordinate where to start drawing the switch (relative to the canvas)
 * @param check - switch check status
 * @param option - option
 * @returns
 */
function drawSwitchBtn(
  ctx: CanvasRenderingContext2D,
  x: number,
  y: number,
  check: boolean,
  animElapsedTime: number,
  {
    uncheckBgColor = '#FFF',
    checkBgColor = '#3BCD4F',
    borderColor = '#D9D9D9',
    btnSize = { width: 52, height: 32 },
  }: DrawSwitchOption = {}
) {
  animElapsedTime =
    animElapsedTime > 1 ? 1 : animElapsedTime < 0 ? 0 : animElapsedTime
  const borderWidth = btnSize.height * 0.078125

  ctx.save()
  try {
    ctx.fillStyle = check ? checkBgColor : borderColor
    const bX = ceil(x) - 1
    const bY = ceil(y) - 1
    const bW = ceil(btnSize.width + 1)
    const bH = ceil(btnSize.height + 1)
    fillRoundRect(ctx, bX, bY, bW, bH, bH / 2)

    const scale = check ? 1 - animElapsedTime : animElapsedTime
    ctx.fillStyle = uncheckBgColor
    const cW = (bW - borderWidth * 2) * scale
    const cH = (bH - borderWidth * 2) * scale
    const cX = bX + bW / 2 - cW / 2
    const cY = bY + bH / 2 - cH / 2
    fillRoundRect(ctx, cX, cY, cW, cH, cH / 2)

    const offsetScale = check ? animElapsedTime : 1 - animElapsedTime
    ctx.shadowColor = borderColor as string
    ctx.shadowOffsetX = 0
    ctx.shadowOffsetY = 2
    ctx.shadowBlur = 3
    const sW = bH - borderWidth * 2
    const sH = sW
    const sX = bX + borderWidth + (bW - borderWidth * 2 - sW) * offsetScale
    const sY = bY + borderWidth
    fillRoundRect(ctx, sX, sY, sW, sH, sH / 2)
  } finally {
    ctx.restore()
  }
}

function drawSwitchButton(
  ctx: CanvasRenderingContext2D,
  x: number,
  y: number,
  check: boolean,
  animElapsedTime: number,
  {
    uncheckBgColor = '#FFF',
    checkBgColor = '#008CD6',
    borderColor = '#BFBFBF',
    btnSize = { width: 44, height: 22 },
  }: DrawSwitchOption = {}
) {
  animElapsedTime =
    animElapsedTime > 1 ? 1 : animElapsedTime < 0 ? 0 : animElapsedTime
  const borderWidth = (btnSize.height * 2) / 22
  const fontSize = (btnSize.height * 12) / 22
  const textMargin = (btnSize.height * 14) / 22

  ctx.save()
  try {
    let backgroundFillStyle
    let trueFillStyle
    let falseFillStyle
    if (0 < animElapsedTime && animElapsedTime < 1) {
      backgroundFillStyle = check
        ? calcElapsedColor(
            borderColor as string,
            checkBgColor as string,
            animElapsedTime
          )
        : calcElapsedColor(
            checkBgColor as string,
            borderColor as string,
            animElapsedTime
          )
      trueFillStyle = check
        ? calcElapsedColor(
            borderColor as string,
            uncheckBgColor as string,
            animElapsedTime
          )
        : calcElapsedColor(
            uncheckBgColor as string,
            borderColor as string,
            animElapsedTime
          )
      falseFillStyle = check
        ? calcElapsedColor(
            uncheckBgColor as string,
            checkBgColor as string,
            animElapsedTime
          )
        : calcElapsedColor(
            checkBgColor as string,
            uncheckBgColor as string,
            animElapsedTime
          )
    } else {
      backgroundFillStyle = check ? checkBgColor : borderColor
      trueFillStyle = check ? uncheckBgColor : borderColor
      falseFillStyle = check ? checkBgColor : uncheckBgColor
    }
    const bX = x
    const bY = y
    const bW = btnSize.width
    const bH = btnSize.height
    ctx.fillStyle = backgroundFillStyle
    fillRoundRect(ctx, bX, bY, bW, bH, bH / 2)

    ctx.textAlign = 'center'
    ctx.textBaseline = 'middle'
    ctx.font = `${fontSize}px sans-serif`
    ctx.fillStyle = trueFillStyle
    ctx.fillText('是', bX + textMargin, bY + bH / 2)
    ctx.fillStyle = falseFillStyle
    ctx.fillText('否', bX + bW - textMargin, bY + bH / 2)

    const offsetScale = check ? animElapsedTime : 1 - animElapsedTime
    const sW = bH - borderWidth * 2
    const sH = sW
    const sX = bX + borderWidth + (bW - borderWidth * 2 - sW) * offsetScale
    const sY = bY + borderWidth
    ctx.fillStyle = uncheckBgColor
    fillRoundRect(ctx, sX, sY, sW, sH, sH / 2)
  } finally {
    ctx.restore()
  }
}

export type DrawButtonOption = {
  backgroundColor?: ColorDef
  bgColor?: ColorDef
  radius?: number
  shadow?: {
    color?: string
    blur?: number
    offsetX?: number
    offsetY?: number
    offset?: { x?: number; y?: number }
  }
}
function drawButton(
  ctx: CanvasRenderingContext2D,
  left: number,
  top: number,
  width: number,
  height: number,
  option: DrawButtonOption = {}
): void {
  const {
    backgroundColor = '#FFF',
    bgColor = backgroundColor,
    radius = 4,
    shadow = {},
  } = option

  ctx.save()
  try {
    ctx.fillStyle = bgColor

    if (shadow) {
      const {
        color = 'rgba(0, 0, 0, 0.24)',
        blur = 1,
        offsetX = 0,
        offsetY = 2,
        offset: { x: ox = offsetX, y: oy = offsetY } = {},
      } = shadow

      ctx.shadowColor = color
      ctx.shadowBlur = blur // 模糊
      ctx.shadowOffsetX = ox
      ctx.shadowOffsetY = oy
    }

    fillRoundRect(ctx, ceil(left), ceil(top), ceil(width), ceil(height), radius)
  } finally {
    ctx.restore()
  }
}

/**
 * canvasHelper
 */
export const canvasHelper = {
  drawButton,
  drawCheckbox,
  drawInlineImageRect,
  drawRadioButton,
  drawSwitchButton,
  drawSwitchBtn,
  fillRoundRect,
  fillTextRect,
  measureCheckbox,
  measureRadioButton,
  roundRect,
  strokeColorsRect,
  strokeRoundRect,
  fillCircle,
  strokeCircle,
}

export type CanvasHelper = {
  drawButton: typeof drawButton
  drawCheckbox: typeof drawCheckbox
  drawInlineImageRect: typeof drawInlineImageRect
  drawRadioButton: typeof drawRadioButton
  drawSwitchButton: typeof drawSwitchButton
  drawSwitchBtn: typeof drawSwitchBtn
  fillRoundRect: typeof fillRoundRect
  fillTextRect: typeof fillTextRect
  measureCheckbox: typeof measureCheckbox
  measureRadioButton: typeof measureRadioButton
  roundRect: typeof roundRect
  strokeColorsRect: typeof strokeColorsRect
  strokeRoundRect: typeof strokeRoundRect
  fillCircle: typeof fillCircle
  strokeCircle: typeof strokeCircle
}
