import {
  InlineLookupEditorOption,
  ListGridAPI,
  EventListenerId,
  CellAddress,
  LayoutObjectId,
} from '../../ts-types'
import { DG_EVENT_TYPE } from '../../core/draw-grid/DG_EVENT_TYPE'
import { getOrApply, isPromise, isDef, obj } from '../../internal/utils'
import { isDisabledRecord, isReadOnlyRecord } from './action-utils'
import { BaseActionInput } from './BaseActionInput'
import { InlineLookupElement } from './internal/InlineLookupElement'
import { getInlineLookupEditorStateId } from '../../internal/symbolManager'

const INLINE_LOOKUP_EDITOR_STATE_ID = getInlineLookupEditorStateId()

let globalInlineLookupElement: InlineLookupElement<any, any> | null = null
let globalInlineLookupBindGridCount = 0

function attachLookupInput<T, L>(
  grid: ListGridAPI<T>,
  cell: CellAddress,
  editor: InlineLookupEditor<T, L>,
  value: any,
  inputValue?: string
) {
  if (!globalInlineLookupElement) {
    globalInlineLookupElement = new InlineLookupElement()
  }
  const state = (editor as any).getState(grid)
  if (!state.element) {
    state.element = globalInlineLookupElement
    globalInlineLookupBindGridCount++
    grid.addDisposable({
      dispose() {
        globalInlineLookupBindGridCount--
        if (!globalInlineLookupBindGridCount && globalInlineLookupElement) {
          globalInlineLookupElement.dispose()
          globalInlineLookupElement = null
        }
      },
    })
  }

  const records = getOrApply(
    editor.records || [],
    grid.getRowRecord(cell.row) as any
  )
  if (isPromise(records)) {
    records.then((rs) => {
      if (globalInlineLookupElement) {
        globalInlineLookupElement.attach(
          grid,
          editor,
          cell.col,
          cell.row,
          value,
          rs,
          inputValue
        )
      }
    })
  } else {
    globalInlineLookupElement.attach(
      grid,
      editor,
      cell.col,
      cell.row,
      value,
      records,
      inputValue
    )
  }
}
function detachLookupInput(gridFocus?: boolean) {
  if (globalInlineLookupElement) {
    globalInlineLookupElement.detach(gridFocus)
  }
}

function doChangeLookupValue() {
  if (globalInlineLookupElement) {
    globalInlineLookupElement.doChangeValue(true)
  }
}

export class InlineLookupEditor<T, L> extends BaseActionInput<T> {
  private _classList?: string | string[]
  private _records: L[] | ((rec: T) => L[] | Promise<L[]>)
  private _valueField: string
  private _valueType: 'string' | 'number'
  private _captionField: string
  private _filterFields: string | string[]
  private _allowOtherInput: boolean | ((rec: T) => boolean)
  private _disableFilterRecords: boolean
  private _disableFilterSort: boolean
  private _filter?: (lookupRecord: L, record: T) => boolean
  private _dropdownTemplate?: (...values: any[]) => string
  private _dropdownEmptyTemplate?: (...values: any[]) => string
  public constructor(options: InlineLookupEditorOption<T, L> = {}) {
    super(options)
    this.action =
      this.action ||
      ((me) => (
        _record: T,
        data: {
          grid: ListGridAPI<T>
          cell: CellAddress
        }
      ) => {
        if (
          isReadOnlyRecord(me.readOnly, data.grid, data.cell.row) ||
          isDisabledRecord(me.disabled, data.grid, data.cell.row)
        ) {
          return
        }
        me.onOpenCellInternal(data.grid, data.cell)
      })(this)
    this._classList = options.classList
    this._records = options.records || []
    this._valueField = options.valueField || ''
    this._valueType = options.valueType || 'string'
    this._captionField = options.captionField || ''
    this._filterFields = options.filterFields || ''
    this._allowOtherInput = options.allowOtherInput || false
    this._disableFilterRecords = !!options.disableFilterRecords
    this._disableFilterSort = !!options.disableFilterSort

    if (isDef(options.disableDropdown)) {
      this.disableDropdown = !!options.disableDropdown
    }

    this._filter = options.filter
    this._dropdownTemplate = options.dropdownTemplate
    this._dropdownEmptyTemplate = options.dropdownEmptyTemplate
  }
  public get classList(): string[] | undefined {
    if (!this._classList) {
      return undefined
    }
    return Array.isArray(this._classList) ? this._classList : [this._classList]
  }
  public set classList(classList) {
    this._classList = classList
  }
  public get records() {
    return this._records
  }
  public set records(records) {
    this._records = records
  }
  public get valueField() {
    return this._valueField
  }
  public set valueField(valueField) {
    this._valueField = valueField
  }
  public get valueType() {
    return this._valueType
  }
  public set valueType(valueType) {
    this._valueType = valueType
  }
  public get captionField() {
    return this._captionField
  }
  public set captionField(captionField) {
    this._captionField = captionField
  }
  public get filterFields() {
    return this._filterFields
  }
  public set filterFields(filterFields) {
    this._filterFields = filterFields
  }
  public get allowOtherInput() {
    return this._allowOtherInput
  }
  public set allowOtherInput(allowOtherInput) {
    this._allowOtherInput = allowOtherInput || false
  }
  public get disableFilterRecords() {
    return this._disableFilterRecords
  }
  public set disableFilterRecords(disableFilterRecords) {
    this._disableFilterRecords = !!disableFilterRecords
  }
  public get disableFilterSort() {
    return this._disableFilterSort
  }
  public set disableFilterSort(disableFilterSort) {
    this._disableFilterSort = !!disableFilterSort
  }
  public get disableDropdown() {
    window.console.warn(
      "'disableDropdown' is deprecated, please use 'disableInput' instead"
    )
    return !!this.disableInput
  }
  public set disableDropdown(disableDropdown) {
    window.console.warn(
      "'disableDropdown' is deprecated, please use 'disableInput' instead"
    )
    this.disableInput = !!disableDropdown
  }
  public get filter() {
    return this._filter
  }
  public set filter(filter) {
    this._filter = filter
  }
  public get dropdownTemplate() {
    return this._dropdownTemplate
  }
  public set dropdownTemplate(dropdownTemplate) {
    this._dropdownTemplate = dropdownTemplate
  }
  public get dropdownEmptyTemplate() {
    return this._dropdownEmptyTemplate
  }
  public set dropdownEmptyTemplate(dropdownEmptyTemplate) {
    this._dropdownEmptyTemplate = dropdownEmptyTemplate
  }
  public clone(): InlineLookupEditor<T, L> {
    return new InlineLookupEditor(this)
  }
  public onActionInputCellInternal(
    grid: ListGridAPI<T>,
    cell: CellAddress,
    inputValue: string
  ) {
    grid.doGetCellValue(cell.col, cell.row, (value) => {
      attachLookupInput(grid, cell, this, value, inputValue)
    })
  }
  public onActionOpenCellInternal(grid: ListGridAPI<T>, cell: CellAddress) {
    grid.doGetCellValue(cell.col, cell.row, (value) => {
      attachLookupInput(grid, cell, this, value)
    })
  }
  public onChangeSelectCellInternal(
    _grid: ListGridAPI<T>,
    _cell: CellAddress,
    _selected: boolean
  ) {
    // cancel input
    doChangeLookupValue()
    detachLookupInput()
  }
  public onGridScrollInternal(grid: ListGridAPI<T>) {
    // cancel input
    doChangeLookupValue()
    detachLookupInput(true)
  }
  public onChangeDisabledInternal() {
    // cancel input
    detachLookupInput(true)
  }
  public onChangeReadOnlyInternal() {
    // cancel input
    detachLookupInput(true)
  }
  public onSetInputAttrsInternal(
    grid: ListGridAPI<T>,
    _cell: CellAddress,
    input: HTMLInputElement
  ) {
    InlineLookupElement.setInputAttrs(this, grid, input)
  }
  public bindGridEvent(
    grid: ListGridAPI<T>,
    cellId: LayoutObjectId
  ): EventListenerId[] {
    const ids = super.bindGridEvent(grid, cellId)
    const detach = () => {
      doChangeLookupValue()
      detachLookupInput(true)
    }
    ids.push(grid.listen(DG_EVENT_TYPE.CLICK_UNDERLAY, detach))
    ids.push(grid.listen(DG_EVENT_TYPE.DBLCLICK_UNDERLAY, detach))
    ids.push(grid.listen(DG_EVENT_TYPE.DBLTAP_UNDERLAY, detach))
    return ids
  }
  protected getState(grid: any): any {
    if (!(grid as any)[INLINE_LOOKUP_EDITOR_STATE_ID]) {
      obj.setReadonly(grid, INLINE_LOOKUP_EDITOR_STATE_ID, {})
    }
    return grid[INLINE_LOOKUP_EDITOR_STATE_ID]
  }
}
