import * as columnUtils from './columnUtils'
import {
  MultilineTextColumnOption,
  DrawCallback,
  CellContext,
  GridCanvasHelperAPI,
  ListGridAPI,
  CellStyle,
} from '../../ts-types'
import { DrawCellInfo } from '../../ts-types-internal'
import { MultilineTextStyle } from '../style/MultilineTextStyle'
import { BaseColumn } from './BaseColumn'
import { isDef } from '../../internal/utils'
import { getInlineTextareaEditorStateId } from '../../internal/symbolManager'
import Rect from '../../internal/Rect'

const INLINE_TEXTAREA_EDITOR_STATE_ID = getInlineTextareaEditorStateId()

export class MultilineTextColumn<T> extends BaseColumn<T, string> {
  private _draw?: DrawCallback
  private _cellStyle?: CellStyle
  public constructor(options: MultilineTextColumnOption<T> = {}) {
    super(options)
    this._draw = options.draw
    this._cellStyle = options.cellStyle
  }
  public get StyleClass(): typeof MultilineTextStyle {
    return MultilineTextStyle
  }
  public clone(): MultilineTextColumn<T> {
    return new MultilineTextColumn(this)
  }
  public get draw() {
    return this._draw
  }
  public get cellStyle(): CellStyle | undefined {
    return this._cellStyle
  }
  public reviseAttachCellsArea(rect: Rect, row: number, grid: ListGridAPI<T>) {
    columnUtils.reviseAttachCellsArea(rect, row, grid, this._cellStyle)
  }
  public reviseAttachCellsPadding(
    padding: [number, number, number, number],
    row: number,
    grid: ListGridAPI<T>
  ) {
    super.reviseAttachCellsPadding(padding, row, grid)
    columnUtils.reviseAttachCellsPadding(padding, row, grid, this._cellStyle)
  }
  public drawInternal(
    value: string,
    context: CellContext,
    style: MultilineTextStyle,
    helper: GridCanvasHelperAPI,
    grid: ListGridAPI<T>,
    { getIcon, getRecord }: DrawCellInfo<T>
  ): void {
    const {
      textAlign,
      textBaseline,
      color,
      font,
      lineHeight,
      autoWrapText,
      lineClamp,
      textOverflow,
    } = style

    const v = isDef(value) ? value + '' : ''
    const multiline = v.replace(/\r?\n/g, '\n').replace(/\r/g, '\n').split('\n')
    helper.testFontLoad(font, v, context)
    columnUtils.loadIcons(getIcon(), context, helper, (icons, ctx) => {
      const isCustomDraw = columnUtils.customDraw(
        helper,
        this.draw,
        multiline,
        context,
        grid,
        getRecord()
      )
      if (!isCustomDraw) {
        helper.multilineText(multiline, ctx, {
          autoWrapText,
          color,
          font,
          icons,
          lineClamp,
          lineHeight,
          padding: columnUtils.getActionTextPadding(context, helper, style),
          textAlign,
          textBaseline,
          textOverflow,
        })
      }
      columnUtils.drawActionButton(
        grid,
        context,
        INLINE_TEXTAREA_EDITOR_STATE_ID,
        helper,
        style
      )
      columnUtils.drawAttachArea(
        grid,
        getRecord(),
        context,
        helper,
        this._cellStyle
      )
    })
  }
}
