import { EventHandler } from './EventHandler'
import { isNode } from './utils'

const handler = new EventHandler()

let ratio: number

function setRatio() {
  if (isNode) {
    ratio = 1
  } else {
    ratio = Math.ceil(window.devicePixelRatio || 1)
    if (ratio > 1 && ratio % 2 !== 0) {
      ratio += 1
    }
  }
}

setRatio()

if (!isNode) {
  handler.on(window, 'resize', setRatio)
}

export default {
  transform(canvas: HTMLCanvasElement) {
    const ctx = canvas.getContext('2d')

    const getAttribute = canvas.getAttribute
    const setAttribute = canvas.setAttribute
    canvas.getAttribute = ((me) => (name: string) => {
      let result = getAttribute.apply(me, [name]) as any
      if (name === 'width' || name === 'height') {
        result /= ratio
      }
      return result
    })(canvas)
    canvas.setAttribute = ((me) => (name: string, val: string) => {
      const wh = name === 'width' || name === 'height'
      if (wh) {
        val = ratio * Number(val) + ''
      }
      const result = setAttribute.apply(me, [name, val])
      if (wh && ctx) {
        ctx.scale(ratio, ratio)
      }
      return result
    })(canvas)

    Object.defineProperty(canvas, 'width', {
      configurable: true,
      enumerable: true,
      get() {
        return canvas.getAttribute('width')
      },
      set: (val) => {
        canvas.setAttribute('width', Math.floor(val) + '')
      },
    })
    Object.defineProperty(canvas, 'height', {
      configurable: true,
      enumerable: true,
      get() {
        return canvas.getAttribute('height')
      },
      set: (val) => {
        canvas.setAttribute('height', Math.floor(val) + '')
      },
    })
    if (ctx) {
      const drawImage = ctx.drawImage
      ctx.drawImage = ((me) => (img: HTMLCanvasElement, ...args: any[]) => {
        if (img !== canvas || ratio === 1) {
          return drawImage.apply(me, [img].concat(args) as any)
        }
        me.save()
        try {
          me.scale(1 / ratio, 1 / ratio)
          if (args.length > 4) {
            args[4] *= ratio
            args[5] *= ratio
          } else {
            args[0] *= ratio
            args[1] *= ratio
          }
          return drawImage.apply(me, [img].concat(args) as any)
        } finally {
          me.restore()
        }
      })(ctx)
    }
    return canvas
  },
}
