import { ListGridAPI } from '../ts-types'
import { TooltipElement } from './internal/TooltipElement'

export abstract class BaseTooltip<T> {
  private _grid: ListGridAPI<T>
  private _tooltipElement?: TooltipElement<T>
  public constructor(grid: ListGridAPI<T>) {
    this._grid = grid
  }
  public dispose() {
    this.detachTooltipElement()
    if (this._tooltipElement) {
      this._tooltipElement.dispose()
    }
    delete this._tooltipElement
  }
  public attachTooltipElement(col: number, row: number, content: string) {
    const tooltipElement = this._getTooltipElement()
    tooltipElement.attach(this._grid, col, row, content)
  }
  public moveTooltipElement(col: number, row: number) {
    const tooltipElement = this._getTooltipElement()
    tooltipElement.move(this._grid, col, row)
  }
  public detachTooltipElement() {
    const tooltipElement = this._getTooltipElement()
    tooltipElement.detach()
  }
  public abstract createTooltipElementInternal(): TooltipElement<T>
  private _getTooltipElement(): TooltipElement<T> {
    return (
      this._tooltipElement ||
      (this._tooltipElement = this.createTooltipElementInternal())
    )
  }
}
