import {
  InlineInputEditorOption,
  ListGridAPI,
  CellAddress,
} from '../../ts-types'
import { obj } from '../../internal/utils'
import { BaseActionInput } from './BaseActionInput'
import { InlineInputElement } from './internal/InlineInputElement'
import { getInlineInputEditorStateId } from '../../internal/symbolManager'

const INLINE_INPUT_EDITOR_STATE_ID = getInlineInputEditorStateId()

let globalInlineInputElement: InlineInputElement<any> | null = null
let globalInlineInputBindGridCount = 0

function attachInput<T>(
  grid: ListGridAPI<T>,
  cell: CellAddress,
  editor: InlineInputEditor<T>,
  value: any,
  inputValue?: string
) {
  if (!globalInlineInputElement) {
    globalInlineInputElement = new InlineInputElement()
  }
  const state = (editor as any).getState(grid)
  if (!state.element) {
    state.element = globalInlineInputElement
    globalInlineInputBindGridCount++
    grid.addDisposable({
      dispose() {
        globalInlineInputBindGridCount--
        if (!globalInlineInputBindGridCount && globalInlineInputElement) {
          globalInlineInputElement.dispose()
          globalInlineInputElement = null
        }
      },
    })
  }

  globalInlineInputElement.attach(
    grid,
    editor,
    cell.col,
    cell.row,
    value,
    inputValue
  )
}
function detachInput(gridFocus?: boolean) {
  if (globalInlineInputElement) {
    globalInlineInputElement.detach(gridFocus)
  }
}
function doChangeValue<T>(_grid: ListGridAPI<T>) {
  if (globalInlineInputElement) {
    globalInlineInputElement.doChangeValue()
  }
}

export class InlineInputEditor<T> extends BaseActionInput<T> {
  private _classList?: string | string[]
  private _type?: string
  private _enabledMouseWheel: boolean
  public constructor(options: InlineInputEditorOption<T> = {}) {
    super(options)
    this._classList = options.classList
    this._type = options.type
    this._enabledMouseWheel = !!options.enabledMouseWheel
  }
  public get classList(): string[] | undefined {
    if (!this._classList) {
      return undefined
    }
    return Array.isArray(this._classList) ? this._classList : [this._classList]
  }
  public set classList(classList) {
    this._classList = classList
  }
  public get type() {
    return this._type
  }
  public set type(type) {
    this._type = type
  }
  public get enabledMouseWheel() {
    return this._enabledMouseWheel
  }
  public set enabledMouseWheel(enabled) {
    this._enabledMouseWheel = enabled
  }
  public clone(): InlineInputEditor<T> {
    return new InlineInputEditor(this)
  }
  public onActionInputCellInternal(
    grid: ListGridAPI<T>,
    cell: CellAddress,
    inputValue: string
  ): void {
    grid.doGetCellValue(cell.col, cell.row, (value) => {
      attachInput(grid, cell, this, value, inputValue)
    })
  }
  public onActionOpenCellInternal(
    grid: ListGridAPI<T>,
    cell: CellAddress
  ): void {
    grid.doGetCellValue(cell.col, cell.row, (value) => {
      attachInput(grid, cell, this, value)
    })
  }
  public onChangeSelectCellInternal(
    grid: ListGridAPI<T>,
    _cell: CellAddress,
    _selected: boolean
  ): void {
    doChangeValue(grid)
    detachInput()
  }
  public onGridScrollInternal(grid: ListGridAPI<T>) {
    doChangeValue(grid)
    detachInput(true)
  }
  public onChangeDisabledInternal(): void {
    // cancel input
    detachInput(true)
  }
  public onChangeReadOnlyInternal(): void {
    // cancel input
    detachInput(true)
  }
  public onSetInputAttrsInternal(
    grid: ListGridAPI<T>,
    _cell: CellAddress,
    input: HTMLInputElement
  ): void {
    InlineInputElement.setInputAttrs(this, grid, input, 'text')
  }
  protected getState(grid: any): any {
    if (!grid[INLINE_INPUT_EDITOR_STATE_ID]) {
      obj.setReadonly(grid, INLINE_INPUT_EDITOR_STATE_ID, {})
    }
    return grid[INLINE_INPUT_EDITOR_STATE_ID]
  }
}
