//see https://github.com/typekit/webfontloader

type Styles1 = {
  display: 'block'
  position: 'absolute'
  top: '-9999px'
  left: '-9999px'
  width: 'auto'
  height: 'auto'
  margin: '0'
  padding: '0'
  'white-space': 'nowrap'
  font: string
}
type Styles2 = {
  'font-variant': 'normal'
  'font-size': '300px'
  'font-style': 'normal'
  'font-weight': '400'
  'line-height': 'normal'
}

type StyleKeys = keyof Styles1 | keyof Styles2

function computeStyle(font: string): [Styles1, Styles2] {
  return [
    {
      display: 'block',
      position: 'absolute',
      top: '-9999px',
      left: '-9999px',
      width: 'auto',
      height: 'auto',
      margin: '0',
      padding: '0',
      'white-space': 'nowrap',
      font,
    },
    {
      'font-variant': 'normal',
      'font-size': '300px',
      'font-style': 'normal',
      'font-weight': '400',
      'line-height': 'normal',
    },
  ]
}

class FontRuler {
  private _el: HTMLSpanElement
  constructor(font: string, testStr: string) {
    const e = document.createElement('span')
    e.setAttribute('aria-hidden', 'true')
    e.textContent = testStr || 'BESbswy'

    computeStyle(font).forEach((style: Styles1 | Styles2) => {
      for (const k in style) {
        const key = k as StyleKeys
        e.style[key as any] = (style as any)[key]
      }
    })
    document.body.appendChild(e)
    this._el = e
  }
  getWidth(): number {
    return this._el.offsetWidth
  }
  remove(): void {
    document.body.removeChild(this._el)
  }
}

export default FontRuler
