import { obj } from '../../internal/utils'
import { BaseActionInput } from './BaseActionInput'
import { InlineTextAreaElement } from './internal/InlineTextAreaElement'
import { getInlineTextareaEditorStateId } from '../../internal/symbolManager'
import {
  InlineTextAreaEditorOption,
  ListGridAPI,
  CellAddress,
} from '../../ts-types'

const INLINE_TEXTAREA_EDITOR_STATE_ID = getInlineTextareaEditorStateId()

let globalInlineTextAreaElement: InlineTextAreaElement<any> | null = null
let globalInlineInputBindGridCount = 0

function attachInput<T>(
  grid: ListGridAPI<T>,
  cell: CellAddress,
  editor: InlineTextAreaEditor<T>,
  value: any,
  inputValue?: string
) {
  if (!globalInlineTextAreaElement) {
    globalInlineTextAreaElement = new InlineTextAreaElement()
  }
  const state = (editor as any).getState(grid)
  if (!state.element) {
    state.element = globalInlineTextAreaElement
    globalInlineInputBindGridCount++
    grid.addDisposable({
      dispose() {
        globalInlineInputBindGridCount--
        if (!globalInlineInputBindGridCount && globalInlineTextAreaElement) {
          globalInlineTextAreaElement.dispose()
          globalInlineTextAreaElement = null
        }
      },
    })
  }

  globalInlineTextAreaElement.attach(
    grid,
    editor,
    cell.col,
    cell.row,
    value,
    inputValue
  )
}
function detachInput(gridFocus?: boolean) {
  if (globalInlineTextAreaElement) {
    globalInlineTextAreaElement.detach(gridFocus)
  }
}
function doChangeValue<T>(grid: ListGridAPI<T>) {
  if (globalInlineTextAreaElement) {
    globalInlineTextAreaElement.doChangeValue()
  }
}

export class InlineTextAreaEditor<T> extends BaseActionInput<T> {
  private _classList: string | string[]
  public constructor(options: InlineTextAreaEditorOption<T> = {}) {
    super(options)
    this._classList = options.classList || ''
  }
  public get classList() {
    return (
      this._classList &&
      (Array.isArray(this._classList) ? this._classList : [this._classList])
    )
  }
  public set classList(classList) {
    this._classList = classList
  }
  public clone(): InlineTextAreaEditor<T> {
    return new InlineTextAreaEditor(this)
  }
  public onActionInputCellInternal(
    grid: ListGridAPI<T>,
    cell: CellAddress,
    inputValue: string
  ) {
    grid.doGetCellValue(cell.col, cell.row, (value) => {
      attachInput(grid, cell, this, value, inputValue)
    })
  }
  public onActionOpenCellInternal(grid: ListGridAPI<T>, cell: CellAddress) {
    grid.doGetCellValue(cell.col, cell.row, (value) => {
      attachInput(grid, cell, this, value)
    })
  }
  public onChangeSelectCellInternal(
    grid: ListGridAPI<T>,
    cell: CellAddress,
    selected: any
  ) {
    doChangeValue(grid)
    detachInput()
  }
  public onGridScrollInternal(grid: ListGridAPI<T>) {
    doChangeValue(grid)
    detachInput(true)
  }
  public onChangeDisabledInternal() {
    // cancel input
    detachInput(true)
  }
  public onChangeReadOnlyInternal() {
    // cancel input
    detachInput(true)
  }
  public onSetInputAttrsInternal(
    grid: ListGridAPI<T>,
    _cell: CellAddress,
    input: HTMLInputElement
  ) {
    InlineTextAreaElement.setInputAttrs(this, grid, input)
  }
  protected getState(grid: any): any {
    if (!(grid as any)[INLINE_TEXTAREA_EDITOR_STATE_ID]) {
      obj.setReadonly(grid, INLINE_TEXTAREA_EDITOR_STATE_ID, {})
    }
    return grid[INLINE_TEXTAREA_EDITOR_STATE_ID]
  }
}
