import { AnyListener, EventListenerId } from '../ts-types'
import { each, isDef } from '../internal/utils'

import { getEventTargetSymbol } from '../internal/symbolManager'
const _ = getEventTargetSymbol()

let eventTargetNextId = 1

/**
 * event target.
 */
export class EventTarget {
  private [_]: {
    listeners: { [type: string]: AnyListener[] }
    listenerData: {
      [id: number]: {
        type: string
        listener: AnyListener
        remove: () => void
      }
    }
  } = {
    listeners: {},
    listenerData: {},
  }
  /**
   * Adds an event listener.
   * @param type - The event type id.
   * @param listener - Callback method.
   * @returns unique id for the listener.
   */
  public listen(type: string, listener: AnyListener): EventListenerId {
    const list = this[_].listeners[type] || (this[_].listeners[type] = [])
    list.push(listener)

    const id = eventTargetNextId++
    this[_].listenerData[id] = {
      listener,
      remove: () => {
        delete this[_].listenerData[id]
        const index = list.indexOf(listener)
        list.splice(index, 1)
        if (!this[_].listeners[type].length) {
          delete this[_].listeners[type]
        }
      },
      type,
    }
    return id
  }
  /**
   * Removes an event listener which was added with listen() by the id returned by listen().
   * @param id - the id returned by listen().
   * @returns
   */
  public unlisten(id: EventListenerId): void {
    if (!this[_]) {
      return
    }
    this[_].listenerData[id].remove()
  }
  /**
   * Adds an event listener.
   * @param type - The event type id.
   * @param listener - Callback method.
   * @returns
   */
  public addEventListener(type: string, listener: AnyListener): void {
    this.listen(type, listener)
  }
  /**
   * Removes an event listener.
   * @param type - The event type id.
   * @param listener - Callback method.
   * @returns
   */
  public removeEventListener(type: string, listener: AnyListener): void {
    if (!this[_]) {
      return
    }
    each(this[_].listenerData, (obj, id) => {
      if (obj.type === type && obj.listener === listener) {
        this.unlisten(id as number)
      }
    })
  }
  /**
   * @param type - The event type id.
   * @returns
   */
  public hasListeners(type: string): boolean {
    return !!this[_].listeners[type]
  }
  /**
   * Fires all registered listeners
   * @param type - The type of the listeners to fire.
   * @param args - fire arguments
   * @returns the result of the last listener
   */
  public fireListeners(type: string, ...args: any[]): any[] {
    const list = this[_].listeners[type]
    if (!list) {
      return []
    }
    return list.map((listener) => listener.call(this, ...args)).filter(isDef)
  }
  /**
   * @returns
   */
  public dispose(): void {}
}
