import { DataSource } from './DataSource'

enum aggregateType {
  count,
  sum,
  avg,
  max,
  min,
}

interface IPivotBase {
  field: string
}

interface IPivotColumn extends IPivotBase {
  aggregate: aggregateType
}

interface IPivotRow extends IPivotBase {}

interface IPivotCol extends IPivotBase {}

interface IPivotDataSourceOptions {
  rowPivots: IPivotRow[]
  colPivots: IPivotCol[]
  columns: IPivotColumn[]
}

interface IPivotKeysMap<T> {
  [rowKey: string]: {
    [colKey: string]: T[]
  }
}

interface IPivotDistinctMap {
  [field: string]: string[]
}

interface IPivotDistinctKeysMap {
  [field: string]: {
    [value: string]: boolean
  }
}

function _getPivotRecordKey<T>(
  record: T,
  pivots: IPivotBase[],
  distinctKeys: IPivotDistinctKeysMap
) {
  const keys = []
  for (const i of pivots) {
    const field = i.field
    const value = JSON.stringify((record as any)[i.field]) || ''
    distinctKeys[field] = distinctKeys[field] || {}
    distinctKeys[field][value] = true
    keys.push(value)
  }
  return keys.join('_')
}

/**
 * grid data source for pivot
 */
export class PivotDataSource<T> extends DataSource<T> {
  public static get EVENT_TYPE() {
    return DataSource.EVENT_TYPE
  }
  private _dataSource: DataSource<T>
  private _options: IPivotDataSourceOptions
  private _records: T[] = []
  private _keysMap: IPivotKeysMap<T> = {}
  private _distinctMap: IPivotDistinctMap = {}
  public constructor(
    dataSource: DataSource<T>,
    options: IPivotDataSourceOptions
  ) {
    super(dataSource)
    this._dataSource = dataSource

    this._options = options

    this.refresh()
  }
  public get options() {
    return this._options
  }
  protected getOriginal(index: number) {
    return this._records[index]
  }
  public refresh() {
    this._keysMap = {}
    const distinctKeys: IPivotDistinctKeysMap = {}
    const rowPivots = this._options.rowPivots || []
    const colPivots = this._options.colPivots || []
    const begin = new Date()
    for (let i = 0; i < this._dataSource.length; i++) {
      const record = this._dataSource.get(i)
      const rowKey = _getPivotRecordKey(record, rowPivots, distinctKeys)
      const colKey = _getPivotRecordKey(record, colPivots, distinctKeys)
      this._keysMap[rowKey] = this._keysMap[rowKey] || {}
      this._keysMap[rowKey][colKey] = this._keysMap[rowKey][colKey] || []
      this._keysMap[rowKey][colKey].push(record as any)
    }
    this._distinctMap = {}
    for (const field in distinctKeys) {
      if (distinctKeys.hasOwnProperty(field)) {
        this._distinctMap[field] = []
        const keys = distinctKeys[field]
        for (const key in keys) {
          if (keys.hasOwnProperty(key)) {
            this._distinctMap[field].push(key)
          }
        }
      }
    }
    const end = new Date()
    const s = Number(end) - Number(begin)
    // TODO: data.ts PivotDataSource.ts
    window.console.log(s)
    this._records = [] // 显示记录列表
    this.length = this._records.length
  }
}
