import {
  ScrollableAPI,
  KeyboardEventListener,
  KeydownEvent,
  EventListenerId,
} from '../../ts-types'
import {
  KEY_TAB,
  KEY_ALPHA_C,
  KEY_ALPHA_V,
  KEY_DOWN,
  KEY_END,
  KEY_HOME,
  KEY_LEFT,
  KEY_RIGHT,
  KEY_UP,
  KEY_ENTER,
  KEY_ALPHA_A,
  KEY_DEL,
  KEY_BS,
} from '../../core/draw-grid/KEY_CODE'
import { EventHandler } from '../../internal/EventHandler'
import Rect from '../../internal/Rect'
import style from '../../internal/style'
import {
  event,
  browser,
  isDescendantElement,
  isDef,
  array,
} from '../../internal/utils'
import { DrawGrid } from '../DrawGrid'
import { EventTarget } from '../EventTarget'

function _setSafeInputValue(input: HTMLInputElement, value: string) {
  const type = input.type
  input.type = ''
  input.value = value
  if (type) {
    input.type = type
  }
}

/**
 * Manage focus
 */
export class FocusControl extends EventTarget {
  private _grid: DrawGrid
  private _scrollable: ScrollableAPI
  private _handler: EventHandler
  private _input: HTMLInputElement
  private _isComposition: boolean = false
  private _compositionEnd: NodeJS.Timeout | undefined
  private _inputStatus?: { [key: string]: string }
  private _keyDownMoveCallback?: KeyboardEventListener
  public constructor(
    grid: DrawGrid,
    parentElement: HTMLElement,
    scrollable: ScrollableAPI
  ) {
    super()
    this._grid = grid
    this._scrollable = scrollable
    const handler = (this._handler = new EventHandler())
    const input = (this._input = document.createElement('input'))
    input.classList.add('grid-focus-control')
    input.readOnly = true
    parentElement.appendChild(input)

    handler.on(input, 'compositionstart', (e) => {
      input.classList.add('composition')
      input.style.font = grid.font || '16px sans-serif'
      this._isComposition = true
      if (this._compositionEnd) {
        clearTimeout(this._compositionEnd)
        delete this._compositionEnd
      }
      grid.focus()
    })
    let lastInputValue: any
    const inputClear = () => {
      lastInputValue = input.value
      if (this._isComposition) {
        return
      }
      if (lastInputValue !== '') {
        _setSafeInputValue(input, '')
      }
    }

    const handleCompositionEnd = () => {
      this._isComposition = false
      input.classList.remove('composition')
      input.style.font = ''
      const value = input.value

      inputClear()

      if (!input.readOnly) {
        this.fireListeners('input', value)
      }

      if (this._compositionEnd) {
        clearTimeout(this._compositionEnd)
        delete this._compositionEnd
      }
    }
    handler.on(input, 'compositionend', (e) => {
      this._compositionEnd = setTimeout(handleCompositionEnd, 0)
    })
    handler.on(input, 'keypress', (e) => {
      if (this._isComposition) {
        return
      }
      if (!input.readOnly && e.key && e.key.length === 1) {
        if (e.ctrlKey || e.metaKey) {
          if (e.key === 'c') {
            // copy! for Firefox & Safari
          } else if (e.key === 'v') {
            // paste! for Firefox & Safari
          } else if (e.key === 'x') {
            // cut! for Firefox & Safari
          }
        } else {
          if (e.key === ' ') {
            // Since the full-width space cannot be determined, it is processed by "input".
            return
          }
          this.fireListeners('input', e.key)
          event.cancel(e)
        }
      }
      inputClear()
    })
    handler.on(input, 'keydown', (e) => {
      if (this._isComposition) {
        if (this._compositionEnd) {
          handleCompositionEnd()
          event.cancel(e)
        }
        return
      }
      const keyCode = event.getKeyCode(e)
      let stopCellMove = false
      const evt: KeydownEvent = {
        keyCode,
        event: e,
        stopCellMoving() {
          stopCellMove = true
        },
      }
      const results = this.fireListeners('keydown', evt)
      if (array.findIndex(results, (v) => v === false) >= 0) {
        return
      }

      if (!input.readOnly && lastInputValue && browser.Safari) {
        // for Safari
        this.fireListeners('input', lastInputValue)
      }
      if (!stopCellMove) this.fireKeyDownMove(keyCode, e)
      if (
        this._grid.keyboardOptions?.deleteCellValueOnDel &&
        (keyCode === KEY_DEL || keyCode === KEY_BS)
      ) {
        this.fireListeners('delete', e)
      }

      inputClear()
    })
    handler.on(input, 'keyup', (e) => {
      if (this._isComposition) {
        if (this._compositionEnd) {
          handleCompositionEnd()
        }
      }
      inputClear()
    })

    handler.on(input, 'input', (e: InputEvent) => {
      if (e.data === ' ' || e.data === '　') {
        // Since the full-width space cannot be determined on "keypress", it is processed by "input".
        this.fireListeners('input', e.data)
      }
      inputClear()
    })
    if (browser.IE) {
      handler.on(document, 'keydown', (e) => {
        if (e.target !== input) {
          return
        }
        const keyCode = event.getKeyCode(e)
        if (keyCode === KEY_ALPHA_C && e.ctrlKey) {
          // When text is not selected copy-event is not emit, on IE.
          _setSafeInputValue(input, 'dummy')
          input.select()
          setTimeout(() => {
            _setSafeInputValue(input, '')
          }, 100)
        } else if (keyCode === KEY_ALPHA_V && e.ctrlKey) {
          // When input is read-only paste-event is not emit, on IE.
          if (input.readOnly) {
            input.readOnly = false
            setTimeout(() => {
              input.readOnly = true
              _setSafeInputValue(input, '')
            }, 10)
          }
        }
      })
    }
    if (browser.Edge) {
      handler.once(document, 'keydown', (e: Event) => {
        if (!isDescendantElement(parentElement, e.target as HTMLElement)) {
          return
        }
        // When the input has focus on the first page opening,
        // the paste-event and copy-event is not emit, on Edge.
        const dummyInput = document.createElement('input')
        grid.getElement().appendChild(dummyInput)
        dummyInput.focus()
        input.focus()
        if (dummyInput.parentElement) {
          dummyInput.parentElement.removeChild(dummyInput)
        }
      })
    }
    handler.on(document, 'paste', (e: Event) => {
      if (this._isComposition) {
        return
      }
      if (!isDescendantElement(parentElement, e.target as HTMLElement)) {
        return
      }
      setTimeout(() => {
        inputClear()
      }, 100)
      let pasteText
      if (browser.IE) {
        // IE
        pasteText = (window as any).clipboardData.getData('Text')
      } else {
        const clipboardData = (e as ClipboardEvent).clipboardData
        if (clipboardData) {
          if (clipboardData.items) {
            // Chrome & Firefox & Edge
            pasteText = clipboardData.getData('text/plain')
          } else {
            // Safari
            if (
              -1 !==
              Array.prototype.indexOf.call(clipboardData.types, 'text/plain')
            ) {
              pasteText = clipboardData.getData('Text')
            }
          }
        }
      }
      if (isDef(pasteText) && pasteText.length) {
        this.fireListeners('paste', { value: pasteText, event: e })
      }
    })
    const copy = (data: any, e: ClipboardEvent) => {
      if (isDef(data)) {
        event.cancel(e)
        if (browser.IE) {
          const cData = (window as any).clipboardData
          if (cData) {
            cData.setData('Text', data) // IE
          }
        } else {
          const cData = e.clipboardData
          if (cData) {
            cData.setData('text/plain', data) // Chrome, Firefox
          }
        }
      }
    }
    handler.on(document, 'copy', (e: Event) => {
      if (this._isComposition) {
        return
      }
      if (!isDescendantElement(parentElement, e.target as HTMLElement)) {
        return
      }
      _setSafeInputValue(input, '')
      const data = array.find(this.fireListeners('copy', e), isDef)
      copy(data, e as ClipboardEvent)
    })
    handler.on(document, 'cut', (e: Event) => {
      if (this._isComposition) {
        return
      }
      if (!isDescendantElement(parentElement, e.target as HTMLElement)) {
        return
      }
      _setSafeInputValue(input, '')
      const data = array.find(this.fireListeners('cut', e), isDef)
      copy(data, e as ClipboardEvent)
    })
    handler.on(input, 'focus', (e) => {
      this.fireListeners('focus', e)
    })
    handler.on(input, 'blur', (e) => {
      this.fireListeners('blur', e)
    })
  }
  public fireKeyDownMove(keyCode: number, e: KeyboardEvent): void {
    const fn = this._keyDownMoveCallback
    if (!fn) {
      return
    }
    if (this._isComposition) {
      return
    }
    if (
      keyCode === KEY_LEFT ||
      keyCode === KEY_UP ||
      keyCode === KEY_RIGHT ||
      keyCode === KEY_DOWN ||
      keyCode === KEY_HOME ||
      keyCode === KEY_END
    ) {
      fn(e)
    } else if (
      this._grid.keyboardOptions?.moveCellOnTab &&
      keyCode === KEY_TAB
    ) {
      fn(e)
    } else if (
      this._grid.keyboardOptions?.moveCellOnEnter &&
      keyCode === KEY_ENTER
    ) {
      fn(e)
    } else if (
      this._grid.keyboardOptions?.selectAllOnCtrlA &&
      keyCode === KEY_ALPHA_A &&
      (e.ctrlKey || e.metaKey)
    ) {
      fn(e)
    }
  }
  public onKeyDownMove(fn: KeyboardEventListener): void {
    this._keyDownMoveCallback = fn
  }
  public onKeyDown(fn: (e: KeydownEvent) => void): EventListenerId {
    return this.listen('keydown', fn)
  }
  public onInput(fn: (value: string) => void): EventListenerId {
    return this.listen('input', fn)
  }
  public onDelete(fn: (e: KeyboardEvent) => void): EventListenerId {
    return this.listen('delete', fn)
  }
  public onCopy(fn: (e: ClipboardEvent) => void): EventListenerId {
    return this.listen('copy', fn)
  }
  public onPaste(
    fn: (e: { value: string; event: ClipboardEvent }) => void
  ): EventListenerId {
    return this.listen('paste', fn)
  }
  public onCut(fn: (e: ClipboardEvent) => void): EventListenerId {
    return this.listen('cut', fn)
  }
  public onFocus(fn: (e: FocusEvent) => void): EventListenerId {
    return this.listen('focus', fn)
  }
  public onBlur(fn: (e: FocusEvent) => void): EventListenerId {
    return this.listen('blur', fn)
  }
  public focus() {
    // this._input.value = "";
    this._input.focus()
  }
  public setFocusRect(rect: Rect) {
    const input = this._input
    const top = this._scrollable.calcTop(rect.top)
    // 位置：相对 但是，如果IE不是position：relative，则不能使用最大值
    input.style.top = `${(top - style.getScrollBarSize()).toFixed()}px`
    input.style.left = `${rect.left.toFixed()}px`
    input.style.width = `${rect.width.toFixed()}px`
    input.style.height = `${rect.height.toFixed()}px`
  }
  public get editMode() {
    return !this._input.readOnly
  }
  public set editMode(editMode) {
    this._input.readOnly = !editMode
  }
  public resetInputStatus() {
    const el = this._input
    if (!el.classList.contains('grid-focus-control--stored-status')) {
      return
    }
    const composition = el.classList.contains('composition')

    const attrs = el.attributes
    const removeNames = []
    for (let i = 0, n = attrs.length; i < n; i++) {
      const att = attrs[i]
      if (!this._inputStatus?.hasOwnProperty(att.nodeName)) {
        removeNames.push(att.name)
      }
    }
    removeNames.forEach((removeName) => {
      el.removeAttribute(removeName)
    })
    for (const name in this._inputStatus) {
      if (this._inputStatus.hasOwnProperty(name)) {
        el.setAttribute(name, this._inputStatus[name])
      }
    }
    if (composition) {
      el.classList.add('composition')
      el.style.font = this._grid.font || '16px sans-serif'
    } else {
      el.classList.remove('composition')
    }
    el.classList.remove('grid-focus-control--stored-status')
  }
  public storeInputStatus() {
    const el = this._input
    if (el.classList.contains('grid-focus-control--stored-status')) {
      return
    }
    const inputStatus: FocusControl['_inputStatus'] = (this._inputStatus = {})
    const atts = el.attributes
    for (let i = 0, n = atts.length; i < n; i++) {
      const att = atts[i]
      inputStatus[att.name] = att.value
    }
    el.classList.add('grid-focus-control--stored-status')
  }
  public setDefaultInputStatus() {
    // 有一种情况，滚动逐渐逐渐变化，所以不要在这里设置。
    // this._input.style.font = this._grid.font || "16px sans-serif";
  }
  public get input() {
    return this._input
  }
  public dispose() {
    super.dispose()
    this._handler.dispose()
  }
}
