import {
  EditorOption,
  EventListenerId,
  CellAddress,
  LayoutObjectId,
} from '../../ts-types'
import { GridInternal, CheckColumnState } from '../../ts-types-internal'
import { DG_EVENT_TYPE } from '../../core/draw-grid/DG_EVENT_TYPE'
import { KEY_ENTER, KEY_SPACE } from '../../core/draw-grid/KEY_CODE'
import { animate } from '../../internal/animate'
import { isPromise, event, cellEquals } from '../../internal/utils'
import { isDisabledRecord, isReadOnlyRecord, toggleValue } from './action-utils'
import { bindCellClickAction, bindCellKeyAction } from './actionBind'
import { Editor } from './Editor'

export abstract class BaseCheckEditor<T> extends Editor<T> {
  public constructor(options: EditorOption<T> = {}) {
    super(options)
  }
  public bindGridEvent(
    grid: GridInternal<T>,
    cellId: LayoutObjectId
  ): EventListenerId[] {
    const state = this.getState(grid)

    const action = (cell: CellAddress) => {
      const range = grid.getCellRange(cell.col, cell.row)
      const cellKey = `${range.start.col}:${range.start.row}`
      if (
        isReadOnlyRecord(this.readOnly, grid, cell.row) ||
        isDisabledRecord(this.disabled, grid, cell.row) ||
        state.block[cellKey]
      ) {
        return
      }
      const ret = grid.doChangeValue(cell.col, cell.row, toggleValue)
      if (ret) {
        const onChange = () => {
          // checkbox animation
          animate(200, (point) => {
            if (point === 1) {
              delete state.elapsed[cellKey]
            } else {
              state.elapsed[cellKey] = point
            }
            grid.invalidateCellRange(range)
          })
        }
        if (isPromise(ret)) {
          state.block[cellKey] = true
          ret.then(() => {
            delete state.block[cellKey]
            onChange()
          })
        } else {
          onChange()
        }
      }
    }
    function isTarget(col: number, row: number): boolean {
      return grid.getLayoutCellId(col, row) === cellId
    }
    return [
      ...bindCellClickAction(grid, cellId, {
        action,
        mouseOver: (e) => {
          if (isDisabledRecord(this.disabled, grid, e.row)) {
            return false
          }
          state.mouseActiveCell = {
            col: e.col,
            row: e.row,
          }
          const range = grid.getCellRange(e.col, e.row)
          grid.invalidateCellRange(range)
          return true
        },
        mouseOut: (e) => {
          delete state.mouseActiveCell
          const range = grid.getCellRange(e.col, e.row)
          grid.invalidateCellRange(range)
        },
      }),
      ...bindCellKeyAction(grid, cellId, {
        action: (e) => {
          const selRange = grid.selection.range
          const col = grid.selection.select.col
          for (let row = selRange.start.row; row <= selRange.end.row; row++) {
            if (!isTarget(col, row)) {
              continue
            }
            action({
              col,
              row,
            })
          }
        },
        acceptKeys: [KEY_ENTER, KEY_SPACE],
      }),
      // paste value
      grid.listen(DG_EVENT_TYPE.PASTE_CELL, (e) => {
        if (e.multi) {
          // ignore multi cell values
          return
        }
        const selectionRange = grid.selection.range
        if (!cellEquals(selectionRange.start, selectionRange.end)) {
          // ignore multi paste values
          return
        }
        if (!isTarget(e.col, e.row)) {
          return
        }
        const pasteValue = e.normalizeValue.trim()
        grid.doGetCellValue(e.col, e.row, (value) => {
          const newValue = toggleValue(value)
          if (`${newValue}`.trim() === pasteValue) {
            event.cancel(e.event)

            action({
              col: e.col,
              row: e.row,
            })
          }
        })
      }),
    ]
  }
  public onPasteCellRangeBox(
    grid: GridInternal<T>,
    cell: CellAddress,
    value: string
  ): void {
    if (
      isReadOnlyRecord(this.readOnly, grid, cell.row) ||
      isDisabledRecord(this.disabled, grid, cell.row)
    ) {
      return
    }
    const pasteValue = value.trim()
    grid.doGetCellValue(cell.col, cell.row, (value) => {
      const newValue = toggleValue(value)
      if (`${newValue}`.trim() === pasteValue) {
        grid.doChangeValue(cell.col, cell.row, toggleValue)
      }
    })
  }
  public onDeleteCellRangeBox(): void {
    // noop
  }
  protected abstract getState(grid: GridInternal<T>): CheckColumnState
}
