import { DrawStyleOption, Appearance } from '../../ts-types'
import { BaseStyle } from './BaseStyle'

let defaultDrawStyle: DrawStyle

export class DrawStyle extends BaseStyle {
  private _appearance: Appearance
  public static get DEFAULT(): DrawStyle {
    return defaultDrawStyle
      ? defaultDrawStyle
      : (defaultDrawStyle = new DrawStyle())
  }
  public constructor(style: DrawStyleOption = {}) {
    super(style)
    this._appearance = style.appearance || 'none'
  }
  public get appearance(): Appearance {
    return this._appearance
  }
  public set appearance(appearance) {
    this._appearance = appearance
    this.doChangeStyle()
  }
  public clone(): DrawStyle {
    return new DrawStyle(this)
  }
}
