import { AnyFunction, EventListenerId } from '../ts-types'
import { EventTarget as CustomEventTarget } from '../core/EventTarget'
import { each } from './utils'

let eventHandlerNextId = 1

type EventHandlerTarget = EventTarget | CustomEventTarget
type Listener = AnyFunction
type EventListenerObject = {
  target: EventHandlerTarget
  type: string
  listener: Listener
  options: any[]
}

export class EventHandler {
  private _listeners: {
    [key: string]: EventListenerObject
  } = {}
  public on<TYPE extends keyof GlobalEventHandlersEventMap>(
    target: EventHandlerTarget,
    type: TYPE,
    listener: (event: GlobalEventHandlersEventMap[TYPE]) => any,
    ...options: any[]
  ): EventListenerId
  public on(
    target: EventHandlerTarget,
    type: string,
    listener: Listener,
    ...options: any[]
  ): EventListenerId
  public on(
    target: EventHandlerTarget,
    type: string,
    listener: Listener,
    ...options: any[]
  ): EventListenerId {
    if (target.addEventListener) {
      target.addEventListener(type, listener, ...(options as []))
    }
    const obj = {
      target,
      type,
      listener,
      options,
    }
    const id = eventHandlerNextId++
    this._listeners[id] = obj
    return id
  }
  public once<TYPE extends keyof GlobalEventHandlersEventMap>(
    target: EventHandlerTarget,
    type: TYPE,
    listener: (event: GlobalEventHandlersEventMap[TYPE]) => any,
    ...options: any[]
  ): EventListenerId
  public once(
    target: EventHandlerTarget,
    type: string,
    listener: Listener,
    ...options: (boolean | AddEventListenerOptions)[]
  ): EventListenerId
  public once(
    target: EventHandlerTarget,
    type: string,
    listener: Listener,
    ...options: (boolean | AddEventListenerOptions)[]
  ): EventListenerId {
    const id = this.on(
      target,
      type,
      (...args) => {
        this.off(id)
        listener(...args)
      },
      ...options
    )
    return id
  }
  public tryWithOffEvents(
    target: EventHandlerTarget,
    type: string,
    call: () => void
  ): void {
    const list: EventListenerObject[] = []
    try {
      each(this._listeners, (obj) => {
        if (obj.target === target && obj.type === type) {
          if (obj.target.removeEventListener) {
            obj.target.removeEventListener(
              obj.type,
              obj.listener,
              ...(obj.options as [])
            )
          }
          list.push(obj)
        }
      })
      call()
    } finally {
      list.forEach((obj) => {
        if (obj.target.addEventListener) {
          obj.target.addEventListener(
            obj.type,
            obj.listener,
            ...(obj.options as [])
          )
        }
      })
    }
  }
  public off(id: EventListenerId | null | undefined): void {
    if (id == null) {
      return
    }
    const obj = this._listeners[id]
    if (!obj) {
      return
    }
    delete this._listeners[id]
    if (obj.target.removeEventListener) {
      obj.target.removeEventListener(
        obj.type,
        obj.listener,
        ...(obj.options as [])
      )
    }
  }
  public fire(target: EventTarget, type: string, ...args: any[]): void {
    each(this._listeners, (obj) => {
      if (obj.target === target && obj.type === type) {
        obj.listener.call(obj.target, ...args)
      }
    })
  }
  public hasListener(target: EventTarget, type: string): boolean {
    let result = false
    each(this._listeners, (obj) => {
      if (obj.target === target && obj.type === type) {
        result = true
      }
    })
    return result
  }
  public clear(): void {
    each(this._listeners, (obj) => {
      if (obj.target.removeEventListener) {
        obj.target.removeEventListener(
          obj.type,
          obj.listener,
          ...(obj.options as [])
        )
      }
    })
    this._listeners = {}
  }
  public dispose(): void {
    this.clear()
  }
}
