import { HeaderStyleOption } from '../ts-types'
import { BaseStyle } from './style/BaseStyle'
import { BaseStdStyle } from './style/BaseStdStyle'
import { BaseCheckStyle } from './style/BaseCheckStyle'
import { CheckHeaderStyle } from './style/CheckHeaderStyle'
import { SortHeaderStyle } from './style/SortHeaderStyle'
import { SwitchHeaderStyle } from './style/SwitchHeaderStyle'
import { MultilineTextHeaderStyle } from './style/MultilineTextHeaderStyle'
import { Style } from './style/Style'

export {
  BaseStyle,
  BaseStdStyle,
  BaseCheckStyle,
  Style,
  SortHeaderStyle,
  CheckHeaderStyle,
  SwitchHeaderStyle,
  MultilineTextHeaderStyle,
}

export function of<T>(
  headerStyle: HeaderStyleOption | null | undefined,
  headerValues: T,
  StyleClass: typeof BaseStyle
): BaseStyle {
  if (headerStyle) {
    if (headerStyle instanceof BaseStyle) {
      return headerStyle
    } else if (typeof headerStyle === 'function') {
      return of((headerStyle as any)(headerValues), headerValues, StyleClass)
    } else if (typeof headerStyle === 'string') {
      if (headerValues && headerStyle in headerValues) {
        return of((headerValues as any)[headerStyle], headerValues, StyleClass)
      } else {
        return new StyleClass({})
      }
    } else {
      return new StyleClass(headerStyle)
    }
  } else {
    return StyleClass.DEFAULT
  }
}
