import {
  BaseActionOption,
  ListGridAPI,
  EventListenerId,
  LayoutObjectId,
  CellAddress,
} from '../../ts-types'

export abstract class BaseAction<T> {
  protected _disabled: boolean | ((record: T) => boolean)
  public constructor(options: BaseActionOption<T> = {}) {
    this._disabled = options.disabled || false
  }
  public get disabled(): boolean | ((record: T) => boolean) {
    return this._disabled
  }
  public set disabled(disabled) {
    if (this._disabled !== disabled) {
      this._disabled = disabled
      this.onChangeDisabledInternal()
    }
  }
  public abstract get editable(): boolean
  public abstract clone(): BaseAction<T>
  public abstract bindGridEvent(
    grid: ListGridAPI<T>,
    cellId: LayoutObjectId
  ): EventListenerId[]

  public abstract onPasteCellRangeBox(
    grid: ListGridAPI<T>,
    cell: CellAddress,
    value: string
  ): void
  public abstract onDeleteCellRangeBox(
    grid: ListGridAPI<T>,
    cell: CellAddress
  ): void
  protected onChangeDisabledInternal(): void {
    // nothing
  }
}
