import { ColorDef, BaseCheckStyleOption } from '../../ts-types'
import { BaseStdStyle } from './BaseStdStyle'
import { defaults } from '../../internal/utils'

let defaultBaseCheckStyle: BaseCheckStyle

export class BaseCheckStyle extends BaseStdStyle {
  public static get DEFAULT(): BaseCheckStyle {
    return defaultBaseCheckStyle
      ? defaultBaseCheckStyle
      : (defaultBaseCheckStyle = new BaseCheckStyle())
  }
  private _uncheckBgColor?: ColorDef
  private _checkBgColor?: ColorDef
  private _borderColor?: ColorDef
  public constructor(style: BaseCheckStyleOption = {}) {
    super(defaults(style, { textAlign: 'center' }))
    const { uncheckBgColor, checkBgColor, borderColor } = style
    this._uncheckBgColor = uncheckBgColor
    this._checkBgColor = checkBgColor
    this._borderColor = borderColor
  }
  public get uncheckBgColor(): ColorDef | undefined {
    return this._uncheckBgColor
  }
  public set uncheckBgColor(uncheckBgColor) {
    this._uncheckBgColor = uncheckBgColor
    this.doChangeStyle()
  }
  public get checkBgColor(): ColorDef | undefined {
    return this._checkBgColor
  }
  public set checkBgColor(checkBgColor) {
    this._checkBgColor = checkBgColor
    this.doChangeStyle()
  }
  public get borderColor(): ColorDef | undefined {
    return this._borderColor
  }
  public set borderColor(borderColor) {
    this._borderColor = borderColor
    this.doChangeStyle()
  }
  public clone(): BaseCheckStyle {
    return new BaseCheckStyle(this)
  }
}
