import { AnyFunction, ColorDef } from '../ts-types'
import { calcStartPosition } from '../internal/canvases'
import { path2DManager } from '../internal/path2DManager'
import { InlineDrawOption, Inline } from './Inline'

export type InlinePath2DConstructorOption = {
  path: Path2D | string
  width: number
  height: number
  color?: ColorDef
}

export class InlinePath2D extends Inline {
  private _path: Path2D
  private _width: number
  private _height: number
  private _color?: ColorDef
  public constructor({
    path,
    width,
    height,
    color,
  }: InlinePath2DConstructorOption) {
    super()
    // Path2D的Polyfill不会反映在IE中，除非是这个时间
    const Path2D = path2DManager.getPath2D()
    this._path = new Path2D(path)
    this._width = width
    this._height = height
    this._color = color
  }
  public width(_arg: { ctx: CanvasRenderingContext2D }): number {
    return this._width
  }
  public font(): string | null {
    return null
  }
  public color(): ColorDef | null {
    return this._color ?? null
  }
  public canDraw(): boolean {
    return true
  }
  public onReady(_callback: AnyFunction): void {}
  public draw({
    ctx,
    canvasHelper,
    rect,
    offset,
    offsetLeft,
    offsetRight,
    offsetTop,
    offsetBottom,
  }: InlineDrawOption): void {
    offset++
    const padding = {
      bottom: offsetBottom,
      left: offsetLeft,
      right: offsetRight,
      top: offsetTop,
    }
    ctx.save()
    try {
      ctx.beginPath()
      ctx.rect(rect.left, rect.top, rect.width, rect.height)
      // clip
      ctx.clip()

      const pos = calcStartPosition(ctx, rect, this._width, this._height, {
        offset,
        padding,
      })
      ctx.translate(pos.x, pos.y)
      ctx.fill(this._path)
    } finally {
      ctx.restore()
    }
  }
  public canBreak(): boolean {
    return false
  }
  public toString(): string {
    return ''
  }
}
