import { RectProps, ColorDef, AnyFunction, InlineAPI } from '../ts-types'
import { GenWordsResult, str, isDef } from '../internal/utils'
import { CanvasHelper } from '../tools/canvasHelper'

function getWidth(ctx: CanvasRenderingContext2D, content: string): number {
  return ctx.measureText(content).width
}

function breakWidth(
  ctx: CanvasRenderingContext2D,
  content: string,
  itr: GenWordsResult,
  candidateIndex: number,
  width: number
): {
  before: Inline | null
  after: Inline | null
} {
  const chars = []
  let ret = itr.next()
  for (let i = 0; i < candidateIndex && ret !== null; i++, ret = itr.next()) {
    chars.push(ret)
  }
  let beforeWidth = getWidth(ctx, chars.join(''))
  if (beforeWidth > width) {
    while (chars.length) {
      const c = chars.pop()
      beforeWidth -= getWidth(ctx, c as string)
      if (beforeWidth <= width) {
        break
      }
    }
  } else if (beforeWidth < width) {
    while (ret !== null) {
      const charWidth = getWidth(ctx, ret)
      if (beforeWidth + charWidth > width) {
        break
      }
      chars.push(ret)
      beforeWidth += charWidth
      ret = itr.next()
    }
  }
  const beforeContent = chars.join('').replace(/\s+$/, '')
  const afterContent = content.slice(beforeContent.length).replace(/^\s+/, '')
  return {
    after: afterContent ? new Inline(afterContent) : null,
    before: beforeContent ? new Inline(beforeContent) : null,
  }
}

export type InlineDrawOption = {
  ctx: CanvasRenderingContext2D
  canvasHelper: CanvasHelper
  rect: RectProps
  offset: number
  offsetLeft: number
  offsetRight: number
  offsetTop: number
  offsetBottom: number
}

export class Inline implements InlineAPI {
  private _content: string
  public constructor(content?: string) {
    this._content = isDef(content) ? String(content) : ''
  }
  public width(obj: { ctx: CanvasRenderingContext2D }): number {
    return getWidth(obj.ctx, this._content)
  }
  public font(): string | null {
    return null
  }
  public color(): ColorDef | null {
    return null
  }
  public canDraw(): boolean {
    return true
  }
  public onReady(_callback: AnyFunction): void {}
  public draw({
    ctx,
    canvasHelper,
    rect,
    offset,
    offsetLeft,
    offsetRight,
    offsetTop,
    offsetBottom,
  }: InlineDrawOption): void {
    canvasHelper.fillTextRect(
      ctx,
      this._content,
      rect.left,
      rect.top,
      rect.width,
      rect.height,
      {
        offset: offset + 1,
        padding: {
          bottom: offsetBottom,
          left: offsetLeft,
          right: offsetRight,
          top: offsetTop,
        },
      }
    )
  }
  public canBreak(): boolean {
    return !!this._content
  }
  public splitIndex(
    index: number
  ): {
    before: Inline | null
    after: Inline | null
  } {
    const content = this._content
    const itr = str.genChars(content)
    const chars = []
    let ret = itr.next()
    for (let i = 0; i < index && ret !== null; i++, ret = itr.next()) {
      chars.push(ret)
    }
    const beforeContent = chars.join('')
    const afterContent = content.slice(beforeContent.length)
    return {
      after: afterContent ? new Inline(afterContent) : null,
      before: beforeContent ? new Inline(beforeContent) : null,
    }
  }
  public breakWord(
    ctx: CanvasRenderingContext2D,
    width: number
  ): {
    before: Inline | null
    after: Inline | null
  } {
    const content = this._content
    const allWidth = this.width({ ctx })
    const candidate = Math.floor((this._content.length * width) / allWidth)
    const itr = str.genWords(content)
    return breakWidth(ctx, content, itr, candidate, width)
  }
  public breakAll(
    ctx: CanvasRenderingContext2D,
    width: number
  ): {
    before: Inline | null
    after: Inline | null
  } {
    const content = this._content
    const allWidth = this.width({ ctx })
    const candidate = Math.floor((this._content.length * width) / allWidth)
    const itr = str.genChars(content)
    return breakWidth(ctx, content, itr, candidate, width)
  }
  public toString(): string {
    return this._content
  }
}
