import {
  GridCanvasHelperAPI,
  CellContext,
  InlineAPI,
  InlineCheckOption,
} from '../../ts-types'
import {
  GridInternal,
  DrawCellInfo,
  CheckHeaderState,
} from '../../ts-types-internal'
import { isDef } from '../../internal/utils'
import { BaseHeader } from './BaseHeader'
import { BaseCheckStyle } from '../style/BaseCheckStyle'

export abstract class BaseCheckHeader<T> extends BaseHeader<T> {
  public drawInternal(
    value: string,
    context: CellContext,
    style: BaseCheckStyle,
    helper: GridCanvasHelperAPI,
    grid: GridInternal<T>,
    { drawCellBase }: DrawCellInfo<T>
  ): void {
    const {
      textAlign,
      textBaseline,
      borderColor,
      checkBgColor,
      uncheckBgColor,
      bgColor,
      color,
      font,
      textOverflow,
    } = style

    if (bgColor) {
      drawCellBase({
        bgColor,
      })
    }

    const { col, row } = context
    const range = grid.getCellRange(col, row)
    const cellKey = `${range.start.col}:${range.start.row}`
    const state = this.getState(grid)
    const elapsed = state?.elapsed[cellKey]

    const checked = grid.getHeaderValue(range.start.col, range.start.row)

    const opt: InlineCheckOption = {
      textAlign,
      textBaseline,
      borderColor,
      checkBgColor,
      uncheckBgColor,
    }
    if (isDef(elapsed)) {
      opt.animElapsedTime = elapsed
    }
    const inlineCheck = this.getInlineCheck(helper, !!checked, context, opt)

    helper.text([inlineCheck, value], context, {
      color,
      font,
      textAlign,
      textBaseline,
      textOverflow,
    })
  }
  protected abstract getState(grid: GridInternal<T>): CheckHeaderState
  protected abstract getInlineCheck(
    helper: GridCanvasHelperAPI,
    checked: boolean,
    context: CellContext,
    opt: InlineCheckOption
  ): InlineAPI
}
