import {
  ListGridAPI,
  CellContext,
  GridCanvasHelperAPI,
  MessageObject,
  ColumnStyle,
} from '../../ts-types'
import { DrawCellInfo } from '../../ts-types-internal'
import { MessageElement } from './internal/MessageElement'

export abstract class BaseMessage<T> {
  private _grid: ListGridAPI<T>
  private _messageElement: MessageElement | undefined
  public constructor(grid: ListGridAPI<T>) {
    this._grid = grid
  }
  public dispose() {
    this.detachMessageElement()
    if (this._messageElement) {
      this._messageElement.dispose()
    }
    delete this._messageElement
  }
  abstract createMessageElementInternal(): MessageElement
  public drawCellMessageInternal(
    message: MessageObject,
    context: CellContext,
    style: ColumnStyle,
    helper: GridCanvasHelperAPI,
    grid: ListGridAPI<T>,
    info: DrawCellInfo<T>
  ): void {
    // nothing
  }
  public attachMessageElement(col: number, row: number, message: any) {
    const messageElement = this._getMessageElement()
    messageElement.attach(this._grid, col, row, message)
  }
  public moveMessageElement(col: number, row: number) {
    const messageElement = this._getMessageElement()
    messageElement.move(this._grid, col, row)
  }
  public detachMessageElement() {
    const messageElement = this._getMessageElement()
    messageElement.detach()
  }
  public drawCellMessage(
    message: MessageObject,
    context: CellContext,
    style: ColumnStyle,
    helper: GridCanvasHelperAPI,
    grid: ListGridAPI<T>,
    info: DrawCellInfo<T>
  ) {
    this.drawCellMessageInternal(message, context, style, helper, grid, info)
  }
  private _getMessageElement() {
    return (
      this._messageElement ||
      (this._messageElement = this.createMessageElementInternal())
    )
  }
}
