import { IconStyleOption } from '../../ts-types'
import { Style } from './Style'
import { defaults } from '../../internal/utils'

let defaultIconStyle: IconStyle

export class IconStyle extends Style {
  public static get DEFAULT() {
    return defaultIconStyle
      ? defaultIconStyle
      : (defaultIconStyle = new IconStyle())
  }
  public constructor(style: IconStyleOption = {}) {
    super(defaults(style, { textAlign: 'center' }))
  }
  public clone(): IconStyle {
    return new IconStyle(this)
  }
}
