import { icons } from '../../internal/icons'
import { CellContext, GridCanvasHelperAPI } from '../../ts-types'

export default {
  loadIcons<T>(
    icon: any,
    context: CellContext,
    helper: GridCanvasHelperAPI,
    callback: (icon: any, context: CellContext) => void
  ) {
    if (icon) {
      const iconList = icons.toNormarizeArray(icon)
      iconList.forEach((i: any) => {
        helper.testFontLoad(i.font, i.content, context)
      })
      icon = iconList
    }
    callback(icon, context)
  },
}
