import { BranchGraphStyleOption, ColorDef } from '../../ts-types'
import { BaseStyle } from './BaseStyle'

let defaultBranchGraphStyle: BranchGraphStyle

const DEFAULT_BRANCH_COLORS = (_name: string, index: number): string => {
  switch (index % 3) {
    case 0:
      return '#979797'
    case 1:
      return '#008fb5'
    case 2:
      return '#f1c109'
    default:
  }
  return '#979797'
}

export class BranchGraphStyle extends BaseStyle {
  private _branchColors: ColorDef | ((name: string, index: number) => ColorDef)
  private _margin: number
  private _circleSize: number
  private _branchLineWidth: number
  private _mergeStyle: 'straight' | 'bezier'
  public static get DEFAULT(): BranchGraphStyle {
    return defaultBranchGraphStyle
      ? defaultBranchGraphStyle
      : (defaultBranchGraphStyle = new BranchGraphStyle())
  }
  public constructor(style: BranchGraphStyleOption = {}) {
    super(style)
    this._branchColors = style.branchColors || DEFAULT_BRANCH_COLORS
    this._margin = style.margin || 4
    this._circleSize = style.circleSize || 16
    this._branchLineWidth = style.branchLineWidth || 4
    this._mergeStyle = style.mergeStyle === 'straight' ? 'straight' : 'bezier'
  }
  public get branchColors():
    | ColorDef
    | ((name: string, index: number) => ColorDef) {
    return this._branchColors
  }
  public set branchColors(branchColors) {
    this._branchColors = branchColors
    this.doChangeStyle()
  }
  public get margin(): number {
    return this._margin
  }
  public set margin(margin) {
    this._margin = margin
    this.doChangeStyle()
  }
  public get circleSize(): number {
    return this._circleSize
  }
  public set circleSize(circleSize) {
    this._circleSize = circleSize
    this.doChangeStyle()
  }
  public get branchLineWidth(): number {
    return this._branchLineWidth
  }
  public set branchLineWidth(branchLineWidth) {
    this._branchLineWidth = branchLineWidth
    this.doChangeStyle()
  }
  public get mergeStyle(): 'straight' | 'bezier' {
    return this._mergeStyle
  }
  public set mergeStyle(mergeStyle) {
    this._mergeStyle = mergeStyle
    this.doChangeStyle()
  }
  public clone(): BranchGraphStyle {
    return new BranchGraphStyle(this)
  }
}
