import { MultilineTextHeaderStyleOption } from '../../ts-types'
import { Style } from './Style'

let defaultMultilineTextStyle: MultilineTextHeaderStyle
export class MultilineTextHeaderStyle extends Style {
  static get DEFAULT(): MultilineTextHeaderStyle {
    return defaultMultilineTextStyle
      ? defaultMultilineTextStyle
      : (defaultMultilineTextStyle = new MultilineTextHeaderStyle())
  }
  constructor(style: MultilineTextHeaderStyleOption = {}) {
    super(style)
  }
  public clone(): MultilineTextHeaderStyle {
    return new MultilineTextHeaderStyle(this)
  }
}
