import { BaseStyle } from './BaseStyle'
import { BaseStdStyleOption } from '../../ts-types'

let defaultHeaderBaseStdStyle: BaseStdStyle

export class BaseStdStyle extends BaseStyle {
  public static get DEFAULT(): BaseStdStyle {
    return defaultHeaderBaseStdStyle
      ? defaultHeaderBaseStdStyle
      : (defaultHeaderBaseStdStyle = new BaseStdStyle())
  }
  private _textAlign: CanvasTextAlign
  private _textBaseline: CanvasTextBaseline
  public constructor(style: BaseStdStyleOption = {}) {
    super(style)
    this._textAlign = style.textAlign || 'left'
    this._textBaseline = style.textBaseline || 'middle'
  }
  public get textAlign(): CanvasTextAlign {
    return this._textAlign
  }
  public set textAlign(textAlign) {
    this._textAlign = textAlign
    this.doChangeStyle()
  }
  public get textBaseline(): CanvasTextBaseline {
    return this._textBaseline
  }
  public set textBaseline(textBaseline) {
    this._textBaseline = textBaseline
    this.doChangeStyle()
  }
  public clone(): BaseStdStyle {
    return new BaseStdStyle(this)
  }
}
