import { getChainSafe } from '../internal/utils'
import { getThemeSymbol } from '../internal/symbolManager'
import {
  ColorPropertyDefine,
  ColorsPropertyDefine,
  PartialThemeDefine,
  ThemeDefine,
  RequiredThemeDefine,
  StylePropertyFunctionArg,
} from '../ts-types'
//private symbol
const _ = getThemeSymbol()

function getProp<T extends ColorPropertyDefine | ColorsPropertyDefine | number>(
  obj: PartialThemeDefine,
  superObj: ThemeDefine,
  names: string[],
  defNames?: string[]
): T {
  return (
    getChainSafe(obj, ...names) ||
    getChainSafe(superObj, ...names) ||
    (defNames && getChainSafe(obj, ...defNames)) ||
    (defNames && getChainSafe(superObj, ...defNames))
  )
}

export class Theme implements RequiredThemeDefine {
  private [_]: {
    obj: PartialThemeDefine
    superTheme: ThemeDefine
  }
  private _checkbox: RequiredThemeDefine['checkbox'] | null = null
  private _radioButton: RequiredThemeDefine['radioButton'] | null = null
  private _button: RequiredThemeDefine['button'] | null = null
  private _header: RequiredThemeDefine['header'] | null = null
  private _switch: RequiredThemeDefine['switch'] | null = null
  private _tree: RequiredThemeDefine['tree'] | null = null
  public constructor(obj: ThemeDefine)
  public constructor(obj: PartialThemeDefine, superTheme: ThemeDefine)
  public constructor(
    obj: PartialThemeDefine | ThemeDefine,
    superTheme?: ThemeDefine
  ) {
    this[_] = {
      obj,
      superTheme: superTheme as ThemeDefine,
    }
  }
  public get underlayBackgroundColor(): string {
    const obj = this[_].obj
    const superTheme = this[_].superTheme
    return getProp(obj, superTheme, ['underlayBackgroundColor'])
  }
  // font
  public get font(): string {
    const obj = this[_].obj
    const superTheme = this[_].superTheme
    return getProp(obj, superTheme, ['font'])
  }
  public get frozenRowsFont(): string {
    const obj = this[_].obj
    const superTheme = this[_].superTheme
    return getProp(obj, superTheme, ['frozenRowsFont'])
  }
  // color
  public get color(): ColorPropertyDefine {
    const obj = this[_].obj
    const superTheme = this[_].superTheme
    return getProp(obj, superTheme, ['color'])
  }
  public get frozenRowsColor(): ColorPropertyDefine {
    const obj = this[_].obj
    const superTheme = this[_].superTheme
    return getProp(obj, superTheme, ['frozenRowsColor'], ['color'])
  }
  // background
  public get defaultBgColor(): ColorPropertyDefine {
    const obj = this[_].obj
    const superTheme = this[_].superTheme
    return getProp(obj, superTheme, ['defaultBgColor'])
  }
  public get frozenRowsBgColor(): ColorPropertyDefine {
    const obj = this[_].obj
    const superTheme = this[_].superTheme
    return getProp(obj, superTheme, ['frozenRowsBgColor'], ['defaultBgColor'])
  }
  public get focusBgColor(): ColorPropertyDefine {
    const obj = this[_].obj
    const superTheme = this[_].superTheme
    return getProp(obj, superTheme, ['focusBgColor'])
  }
  public get selectionBgColor(): ColorPropertyDefine {
    const obj = this[_].obj
    const superTheme = this[_].superTheme
    return getProp(obj, superTheme, ['selectionBgColor'], ['defaultBgColor'])
  }
  public get selectionDragBgColor(): ColorPropertyDefine {
    const obj = this[_].obj
    const superTheme = this[_].superTheme
    return getProp(
      obj,
      superTheme,
      ['selectionDragBgColor'],
      ['selectionBgColor']
    )
  }
  public get highlightBgColor(): ColorPropertyDefine {
    if (this.hasProperty(['highlightBgColor'])) {
      const { obj, superTheme } = this[_]
      return getProp(obj, superTheme, ['highlightBgColor'])
    }
    return (args: StylePropertyFunctionArg): any => {
      const color =
        args.row < args.grid.frozenRowCount
          ? this.frozenRowsBgColor
          : this.defaultBgColor
      if (typeof color === 'function') {
        return color(args)
      }
      return color
    }
  }
  // border
  public get borderColor(): ColorsPropertyDefine {
    const obj = this[_].obj
    const superTheme = this[_].superTheme
    return getProp(obj, superTheme, ['borderColor'])
  }
  public get frozenRowsBorderColor(): ColorsPropertyDefine {
    const obj = this[_].obj
    const superTheme = this[_].superTheme
    return getProp(obj, superTheme, ['frozenRowsBorderColor'], ['borderColor'])
  }
  public get highlightBorderColor(): ColorsPropertyDefine {
    const obj = this[_].obj
    const superTheme = this[_].superTheme
    return getProp(obj, superTheme, ['highlightBorderColor'], ['borderColor'])
  }
  // grid border
  public get gridBorderColor(): string {
    const obj = this[_].obj
    const superTheme = this[_].superTheme
    return getProp(obj, superTheme, ['gridBorderColor'])
  }
  public get gridBorderWidth(): number {
    const obj = this[_].obj
    const superTheme = this[_].superTheme
    return getProp(obj, superTheme, ['gridBorderWidth'])
  }
  // size
  public get defaultRowHeight(): number {
    const obj = this[_].obj
    const superTheme = this[_].superTheme
    return getProp(obj, superTheme, ['defaultRowHeight'])
  }
  public get defaultColWidth(): number {
    const obj = this[_].obj
    const superTheme = this[_].superTheme
    return getProp(obj, superTheme, ['defaultColWidth'])
  }
  public get highlightBorderWidth(): number {
    const obj = this[_].obj
    const superTheme = this[_].superTheme
    return getProp(obj, superTheme, ['highlightBorderWidth'])
  }
  // other
  public get checkbox(): RequiredThemeDefine['checkbox'] {
    const obj = this[_].obj
    const superTheme = this[_].superTheme
    return (
      this._checkbox ||
      (this._checkbox = {
        get uncheckBgColor(): ColorPropertyDefine {
          return getProp(
            obj,
            superTheme,
            ['checkbox', 'uncheckBgColor'],
            ['defaultBgColor']
          )
        },
        get checkBgColor(): ColorPropertyDefine {
          return getProp(
            obj,
            superTheme,
            ['checkbox', 'checkBgColor'],
            ['color']
          )
        },
        get borderColor(): ColorPropertyDefine {
          return getProp(obj, superTheme, ['checkbox', 'borderColor'])
        },
      })
    )
  }
  public get radioButton(): RequiredThemeDefine['radioButton'] {
    const { obj, superTheme } = this[_]
    return (
      this._radioButton ||
      (this._radioButton = {
        get checkColor(): ColorPropertyDefine {
          return getProp(
            obj,
            superTheme,
            ['radioButton', 'checkColor'],
            ['color']
          )
        },
        get uncheckBorderColor(): ColorPropertyDefine {
          return getProp(
            obj,
            superTheme,
            ['radioButton', 'uncheckBorderColor'],
            ['borderColor']
          )
        },
        get checkBorderColor(): ColorPropertyDefine {
          return getProp(
            obj,
            superTheme,
            ['radioButton', 'checkBorderColor'],
            ['borderColor']
          )
        },
        get uncheckBgColor(): ColorPropertyDefine {
          return getProp(
            obj,
            superTheme,
            ['radioButton', 'uncheckBgColor'],
            ['defaultBgColor']
          )
        },
        get checkBgColor(): ColorPropertyDefine {
          return getProp(
            obj,
            superTheme,
            ['radioButton', 'checkBgColor'],
            ['defaultBgColor']
          )
        },
      })
    )
  }
  public get button(): RequiredThemeDefine['button'] {
    const obj = this[_].obj
    const superTheme = this[_].superTheme
    return (
      this._button ||
      (this._button = {
        get color(): ColorPropertyDefine {
          return getProp(obj, superTheme, ['button', 'color'], ['color'])
        },
        get bgColor(): ColorPropertyDefine {
          return getProp(
            obj,
            superTheme,
            ['button', 'bgColor'],
            ['defaultBgColor']
          )
        },
      })
    )
  }
  public get header(): RequiredThemeDefine['header'] {
    const obj = this[_].obj
    const superTheme = this[_].superTheme
    return (
      this._header ||
      (this._header = {
        get sortArrowColor(): ColorPropertyDefine {
          return getProp(obj, superTheme, ['header', 'sortArrowColor'])
        },
      })
    )
  }
  public get switch(): RequiredThemeDefine['switch'] {
    const obj = this[_].obj
    const superTheme = this[_].superTheme
    return (
      this._switch ||
      (this._switch = {
        get uncheckBgColor(): ColorPropertyDefine {
          return getProp(
            obj,
            superTheme,
            ['switch', 'uncheckBgColor'],
            ['defaultBgColor']
          )
        },
        get checkBgColor(): ColorPropertyDefine {
          return getProp(obj, superTheme, ['switch', 'checkBgColor'], ['color'])
        },
        get borderColor(): ColorPropertyDefine {
          return getProp(obj, superTheme, ['switch', 'borderColor'])
        },
      })
    )
  }
  public get tree(): RequiredThemeDefine['tree'] {
    const obj = this[_].obj
    const superTheme = this[_].superTheme
    return (
      this._tree ||
      (this._tree = {
        get lineColor(): ColorPropertyDefine {
          return getProp(obj, superTheme, ['tree', 'lineColor'])
        },
        get buttonColor(): ColorPropertyDefine {
          return getProp(obj, superTheme, ['tree', 'buttonColor'], ['color'])
        },
        get buttonBgColor(): ColorPropertyDefine {
          return getProp(
            obj,
            superTheme,
            ['tree', 'buttonBgColor'],
            ['defaultBgColor']
          )
        },
        get buttonBorderColor(): ColorPropertyDefine {
          return getProp(obj, superTheme, ['tree', 'buttonBorderColor'])
        },
        get linkColor(): ColorPropertyDefine {
          return getProp(obj, superTheme, ['tree', 'linkColor'])
        },
      })
    )
  }
  public hasProperty(names: string[]): boolean {
    const { obj, superTheme } = this[_]
    return hasThemeProperty(obj, names) || hasThemeProperty(superTheme, names)
  }
  public extends(obj: PartialThemeDefine): Theme {
    return new Theme(obj, this)
  }
}

function hasThemeProperty(obj: PartialThemeDefine, names: string[]): boolean {
  if (obj instanceof Theme) {
    return obj.hasProperty(names)
  } else {
    let o: any = obj
    if (!o) {
      return false
    }
    for (let index = 0; index < names.length; index++) {
      const name = names[index]
      o = o[name]
      if (!o) {
        return false
      }
    }
    return !!o
  }
}
