import { obj } from '../../internal/utils'
import { BaseInputEditor } from './BaseInputEditor'
import { SmallDialogInputElement } from './internal/SmallDialogInputElement'

import { GridInternal, InputEditorState } from '../../ts-types-internal'
import { getSmallDialogInputEditorStateId } from '../../internal/symbolManager'
import {
  SmallDialogInputEditorOption,
  ListGridAPI,
  MaybePromise,
  CellAddress,
} from '../../ts-types'

const SMALL_DIALOG_INPUT_EDITOR_STATE_ID = getSmallDialogInputEditorStateId()

let globalSmallDialogInputElement: SmallDialogInputElement<any> | null = null
let globalSmallDialogInputBindGridCount = 0

function getState<T>(grid: GridInternal<T>): InputEditorState {
  let state = grid[SMALL_DIALOG_INPUT_EDITOR_STATE_ID]
  if (!state) {
    state = {}
    obj.setReadonly(grid, SMALL_DIALOG_INPUT_EDITOR_STATE_ID, state)
  }
  return state
}

function attachDialogInput<T>(
  grid: ListGridAPI<T>,
  cell: CellAddress,
  editor: SmallDialogInputEditor<T>,
  value: string
): void {
  const state = getState(grid as any)
  if (!globalSmallDialogInputElement) {
    globalSmallDialogInputElement = new SmallDialogInputElement()
  }
  if (!state.element) {
    state.element = globalSmallDialogInputElement
    globalSmallDialogInputBindGridCount++
    grid.addDisposable({
      dispose() {
        globalSmallDialogInputBindGridCount--
        if (
          !globalSmallDialogInputBindGridCount &&
          globalSmallDialogInputElement
        ) {
          globalSmallDialogInputElement.dispose()
          globalSmallDialogInputElement = null
        }
      },
    })
  }

  globalSmallDialogInputElement.attach(grid, editor, cell.col, cell.row, value)
}

function detachDialogInput(gridFocus?: boolean) {
  if (globalSmallDialogInputElement) {
    globalSmallDialogInputElement.detach(gridFocus)
  }
}
type GetValueResult<T, R> = (
  value: string,
  info: { grid: ListGridAPI<T>; col: number; row: number }
) => R

export class SmallDialogInputEditor<T> extends BaseInputEditor<T> {
  private _helperText?: string | GetValueResult<T, string>
  private _inputValidator?: GetValueResult<T, MaybePromise<string>>
  private _validator?: GetValueResult<T, MaybePromise<string>>
  private _classList?: string | string[]
  private _type?: string
  public constructor(options: SmallDialogInputEditorOption<T> = {}) {
    super(options)
    this._helperText = options.helperText
    this._inputValidator = options.inputValidator
    this._validator = options.validator
    this._classList = options.classList || ''
    this._type = options.type || ''
  }
  public dispose() {
    // noop
  }
  public get classList(): string[] | undefined {
    if (!this._classList) {
      return undefined
    }
    return Array.isArray(this._classList) ? this._classList : [this._classList]
  }
  public set classList(classList) {
    this._classList = classList
  }
  public get type() {
    return this._type
  }
  public set type(type) {
    this._type = type
  }
  public get helperText() {
    return this._helperText
  }
  public get inputValidator() {
    return this._inputValidator
  }
  public get validator() {
    return this._validator
  }
  public clone(): SmallDialogInputEditor<T> {
    return new SmallDialogInputEditor(this)
  }
  public onInputCellInternal(
    grid: ListGridAPI<T>,
    cell: CellAddress,
    inputValue: string
  ) {
    attachDialogInput(grid, cell, this, inputValue)
  }
  public onOpenCellInternal(grid: ListGridAPI<T>, cell: CellAddress) {
    grid.doGetCellValue(cell.col, cell.row, (value) => {
      attachDialogInput(grid, cell, this, value)
    })
  }
  public onChangeSelectCellInternal(
    grid: ListGridAPI<T>,
    cell: CellAddress,
    selected: any
  ) {
    // cancel input
    detachDialogInput()
  }
  public onGridScrollInternal(grid: ListGridAPI<T>) {
    // cancel input
    detachDialogInput(true)
  }
  public onChangeDisabledInternal() {
    // cancel input
    detachDialogInput(true)
  }
  public onChangeReadOnlyInternal() {
    // cancel input
    detachDialogInput(true)
  }
  public onSetInputAttrsInternal(
    grid: ListGridAPI<T>,
    cell: CellAddress,
    input: HTMLInputElement
  ) {
    SmallDialogInputElement.setInputAttrs(this, grid, input)
  }
}
