import { PercentCompleteBarStyleOption, ColorDef } from '../../ts-types'
import { Style } from './Style'

let defaultPercentCompleteBarStyle: PercentCompleteBarStyle

const DEFAULT_BAR_COLOR = (num: number): string => {
  if (num > 80) {
    return '#20a8d8'
  }
  if (num > 50) {
    return '#4dbd74'
  }
  if (num > 20) {
    return '#ffc107'
  }
  return '#f86c6b'
}

export class PercentCompleteBarStyle extends Style {
  public static get DEFAULT(): PercentCompleteBarStyle {
    return defaultPercentCompleteBarStyle
      ? defaultPercentCompleteBarStyle
      : (defaultPercentCompleteBarStyle = new PercentCompleteBarStyle())
  }
  private _barColor: ColorDef | ((num: number) => ColorDef)
  private _barBgColor: ColorDef
  private _barHeight: number
  public constructor(style: PercentCompleteBarStyleOption = {}) {
    super(style)
    this._barColor = style.barColor || DEFAULT_BAR_COLOR
    this._barBgColor = style.barBgColor || '#f0f3f5'
    this._barHeight = style.barHeight || 3
  }
  public get barColor(): ColorDef | ((num: number) => ColorDef) {
    return this._barColor
  }
  public set barColor(barColor) {
    this._barColor = barColor
    this.doChangeStyle()
  }
  public get barBgColor(): ColorDef {
    return this._barBgColor
  }
  public set barBgColor(barBgColor) {
    this._barBgColor = barBgColor
    this.doChangeStyle()
  }
  public get barHeight(): number {
    return this._barHeight
  }
  public set barHeight(barHeight) {
    this._barHeight = barHeight
    this.doChangeStyle()
  }
  public clone(): PercentCompleteBarStyle {
    return new PercentCompleteBarStyle(this)
  }
}
