import {
  InlineMenuEditorOption,
  ListGridAPI,
  EventListenerId,
  CellAddress,
  LayoutObjectId,
  SimpleColumnMenuItemOption,
  ColumnTypeAPI,
  ColumnMenuItemOption,
} from '../../ts-types'
import { KEY_ENTER, KEY_F2 } from '../../core/draw-grid/KEY_CODE'
import { DG_EVENT_TYPE } from '../../core/draw-grid/DG_EVENT_TYPE'
import menuItems from '../../internal/menu-items'
import {
  obj,
  event,
  array,
  then,
  isPromise,
  cellEquals,
} from '../../internal/utils'
import { isDisabledRecord, isReadOnlyRecord } from './action-utils'
import { Editor } from './Editor'
import { InlineMenuElement } from './internal/InlineMenuElement'

import { GridInternal, InputEditorState } from '../../ts-types-internal'
import { getInlineMenuEditorStateId } from '../../internal/symbolManager'
import { MenuColumn } from '../type/MenuColumn'

const INLINE_MENU_EDITOR_STATE_ID = getInlineMenuEditorStateId()

let globalInlineMenuElement: InlineMenuElement<any> | null = null
let globalInlineMenuBindGridCount = 0

function getState<T>(grid: GridInternal<T>): InputEditorState {
  let state = grid[INLINE_MENU_EDITOR_STATE_ID]
  if (!state) {
    state = {}
    obj.setReadonly(grid, INLINE_MENU_EDITOR_STATE_ID, state)
  }
  return state
}

function attachMenu<T>(
  grid: ListGridAPI<T>,
  cell: CellAddress,
  editor: InlineMenuEditor<T>,
  value: any,
  record: T | undefined
) {
  const state = getState(grid as any)
  if (!globalInlineMenuElement) {
    globalInlineMenuElement = new InlineMenuElement()
  }
  if (!state.element) {
    state.element = globalInlineMenuElement
    globalInlineMenuBindGridCount++
    grid.addDisposable({
      dispose() {
        globalInlineMenuBindGridCount--
        if (!globalInlineMenuBindGridCount && globalInlineMenuElement) {
          globalInlineMenuElement.dispose()
          globalInlineMenuElement = null
        }
      },
    })
  }

  globalInlineMenuElement.attach(
    grid,
    editor,
    cell.col,
    cell.row,
    value,
    record
  )
}
function detachMenu(gridFocus?: boolean) {
  if (globalInlineMenuElement) {
    globalInlineMenuElement.detach(gridFocus)
  }
}

export class InlineMenuEditor<T> extends Editor<T> {
  private _classList?: string | string[]
  private _options: (record: T | undefined) => ColumnMenuItemOption[]
  private _autoWidth: boolean
  public constructor(options: InlineMenuEditorOption<T> = {}) {
    super(options)
    this._classList = options.classList || ''
    this._options = menuItems.normalizeToFn(options.options)
    this._autoWidth = options.autoWidth || false
  }
  public dispose() {
    // nothing
  }
  public get classList(): string[] | undefined {
    if (!this._classList) {
      return undefined
    }
    return Array.isArray(this._classList) ? this._classList : [this._classList]
  }
  public set classList(classList) {
    this._classList = classList
  }
  public get options(): (record: T | undefined) => ColumnMenuItemOption[] {
    return this._options
  }
  public set options(options) {
    this._options = menuItems.normalizeToFn(options)
  }
  public get autoWidth(): boolean {
    return this._autoWidth
  }
  public set autoWidth(autoWidth) {
    this._autoWidth = autoWidth
  }
  public clone(): InlineMenuEditor<T> {
    return new InlineMenuEditor(this)
  }
  public onChangeDisabledInternal() {
    // cancel input
    detachMenu(true)
  }
  public onChangeReadOnlyInternal() {
    // cancel input
    detachMenu(true)
  }
  public bindGridEvent(
    grid: ListGridAPI<T>,
    cellId: LayoutObjectId
  ): EventListenerId[] {
    const open = (cell: CellAddress): boolean => {
      const allowOpen =
        !isReadOnlyRecord(this.readOnly, grid, cell.row) &&
        !isDisabledRecord(this.disabled, grid, cell.row)
      if (allowOpen) {
        grid.doGetCellValue(cell.col, cell.row, (value) => {
          const record = grid.getRowRecord(cell.row)
          if (isPromise(record)) {
            return
          }
          attachMenu(grid, cell, this, value, record)
        })
      }
      return allowOpen
    }

    function isTarget(col: number, row: number): boolean {
      return grid.getLayoutCellId(col, row) === cellId
    }
    return [
      grid.listen(DG_EVENT_TYPE.CLICK_CELL, (cell) => {
        if (!isTarget(cell.col, cell.row)) {
          return
        }
        open({
          col: cell.col,
          row: cell.row,
        })
      }),
      grid.listen(DG_EVENT_TYPE.KEYDOWN, (e) => {
        if (
          (e.keyCode !== KEY_F2 && e.keyCode !== KEY_ENTER) ||
          (e.keyCode === KEY_ENTER && e.event.shiftKey)
        ) {
          return
        }
        const sel = grid.selection.select
        if (!isTarget(sel.col, sel.row)) {
          return
        }
        if (
          open({
            col: sel.col,
            row: sel.row,
          })
        ) {
          event.cancel(e.event)
          e.stopCellMoving()
        }
      }),
      grid.listen(DG_EVENT_TYPE.SELECTED_CELL, (e) => {
        detachMenu()
      }),
      grid.listen(DG_EVENT_TYPE.SCROLL, () => {
        detachMenu(true)
      }),

      // mouse move
      grid.listen(DG_EVENT_TYPE.MOUSEOVER_CELL, (e) => {
        if (!isTarget(e.col, e.row)) {
          return
        }
        if (
          isReadOnlyRecord(this.readOnly, grid, e.row) ||
          isDisabledRecord(this.disabled, grid, e.row)
        ) {
          return
        }
        grid.getElement().style.cursor = 'pointer'
      }),
      grid.listen(DG_EVENT_TYPE.MOUSEMOVE_CELL, (e) => {
        if (!isTarget(e.col, e.row)) {
          return
        }
        if (
          isReadOnlyRecord(this.readOnly, grid, e.row) ||
          isDisabledRecord(this.disabled, grid, e.row)
        ) {
          return
        }
        if (!grid.getElement().style.cursor) {
          grid.getElement().style.cursor = 'pointer'
        }
      }),
      grid.listen(DG_EVENT_TYPE.MOUSEOUT_CELL, (e) => {
        if (!isTarget(e.col, e.row)) {
          return
        }
        grid.getElement().style.cursor = ''
      }),

      // paste value
      grid.listen(DG_EVENT_TYPE.PASTE_CELL, (e) => {
        if (e.multi) {
          // ignore multi cell values
          return
        }
        const selectionRange = grid.selection.range
        if (!cellEquals(selectionRange.start, selectionRange.end)) {
          // ignore multi paste values
          return
        }
        if (!isTarget(e.col, e.row)) {
          return
        }
        if (
          isReadOnlyRecord(this.readOnly, grid, e.row) ||
          isDisabledRecord(this.disabled, grid, e.row)
        ) {
          return
        }
        const record = grid.getRowRecord(e.row)
        if (isPromise(record)) {
          return
        }
        const pasteOpt = this._pasteDataToOptionValue(
          e.normalizeValue,
          grid,
          e,
          record
        )
        if (pasteOpt) {
          event.cancel(e.event)
          then(
            grid.doChangeValue(e.col, e.row, () => pasteOpt.value),
            () => {
              const range = grid.getCellRange(e.col, e.row)
              grid.invalidateCellRange(range)
            }
          )
        }
      }),
    ]
  }
  public onPasteCellRangeBox(
    grid: ListGridAPI<T>,
    cell: CellAddress,
    value: string
  ): void {
    if (
      isReadOnlyRecord(this.readOnly, grid, cell.row) ||
      isDisabledRecord(this.disabled, grid, cell.row)
    ) {
      return
    }
    const record = grid.getRowRecord(cell.row)
    if (isPromise(record)) {
      return
    }
    const pasteOpt = this._pasteDataToOptionValue(value, grid, cell, record)
    if (pasteOpt) {
      grid.doChangeValue(cell.col, cell.row, () => pasteOpt.value)
    }
  }
  public onDeleteCellRangeBox(grid: ListGridAPI<T>, cell: CellAddress): void {
    if (
      isReadOnlyRecord(this.readOnly, grid, cell.row) ||
      isDisabledRecord(this.disabled, grid, cell.row)
    ) {
      return
    }
    const record = grid.getRowRecord(cell.row)
    if (isPromise(record)) {
      return
    }
    const pasteOpt = this._pasteDataToOptionValue('', grid, cell, record)
    if (pasteOpt) {
      grid.doChangeValue(cell.col, cell.row, () => pasteOpt.value)
    }
  }
  private _pasteDataToOptionValue(
    value: string,
    grid: ListGridAPI<T>,
    cell: CellAddress,
    record: T | undefined
  ): SimpleColumnMenuItemOption | undefined {
    const options = this._options(record)
    const pasteOpt = _textToOptionValue(value, options)
    if (pasteOpt) {
      return pasteOpt
    }
    const columnType = grid.getColumnType(cell.col, cell.row)
    if (hasOptions(columnType)) {
      // Find with caption.
      const pasteValue = normalizePasteValueStr(value)
      const captionOpt = array.find(
        columnType.options,
        (opt) => normalizePasteValueStr(opt.label) === pasteValue
      )
      if (captionOpt) {
        return _textToOptionValue(captionOpt.value, options)
      }
    }
    return undefined
  }
}

function _textToOptionValue<T>(
  value: string,
  options: SimpleColumnMenuItemOption[]
): SimpleColumnMenuItemOption | undefined {
  const pasteValue = normalizePasteValueStr(value)
  const pasteOpt = array.find(
    options,
    (opt) => normalizePasteValueStr(opt.value) === pasteValue
  )
  if (pasteOpt) {
    return pasteOpt
  }
  return undefined
}

function normalizePasteValueStr(value: any): string {
  if (value == null) {
    return ''
  }
  return `${value}`.trim()
}

function hasOptions(columnType: ColumnTypeAPI): columnType is MenuColumn<any> {
  if (columnType instanceof MenuColumn) {
    return true
  }
  if (Array.isArray((columnType as any).options)) {
    return true
  }

  return false
}
