import { CheckHeaderState, GridInternal } from '../../ts-types-internal'
import { obj } from '../../internal/utils'
import { BaseCheckEditor } from './BaseCheckEditor'
import { getCheckColumnStateId } from '../../internal/symbolManager'

const CHECK_COLUMN_STATE_ID = getCheckColumnStateId()

export class CheckEditor<T> extends BaseCheckEditor<T> {
  public clone(): CheckEditor<T> {
    return new CheckEditor(this)
  }
  protected getState(grid: GridInternal<T>): CheckHeaderState {
    let state = grid[CHECK_COLUMN_STATE_ID]
    if (!state) {
      state = { block: {}, elapsed: {} }
      obj.setReadonly(grid, CHECK_COLUMN_STATE_ID, state)
    }
    return state
  }
}
