import * as utils from './columnUtils'
import {
  ButtonColumnOption,
  ListGridAPI,
  GridCanvasHelperAPI,
  CellContext,
} from '../../ts-types'
import { DrawCellInfo } from '../../ts-types-internal'
import { ButtonStyle } from '../style/ButtonStyle'
import { Column } from './Column'
import { getButtonColumnStateId } from '../../internal/symbolManager'
import { cellInRange } from '../../internal/utils'

const BUTTON_COLUMN_STATE_ID = getButtonColumnStateId()

export class ButtonColumn<T> extends Column<T> {
  private _caption?: string
  private _linkButton: boolean
  public constructor(options: ButtonColumnOption<T> = {}) {
    super(options)
    this._caption = options.caption
    this._linkButton = !!options.linkButton
  }
  public get StyleClass(): typeof ButtonStyle {
    return ButtonStyle
  }
  public get caption(): string | undefined {
    return this._caption
  }
  public withCaption(caption: string): ButtonColumn<T> {
    const c = this.clone()
    c._caption = caption
    return c
  }
  public clone(): ButtonColumn<T> {
    return new ButtonColumn(this)
  }
  public drawInternal(
    value: string,
    context: CellContext,
    style: ButtonStyle,
    helper: GridCanvasHelperAPI,
    grid: ListGridAPI<T>,
    { getIcon }: DrawCellInfo<T>
  ): void {
    const {
      textAlign,
      textBaseline,
      color,
      buttonBgColor,
      font,
      padding,
      textOverflow,
    } = style

    helper.testFontLoad(font, value, context)
    const { col, row } = context
    const range = grid.getCellRange(col, row)
    let active = false
    let selected = false
    const state = (grid as any)[BUTTON_COLUMN_STATE_ID]
    if (state) {
      if (
        state.mouseActiveCell &&
        cellInRange(range, state.mouseActiveCell.col, state.mouseActiveCell.row)
      ) {
        active = true
      }
      const { select } = context.getSelection()
      if (cellInRange(range, select.col, select.row)) {
        selected = true
      }
    }

    utils.loadIcons(getIcon(), context, helper, (icons, ctx) => {
      if (this._linkButton) {
        if (active && state && state.mouseRelativePos) {
          const rect = helper.getTextRect(value, ctx, {
            font,
            icons,
            padding,
            textAlign,
            textBaseline,
            textOverflow,
          })
          active =
            rect.left <= state.mouseRelativePos.x &&
            rect.right >= state.mouseRelativePos.x &&
            rect.top <= state.mouseRelativePos.y &&
            rect.bottom >= state.mouseRelativePos.y
          state.mouseActive = active
        }
        helper.text(value, ctx, {
          color: active ? buttonBgColor || helper.theme.button.bgColor : color,
          font,
          icons,
          padding,
          textAlign,
          textBaseline,
          textOverflow,
        })
      } else {
        if (active) {
          state.mouseActive = active
        }
        helper.button(value, ctx, {
          bgColor: buttonBgColor,
          color,
          font,
          icons,
          padding,
          shadow:
            active || selected
              ? {
                  blur: 6,
                  color: 'rgba(0, 0, 0, 0.48)',
                  offsetY: 3,
                }
              : {},
          textAlign,
          textBaseline,
          textOverflow,
        })
      }
    })
  }
  protected convertInternal(value: unknown) {
    return this._caption || super.convertInternal(value)
  }
}
