import { SwitchStyleOption } from '../../ts-types'
import { BaseCheckStyle } from './BaseCheckStyle'

let defaultSwitchStyle: SwitchStyle

export class SwitchStyle extends BaseCheckStyle {
  public static get DEFAULT(): SwitchStyle {
    return defaultSwitchStyle
      ? defaultSwitchStyle
      : (defaultSwitchStyle = new SwitchStyle())
  }
  public constructor(style: SwitchStyleOption = {}) {
    super(style)
  }
  public clone(): SwitchStyle {
    return new SwitchStyle(this)
  }
}
