import { KEY_ENTER } from '../../core/draw-grid/KEY_CODE'
import { event, isDef } from '../../internal/utils'
import { isDisabledRecord, isReadOnlyRecord } from './action-utils'
import { bindCellClickAction } from './actionBind'
import { BaseInputEditor } from './BaseInputEditor'
import {
  ActionListener,
  BaseActionInputOption,
  ListGridAPI,
  EventListenerId,
  CellAddress,
  LayoutObjectId,
} from '../../ts-types'

export abstract class BaseActionInput<T> extends BaseInputEditor<T> {
  private _disableInput: boolean | ActionListener<T>
  private _disableAction: boolean | ActionListener<T>
  private _action?: ActionListener<T>
  public constructor(options: BaseActionInputOption<T> = {}) {
    super(options)
    this._disableInput = options.disableInput || false
    this._disableAction = options.disableAction || false
    this._action = options.action
  }
  public get disableInput() {
    return this._disableInput
  }
  public set disableInput(disableInput) {
    this._disableInput = disableInput
  }
  public get disableAction() {
    return this._disableAction
  }
  public set disableAction(disableAction) {
    this._disableAction = disableAction
  }
  public get action() {
    return this._action
  }
  public set action(action) {
    this._action = action
  }
  public onBeforeKeydownInternal(
    grid: ListGridAPI<T>,
    keyCode: number,
    e: KeyboardEvent,
    cellId: LayoutObjectId
  ) {
    function isTarget(col: number, row: number): boolean {
      return grid.getLayoutCellId(col, row) === cellId
    }
    const sel = grid.selection.select
    if (
      isReadOnlyRecord(this.readOnly, grid, sel.row) ||
      isDisabledRecord(this.disabled, grid, sel.row) ||
      !isTarget(sel.col, sel.row) ||
      !this._action ||
      this.isDisabledAction(grid, sel)
    ) {
      return true
    }
    if (
      (this.isDisabledInput(grid, sel) &&
        keyCode === KEY_ENTER &&
        !e.ctrlKey &&
        !e.metaKey) ||
      (keyCode === KEY_ENTER && (e.ctrlKey || e.metaKey))
    ) {
      const record = grid.getRowRecord(sel.row)
      if (this._action) {
        this._action.apply(this, [
          record,
          {
            cell: {
              col: sel.col,
              row: sel.row,
            },
            grid,
          },
        ])
        event.cancel(e)
        return false
      }
    }
    return true
  }
  public bindGridEvent(
    grid: ListGridAPI<T>,
    cellId: LayoutObjectId
  ): EventListenerId[] {
    const state = this.getState(grid)
    const action = (cell: CellAddress) => {
      if (
        isReadOnlyRecord(this.readOnly, grid, cell.row) ||
        isDisabledRecord(this.disabled, grid, cell.row) ||
        !this._action ||
        this.isDisabledAction(grid, cell)
      ) {
        return
      }
      if (state.mouseActive) {
        const record = grid.getRowRecord(cell.row)
        if (this._action) {
          this._action.apply(this, [
            record,
            {
              cell,
              grid,
            },
          ])
        }
      }
    }
    const mouseOut = (cell: CellAddress) => {
      delete state.mouseRelativePos
      delete state.mouseActiveCell
      delete state.mouseActive
      const range = grid.getCellRange(cell.col, cell.row)
      grid.invalidateCellRange(range)
    }
    const mouseMove = (cell: CellAddress, event: any) => {
      if (
        isReadOnlyRecord(this.readOnly, grid, cell.row) ||
        isDisabledRecord(this.disabled, grid, cell.row) ||
        !this._action ||
        this.isDisabledAction(grid, cell)
      ) {
        return false
      }
      state.mouseRelativePos = grid._getMouseRelativePoint(event)
      state.mouseActiveCell = {
        col: cell.col,
        row: cell.row,
      }
      const range = grid.getCellRange(cell.col, cell.row)
      grid.invalidateCellRange(range)
      return isDef(state.mouseActive) ? state.mouseActive : true
    }
    let ids = super.bindGridEvent(grid, cellId)
    ids = ids.concat(
      bindCellClickAction(grid, cellId, {
        action,
        mouseMove,
        mouseOut,
        mouseOver: mouseMove,
      })
    )
    return ids
  }
  public onInputCellInternal(
    grid: ListGridAPI<T>,
    cell: CellAddress,
    inputValue: string
  ) {
    if (this.isDisabledInput(grid, cell)) {
      return
    }
    this.onActionInputCellInternal(grid, cell, inputValue)
  }
  public onOpenCellInternal(grid: ListGridAPI<T>, cell: CellAddress) {
    if (this.isDisabledInput(grid, cell)) {
      return
    }
    this.onActionOpenCellInternal(grid, cell)
  }
  public onActionInputCellInternal(
    grid: ListGridAPI<T>,
    cell: CellAddress,
    inputValue: string
  ) {
    throw new Error()
  }
  public onActionOpenCellInternal(grid: ListGridAPI<T>, cell: CellAddress) {
    throw new Error()
  }
  protected abstract getState(grid: any): any
  private isDisabledInput(grid: ListGridAPI<T>, cell: CellAddress) {
    return this.isDisabled(this._disableInput, grid, cell)
  }
  private isDisabledAction(grid: ListGridAPI<T>, cell: CellAddress) {
    return this.isDisabled(this._disableAction, grid, cell)
  }
  private isDisabled(
    p: boolean | ActionListener<T>,
    grid: ListGridAPI<T>,
    cell: CellAddress
  ) {
    let isDisabled = false
    if (p) {
      if (typeof p === 'function') {
        const record = grid.getRowRecord(cell.row)
        if (
          p.apply(this, [
            record,
            {
              cell,
              grid,
            },
          ])
        ) {
          isDisabled = true
        }
      } else {
        isDisabled = true
      }
    }
    return isDisabled
  }
}
