import { ColorDef, RadioStyleOption } from '../../ts-types'
import { BaseStdStyle } from './BaseStdStyle'
import { defaults } from '../../internal/utils'

let defaultRadioStyle: RadioStyle
export class RadioStyle extends BaseStdStyle {
  public static get DEFAULT(): RadioStyle {
    return defaultRadioStyle
      ? defaultRadioStyle
      : (defaultRadioStyle = new RadioStyle())
  }
  private _checkColor?: ColorDef
  private _uncheckBorderColor?: ColorDef
  private _checkBorderColor?: ColorDef
  private _uncheckBgColor?: ColorDef
  private _checkBgColor?: ColorDef
  public constructor(style: RadioStyleOption = {}) {
    super(defaults(style, { textAlign: 'center' }))
    const {
      checkColor,
      uncheckBorderColor,
      checkBorderColor,
      uncheckBgColor,
      checkBgColor,
    } = style
    this._checkColor = checkColor
    this._uncheckBorderColor = uncheckBorderColor
    this._checkBorderColor = checkBorderColor
    this._uncheckBgColor = uncheckBgColor
    this._checkBgColor = checkBgColor
  }
  public get checkColor(): ColorDef | undefined {
    return this._checkColor
  }
  public set checkColor(checkColor) {
    this._checkColor = checkColor
    this.doChangeStyle()
  }
  public get uncheckBorderColor(): ColorDef | undefined {
    return this._uncheckBorderColor
  }
  public set uncheckBorderColor(uncheckBorderColor) {
    this._uncheckBorderColor = uncheckBorderColor
    this.doChangeStyle()
  }
  public get checkBorderColor(): ColorDef | undefined {
    return this._checkBorderColor
  }
  public set checkBorderColor(checkBorderColor) {
    this._checkBorderColor = checkBorderColor
    this.doChangeStyle()
  }
  public get uncheckBgColor(): ColorDef | undefined {
    return this._uncheckBgColor
  }
  public set uncheckBgColor(uncheckBgColor) {
    this._uncheckBgColor = uncheckBgColor
    this.doChangeStyle()
  }
  public get checkBgColor(): ColorDef | undefined {
    return this._checkBgColor
  }
  public set checkBgColor(checkBgColor) {
    this._checkBgColor = checkBgColor
    this.doChangeStyle()
  }
  public clone(): RadioStyle {
    return new RadioStyle(this)
  }
}
