import { getIgnoreCase, extend } from './internal/utils'
import { themes as plugins } from './plugins/themes'
import basicTheme from './themes/BASIC'
import materialDesignTheme from './themes/MATERIAL_DESIGN'
import { Theme } from './themes/theme'
import { ThemeDefine } from './ts-types'

const BASIC = new Theme(basicTheme)
const MATERIAL_DESIGN = new Theme(materialDesignTheme)

const builtin: { [key: string]: Theme } = {
  BASIC,
  MATERIAL_DESIGN,
}
let defTheme = MATERIAL_DESIGN

const theme = { Theme }
function of(value: ThemeDefine | string | undefined | null): Theme | null {
  if (!value) {
    return null
  }
  if (typeof value === 'string') {
    const t = getIgnoreCase(getChoices(), value)
    if (t) {
      return t
    }
    return null
  }
  if (value instanceof Theme) {
    return value
  }
  return new Theme(value)
}

function getDefault(): Theme {
  return defTheme
}
function setDefault(defaultTheme: Theme): void {
  defTheme = of(defaultTheme) || defTheme
}
function getChoices(): { [key: string]: Theme } {
  return extend(builtin, plugins)
}

export const themes = {
  BASIC,
  MATERIAL_DESIGN,
  theme,
  of,
  getDefault,
  setDefault,
  getChoices,
}
