import { then, isPromise } from '../internal/utils'
import { InlineImage } from './InlineImage'
import { MaybePromise } from '../ts-types'
import { toPx } from '../internal/calc'

function buildSvgDataUrl(svg: string | Node): string {
  const data =
    typeof svg === 'string' ? svg : new XMLSerializer().serializeToString(svg)
  const url = `data:image/svg+xml;charset=utf-8,${encodeURIComponent(data)}` // svg -> base64
  return url
}
function getSvgElement(svg: string | SVGElement): SVGElement {
  let el
  if (typeof svg === 'string') {
    const parser = new DOMParser()
    const doc = parser.parseFromString(svg, 'image/svg+xml')
    const nodes = doc.children || doc.childNodes
    el = nodes[0] as SVGElement
    for (let i = 0; i < nodes.length; i++) {
      if (nodes[i] instanceof SVGElement) {
        el = nodes[i] as SVGElement
        break
      }
    }
  } else {
    el = svg
  }
  return el
}

export type InlineSvgConstructorOption = {
  svg: MaybePromise<string | SVGElement>
  width?: number
  height?: number
}

export class InlineSvg extends InlineImage {
  public constructor({ svg, width, height }: InlineSvgConstructorOption) {
    const svgElem = then(svg, getSvgElement)
    const elmWidth = !isPromise(svgElem)
      ? svgElem.getAttribute('width') ?? undefined
      : undefined
    const elmHeight = !isPromise(svgElem)
      ? svgElem.getAttribute('height') ?? undefined
      : undefined
    const context = {
      full: 0,
      em: 0
    }
    const numElmWidth = elmWidth != null ? toPx(elmWidth, context) : undefined
    const numElmHeight = elmHeight != null ? toPx(elmHeight, context) : undefined
    super({
      src: then(svg, buildSvgDataUrl),
      width: width || numElmWidth,
      height: height || numElmHeight,
      imageWidth: numElmWidth,
      imageHeight: numElmHeight,
    })
  }
  public canBreak(): boolean {
    return false
  }
  public toString(): string {
    return ''
  }
}
