import fonts from '../internal/fonts'
import { InlineDrawOption, Inline } from './Inline'
import { ColorDef, AnyFunction } from '../ts-types'

export type InlineIconConstructorOption = {
  width?: number
  font?: string
  content?: string
  color?: ColorDef
}

export class InlineIcon extends Inline {
  private _icon: InlineIconConstructorOption
  public constructor(icon: InlineIconConstructorOption) {
    super()
    this._icon = icon || {}
  }
  public width({ ctx }: { ctx: CanvasRenderingContext2D }): number {
    const icon = this._icon
    if (icon.width) {
      return icon.width
    }
    if (icon.font && fonts.check(icon.font, icon.content || '')) {
      ctx.save()
      ctx.canvas.style.letterSpacing = 'normal'
      try {
        ctx.font = icon.font || ctx.font
        return ctx.measureText(icon.content || '').width
      } finally {
        ctx.canvas.style.letterSpacing = ''
        ctx.restore()
      }
    }
    return 0 // unknown
  }
  public font(): string | null {
    return this._icon.font ?? null
  }
  public color(): ColorDef | null {
    return this._icon.color ?? null
  }
  public canDraw(): boolean {
    const icon = this._icon
    return icon.font ? fonts.check(icon.font, icon.content || '') : true
  }
  public onReady(callback: AnyFunction): void {
    const icon = this._icon
    if (icon.font && !fonts.check(icon.font, icon.content || '')) {
      fonts.load(icon.font, icon.content || '', callback)
    }
  }
  public draw({
    ctx,
    canvasHelper,
    rect,
    offset,
    offsetLeft,
    offsetRight,
    offsetTop,
    offsetBottom,
  }: InlineDrawOption): void {
    const icon = this._icon
    if (icon.content) {
      ctx.canvas.style.letterSpacing = 'normal'
      try {
        ctx.font = ctx.font // To apply letterSpacing, we need to reset it.
        canvasHelper.fillTextRect(
          ctx,
          icon.content,
          rect.left,
          rect.top,
          rect.width,
          rect.height,
          {
            offset: offset + 1,
            padding: {
              bottom: offsetBottom,
              left: offsetLeft,
              right: offsetRight,
              top: offsetTop,
            },
          }
        )
      } finally {
        ctx.canvas.style.letterSpacing = ''
      }
    }
  }
  public canBreak(): boolean {
    return false
  }
  public toString(): string {
    return ''
  }
}
