import { createElement } from '../../internal/dom'
import { TooltipElement } from './TooltipElement'

export class HtmlTooltipElement<T> extends TooltipElement<T> {
  protected createTooltipMessageElement(
    rootElement: HTMLDivElement,
    cls: string
  ): HTMLElement {
    const messageElement = createElement('div', { classList: [cls] })
    rootElement.appendChild(messageElement)
    return messageElement
  }
  protected doSetContent(element: Element, content: string) {
    element.innerHTML = content
  }
}
