import * as columnUtils from './columnUtils'
import {
  CellAddress,
  LookupColumnOption,
  CellContext,
  GridCanvasHelperAPI,
  ListGridAPI,
  DrawCallback,
  CellStyle,
} from '../../ts-types'
import { DrawCellInfo } from '../../ts-types-internal'
import { LookupStyle } from '../style/LookupStyle'
import { BaseColumn } from './BaseColumn'
import { getOrApply, isPromise, isDef } from '../../internal/utils'
import { getInlineLookupEditorStateId } from '../../internal/symbolManager'
import Rect from '../../internal/Rect'

const INLINE_LOOKUP_EDITOR_STATE_ID = getInlineLookupEditorStateId()

let lookupColumnPromiseDrawingCol = ''

export class LookupColumn<T, L> extends BaseColumn<T, string> {
  private _records?: L[] | ((rec: T) => L[] | Promise<L[]>)
  private _lookupMap: {
    [key: string]: string
  } = {}
  private _valueField?: string
  private _captionField?: string
  private _draw?: DrawCallback
  private _cellStyle?: CellStyle
  public constructor(options: LookupColumnOption<T, L> = {}) {
    super(options)
    this._records = options.records
    this._valueField = options.valueField
    this._captionField = options.captionField
    this._draw = options.draw
    this._cellStyle = options.cellStyle
  }
  public get StyleClass(): typeof LookupStyle {
    return LookupStyle
  }
  public get records(): L[] | ((rec: T) => L[] | Promise<L[]>) | undefined {
    return this._records
  }
  public get valueField(): string | undefined {
    return this._valueField
  }
  public get captionField(): string | undefined {
    return this._captionField
  }
  public get draw(): DrawCallback | undefined {
    return this._draw
  }
  public get cellStyle(): CellStyle | undefined {
    return this._cellStyle
  }
  public reviseAttachCellsArea(rect: Rect, row: number, grid: ListGridAPI<T>) {
    columnUtils.reviseAttachCellsArea(rect, row, grid, this._cellStyle)
  }
  public reviseAttachCellsPadding(
    padding: [number, number, number, number],
    row: number,
    grid: ListGridAPI<T>
  ) {
    super.reviseAttachCellsPadding(padding, row, grid)
    columnUtils.reviseAttachCellsPadding(padding, row, grid, this._cellStyle)
  }
  public clone(): LookupColumn<T, L> {
    return new LookupColumn(this)
  }
  public drawInternal(
    value: any,
    context: CellContext,
    style: LookupStyle,
    helper: GridCanvasHelperAPI,
    grid: ListGridAPI<T>,
    info: DrawCellInfo<T>
  ) {
    const getIcon = info.getIcon
    const getCell = info.getCell
    const getRecord = info.getRecord
    const records = getOrApply(this.records || [], getRecord())
    if (isPromise(records)) {
      const cell = getCell()
      const col = cell.col
      const row = cell.row
      if (!lookupColumnPromiseDrawingCol) {
        records.then(
          ((me, col, row) => (rs: L[]) => {
            lookupColumnPromiseDrawingCol = String(col)
            try {
              me._mergeRecords(rs)
              const range = grid.getCellRange(col, row)
              grid.invalidateCellRange(range)
            } finally {
              lookupColumnPromiseDrawingCol = ''
            }
          })(this, col, row)
        )
        return
      }
    } else {
      this._mergeRecords(records)
    }

    const textAlign = style.textAlign
    const textBaseline = style.textBaseline
    const font = style.font
    const textOverflow = style.textOverflow

    let color = style.color

    const text = this._convertInternal(value, getCell(), grid)
    helper.testFontLoad(font, text, context)

    columnUtils.loadIcons(getIcon(), context, helper, (icons, ctx) => {
      const isCustomDraw = columnUtils.customDraw(
        helper,
        this.draw,
        text,
        context,
        grid,
        getRecord()
      )
      if (!isCustomDraw) {
        if (!isDef(color) && (!isDef(value) || value === '')) {
          color = 'rgba(0, 0, 0, .38)'
        }
        helper.text(text, ctx, {
          color,
          font,
          icons,
          padding: columnUtils.getActionTextPadding(ctx, helper, style),
          textAlign,
          textBaseline,
          textOverflow,
        })
      }
      columnUtils.drawActionButton(
        grid,
        ctx,
        INLINE_LOOKUP_EDITOR_STATE_ID,
        helper,
        style
      )
      columnUtils.drawAttachArea(
        grid,
        getRecord(),
        context,
        helper,
        this._cellStyle
      )
    })
  }
  public convertCopyInternal(value: unknown) {
    return isDef(value) ? this._convertInternalValue(value) : ''
  }
  protected convertInternal(value: any): string {
    return value
  }
  protected doConvertInternal(
    value: unknown,
    _cell: CellAddress,
    _grid: ListGridAPI<T>
  ) {
    return this.convertInternal(value)
  }
  private _convertInternalValue(value: unknown): string {
    const key = isDef(value) ? value + '' : ''
    let v = this._lookupMap[key]
    if (!isDef(v)) {
      v = key
    }
    return v
  }
  private _convertInternal(
    value: any,
    cell: CellAddress,
    grid: ListGridAPI<T>
  ) {
    let v = this._convertInternalValue(value)
    if (this.convert) {
      v = this.convert(value, v, cell, grid)
    }
    return this.doConvertInternal(v, cell, grid)
  }
  private _mergeRecords(recs: L[] = []) {
    const valueField = this.valueField || 'id'
    const captionField = this.captionField || valueField
    for (const record of recs) {
      let key = (record as any)[valueField]
      key = isDef(key) ? key + '' : ''
      let value = (record as any)[captionField]
      value = isDef(value) ? value + '' : ''
      this._lookupMap[key] = value
    }
  }
}
