import { isDef } from '../../../internal/utils'
import { BranchLine } from './BranchLine'

export class BranchPoint {
  public static mergeLines(lines: BranchLine[]): BranchLine[] {
    const result = lines.filter((l) => isDef(l.fromIndex) && isDef(l.toIndex))

    const fromLines = lines.filter(
      (l) => isDef(l.fromIndex) && !isDef(l.toIndex)
    )
    const toLines = lines.filter((l) => !isDef(l.fromIndex) && isDef(l.toIndex))

    fromLines.forEach((f) => {
      for (let i = 0; i < toLines.length; i++) {
        const t = toLines[i]
        if (t.point) {
          continue
        }
        if (f.colorIndex === t.colorIndex) {
          f.toIndex = t.toIndex
          toLines.splice(i, 1)
          break
        }
      }

      result.push(f)
    })

    return result.concat(toLines)
  }
  public static merge(a: BranchPoint, b: BranchPoint): BranchPoint {
    if (!a) {
      return b
    }
    return new BranchPoint({
      commit: a.commit || b.commit,
      index: a.index,
      lines: BranchPoint.mergeLines(a.lines.concat(b.lines)),
      tag: a.tag || b.tag,
    })
  }
  public index: number
  public commit: boolean
  public lines: BranchLine[]
  public tag?: string
  public constructor({
    index,
    commit = false,
    lines = [],
    tag,
  }: {
    index: number
    commit?: boolean
    lines?: BranchLine[]
    tag?: string
  }) {
    this.index = index
    this.commit = commit
    this.lines = lines
    this.tag = tag
  }
}
