import { BranchPoint } from './BranchPoint'

export class BranchLine {
  public fromIndex?: number
  public toIndex?: number
  public colorIndex: number
  public point?: BranchPoint
  public constructor({
    fromIndex,
    toIndex,
    colorIndex,
    point,
  }: {
    fromIndex?: number
    toIndex?: number
    colorIndex: number
    point?: BranchPoint
  }) {
    this.fromIndex = fromIndex
    this.toIndex = toIndex
    this.colorIndex = colorIndex
    this.point = point
  }
}
