import {
  GridCanvasHelperAPI,
  CellContext,
  InlineCheckOption,
} from '../../ts-types'
import { CheckColumnState, GridInternal } from '../../ts-types-internal'
import { SwitchStyle } from '../style/SwitchStyle'
import { BaseCheckColumn } from './BaseCheckColumn'
import { getSwitchColumnStateId } from '../../internal/symbolManager'
import { obj } from '../../internal/utils'

const SWITCH_COLUMN_STATE_ID = getSwitchColumnStateId()

export class SwitchColumn<T> extends BaseCheckColumn<T> {
  public get StyleClass(): typeof SwitchStyle {
    return SwitchStyle
  }
  public clone(): SwitchColumn<T> {
    return new SwitchColumn(this)
  }
  protected getState(grid: GridInternal<T>): CheckColumnState {
    let state = grid[SWITCH_COLUMN_STATE_ID]
    if (!state) {
      state = { block: {}, elapsed: {} }
      obj.setReadonly(grid, SWITCH_COLUMN_STATE_ID, state)
    }
    return state
  }
  protected doDrawInternal(
    helper: GridCanvasHelperAPI,
    value: boolean,
    context: CellContext,
    opt: InlineCheckOption
  ): void {
    helper.switch(value, context, opt)
  }
}
