import { isPromise } from '../../internal/utils'
import { DataSource } from '../DataSource'
import { FilterDataSource } from '../FilterDataSource'
import { DataSourceIterator } from './DataSourceIterator'
import { MaybePromiseOrUndef } from '../../ts-types'

export type Filter<T> = (record: T | undefined) => boolean

export class FilterData<T> {
  private _owner: FilterDataSource<T>
  private _dataSourceItr: DataSourceIterator<T>
  private _filter: Filter<T>
  private _filteredList: (T | undefined)[]
  private _queues: (Promise<T | undefined> | null)[]
  private _cancel: boolean = false
  public constructor(
    dc: FilterDataSource<T>,
    original: DataSource<T>,
    filter: Filter<T>
  ) {
    this._owner = dc
    this._dataSourceItr = new DataSourceIterator(original)
    this._filter = filter
    this._filteredList = []
    this._queues = []
  }
  public get filter() {
    return this._filter
  }
  public get(index: number): MaybePromiseOrUndef<T> {
    if (this._cancel) {
      return undefined
    }
    const filteredList = this._filteredList
    if (index < filteredList.length) {
      return filteredList[index]
    }
    const queues = this._queues
    const indexQueue = queues[index]
    if (indexQueue) {
      return indexQueue
    }
    return queues[index] || this._findIndex(index)
  }
  public cancel(): void {
    this._cancel = true
  }
  private _findIndex(index: number): MaybePromiseOrUndef<T> {
    if (window.Promise) {
      const timeout = Date.now() + 100
      let count = 0
      return this._findIndexWithTimeout(index, () => {
        count++
        if (count >= 100) {
          count = 0
          return timeout < Date.now()
        }
        return false
      })
    }
    return this._findIndexWithTimeout(index, () => false)
  }
  private _findIndexWithTimeout(
    index: number,
    testTimeout: () => boolean
  ): MaybePromiseOrUndef<T> {
    const filteredList = this._filteredList
    const filter = this._filter
    const dataSourceItr = this._dataSourceItr

    const queues = this._queues

    while (dataSourceItr.hasNext()) {
      if (this._cancel) {
        return undefined
      }
      const record = dataSourceItr.next()
      if (isPromise(record)) {
        dataSourceItr.movePrev()
        return (queues[index] = record.then((value: any) => {
          queues[index] = null
          return this.get(index)
        }))
      }
      if (filter(record)) {
        filteredList.push(record)
        if (index < filteredList.length) {
          return filteredList[index]
        }
      }
      if (testTimeout()) {
        const promise = new Promise((resolve) => {
          setTimeout(() => {
            resolve(undefined)
          }, 300)
        })
        const queue = promise.then(() => {
          queues[index] = null
          return this.get(index)
        })
        queues[index] = queue
        return queue
      }
    }
    const dc = this._owner
    dc.length = filteredList.length
    return undefined
  }
}
