export default class Rect {
  public static bounds(
    left: number,
    top: number,
    right: number,
    bottom: number
  ) {
    return new Rect(left, top, right - left, bottom - top)
  }
  public static max(rect1: Rect, rect2: Rect) {
    return Rect.bounds(
      Math.min(rect1.left, rect2.left),
      Math.min(rect1.top, rect2.top),
      Math.max(rect1.right, rect2.right),
      Math.max(rect1.bottom, rect2.bottom)
    )
  }
  private _left: number
  private _top: number
  private _width: number
  private _height: number
  private _right: number | undefined
  private _bottom: number | undefined
  public constructor(left: number, top: number, width: number, height: number) {
    this._left = left
    this._top = top
    this._width = width
    this._height = height
    this._right = undefined
  }
  public get left() {
    return this._left
  }
  public set left(left) {
    const right = this.right
    this._left = left
    this.right = right
  }
  public get top() {
    return this._top
  }
  public set top(top) {
    const bottom = this.bottom
    this._top = top
    this.bottom = bottom
  }
  public get width() {
    return this._width
  }
  public set width(width) {
    this._width = width
    this._right = undefined
  }
  public get height() {
    return this._height
  }
  public set height(height) {
    this._height = height
    this._bottom = undefined
  }
  public get right() {
    return this._right !== undefined
      ? this._right
      : (this._right = this.left + this.width)
  }
  public set right(right) {
    this._right = right
    this.width = right - this.left
  }
  public get bottom() {
    return this._bottom !== undefined
      ? this._bottom
      : (this._bottom = this.top + this.height)
  }
  public set bottom(bottom) {
    this._bottom = bottom
    this.height = bottom - this.top
  }
  public offsetLeft(offset: number) {
    this._left += offset
    this._right = undefined
  }
  public offsetTop(offset: number) {
    this._top += offset
    this._bottom = undefined
  }
  public copy() {
    return new Rect(this.left, this.top, this.width, this.height)
  }
  public intersection(rect: Rect) {
    const x0 = Math.max(this.left, rect.left)
    const x1 = Math.min(this.left + this.width, rect.left + rect.width)
    if (x0 <= x1) {
      const y0 = Math.max(this.top, rect.top)
      const y1 = Math.min(this.top + this.height, rect.top + rect.height)
      if (y0 <= y1) {
        return Rect.bounds(x0, y0, x1, y1)
      }
    }
    return null
  }
  public contains(another: Rect) {
    return (
      this.left <= another.left &&
      this.left + this.width >= another.left + another.width &&
      this.top <= another.top &&
      this.top + this.height >= another.top + another.height
    )
  }
  public inPoint(x: number, y: number) {
    return (
      this.left <= x &&
      this.left + this.width >= x &&
      this.top <= y &&
      this.top + this.height >= y
    )
  }
}
