import { LookupStyleOption } from '../../ts-types'
import { Style } from './Style'
import { defaults } from '../../internal/utils'

let defaultLookupStyle: LookupStyle

export class LookupStyle extends Style {
  public static get DEFAULT() {
    return defaultLookupStyle
      ? defaultLookupStyle
      : (defaultLookupStyle = new LookupStyle())
  }
  public constructor(style: LookupStyleOption = {}) {
    super(defaults(style, { textAlign: 'left' }))
    this.appearance = style.appearance || 'menulist-button'
  }
  public clone(): LookupStyle {
    return new LookupStyle(this)
  }
}
