import * as columnUtils from './columnUtils'
import {
  CellAddress,
  MenuColumnOption,
  ColumnMenuItemOptions,
  ListGridAPI,
  GridCanvasHelperAPI,
  SimpleColumnMenuItemOption,
  CellContext,
} from '../../ts-types'
import { DrawCellInfo } from '../../ts-types-internal'
import { BaseColumn } from './BaseColumn'
import { MenuStyle } from '../style/MenuStyle'
import { isDef } from '../../internal/utils'
import menuItems from '../../internal/menu-items'

export class MenuColumn<T> extends BaseColumn<T, unknown> {
  private _options: SimpleColumnMenuItemOption[]
  public constructor(options: MenuColumnOption<T> = {}) {
    super(options)
    this._options = menuItems.normalize(options.options)
  }
  public get StyleClass(): typeof MenuStyle {
    return MenuStyle
  }
  public get options(): SimpleColumnMenuItemOption[] {
    return this._options
  }
  public clone(): MenuColumn<T> {
    return new MenuColumn(this)
  }
  public withOptions(options: ColumnMenuItemOptions): MenuColumn<T> {
    const c = this.clone()
    c._options = menuItems.normalize(options)
    return c
  }
  public drawInternal(
    value: string,
    context: CellContext,
    style: MenuStyle,
    helper: GridCanvasHelperAPI,
    grid: ListGridAPI<T>,
    { getCell, getIcon }: DrawCellInfo<T>
  ): void {
    const {
      textAlign,
      textBaseline,
      font,
      padding,
      textOverflow,
      appearance,
    } = style
    let { color } = style
    const text = this._convertInternal(value, getCell(), grid)
    helper.testFontLoad(font, text, context)
    columnUtils.loadIcons(getIcon(), context, helper, (icons, ctx) => {
      const basePadding = helper.toBoxPixelArray(padding || 0, context, font)
      const textPadding = basePadding.slice(0)
      textPadding[1] += 26 // icon padding
      const iconPadding = basePadding.slice(0)
      iconPadding[1] += 8
      if (!isDef(color) && (!isDef(value) || value === '')) {
        color = 'rgba(0, 0, 0, .38)'
      }
      helper.text(text, ctx, {
        color,
        font,
        icons,
        padding: textPadding,
        textAlign,
        textBaseline,
        textOverflow,
      })
      if (appearance === 'menulist-button') {
        // draw dropdown arrow icon
        helper.text('', context, {
          color,
          font,
          icons: [
            {
              color: 'rgba(0, 0, 0, .54)',
              path: 'M0 2 5 7 10 2z',
              width: 10,
            },
          ],
          padding: iconPadding,
          textAlign: 'right',
          textBaseline,
        })
      } else if (appearance !== 'none') {
        window.console.warn(`unsupported appearance:${appearance}`)
      }
    })
  }
  protected convertInternal(value: unknown) {
    return value
  }
  private _convertInternal(
    value: unknown,
    cell: CellAddress,
    grid: ListGridAPI<T>
  ): string {
    const options = this._options
    for (const option of options) {
      if (option.value === value) {
        value = option.label
        break
      }
    }
    return super.doConvertInternal(value, cell, grid) as string
  }
  public getCopyCellValue(
    value: unknown,
    grid: ListGridAPI<T>,
    cell: CellAddress
  ): string {
    return this._convertInternal(value, cell, grid)
  }
}
