import FontWatchRunner from './legacy/fontwatch/FontWatchRunner'
import { isNode } from './utils'

const loads: {
  [font: string]: boolean
  all: boolean
} = {
  all: false,
}
let load: (font: string, testStr: string, callback: () => void) => void
let check: (font: string, testStr: string) => boolean
if (isNode) {
  load = (font, testStr, callback) => {
    callback()
  }
  check = () => {
    return false
  }
} else {
  const legacy = !(document as any).fonts
  load = legacy
    ? (font: string, testStr: string, callback: () => void) => {
        // for legacy(IE)
        if (loads[`${font} @ ${testStr}`]) {
          callback()
          return
        }
        FontWatchRunner.load(
          font,
          testStr,
          () => {
            loads[`${font} @ ${testStr}`] = true
            callback()
          },
          () => {
            loads[`${font} @ ${testStr}`] = true
            callback()
          }
        )
      }
    : (font: string, testStr: string, callback: () => void) => {
        if (loads.all || loads[font]) {
          callback()
          return
        }
        ;(document as any).fonts.ready.then(() => {
          loads.all = true
        })
        ;(document as any).fonts.load(font).then(() => {
          loads[font] = true
          callback()
        })
      }
  check = legacy
    ? (font: string, testStr: string) => {
        // for legacy(IE)
        if (loads[`${font} @ ${testStr}`]) {
          return true
        }
        load(font, testStr, () => {
          // nothing
        })
        return false
      }
    : (font: string, testStr: string) => {
        if (loads.all || loads[font]) {
          return true
        }
        if (!(document as any).fonts.check(font)) {
          load(font, testStr, () => {
            // nothing
          })
          return false
        }
        return true
      }
}

export default {
  check,
  load,
}
