import {
  CellContext,
  GridCanvasHelperAPI,
  ListGridAPI,
  SortState,
} from '../../ts-types'
import { DrawCellInfo, SimpleColumnIconOption } from '../../ts-types-internal'
import { BaseHeader } from './BaseHeader'
import { SortHeaderStyle } from '../style/SortHeaderStyle'
import { getFontSize } from '../../internal/canvases'
import { isDef, cellInRange } from '../../internal/utils'

export class SortHeader<T> extends BaseHeader<T> {
  public get StyleClass(): typeof SortHeaderStyle {
    return SortHeaderStyle
  }
  public drawInternal(
    value: string,
    context: CellContext,
    style: SortHeaderStyle,
    helper: GridCanvasHelperAPI,
    grid: ListGridAPI<T>,
    { drawCellBase }: DrawCellInfo<T>
  ): void {
    const textAlign = style.textAlign
    const textBaseline = style.textBaseline || 'middle'
    const color = style.color
    const bgColor = style.bgColor
    const padding = style.padding
    const lineHeight = style.lineHeight
    const autoWrapText = style.autoWrapText
    const lineClamp = style.lineClamp
    const font = style.font
    const textOverflow = style.textOverflow
    const sortArrowColor = style.sortArrowColor

    if (bgColor) {
      drawCellBase({
        bgColor,
      })
    }

    const state = grid.sortState as SortState
    let order = undefined
    const col = context.col
    const row = context.row
    const range = grid.getCellRange(col, row)
    if (cellInRange(range, state.col, state.row)) {
      ;({ order } = state)
    }

    const ctx = context.getContext()
    const arrowSize = getFontSize(ctx, font).width * 1.2

    const v = isDef(value) ? value + '' : ''
    const multilines = v
      .replace(/\r?\n/g, '\n')
      .replace(/\r/g, '\n')
      .split('\n')
    const icons: SimpleColumnIconOption[] = [
      {
        color:
          helper.getColor(
            sortArrowColor || helper.theme.header.sortArrowColor,
            col,
            row,
            ctx
          ) || 'rgba(0, 0, 0, 0.38)',
        name: isDef(order)
          ? order === 'asc'
            ? 'arrowDownward'
            : 'arrowUpward'
          : undefined,
        width: arrowSize,
      },
    ]
    if (multilines.length > 1) {
      helper.multilineText(multilines, context, {
        autoWrapText,
        color,
        font,
        icons,
        lineClamp,
        lineHeight,
        padding,
        textAlign,
        textBaseline,
        textOverflow,
      })
    } else {
      helper.text(multilines, context, {
        color,
        font,
        icons,
        padding,
        textAlign,
        textBaseline,
        textOverflow,
      })
    }
  }
}
