import { BaseAction } from './BaseAction'
import { EditorOption } from '../../ts-types'

export abstract class Editor<T> extends BaseAction<T> {
  protected _readOnly: boolean | ((record: T) => boolean)
  public constructor(options: EditorOption<T> = {}) {
    super(options)
    this._readOnly = options.readOnly || (options as any).readonly || false
  }
  public get editable(): boolean {
    return true
  }
  public get readOnly() {
    return this._readOnly
  }
  public set readOnly(readOnly) {
    if (this._readOnly !== readOnly) {
      this._readOnly = readOnly
      this.onChangeReadOnlyInternal()
    }
  }
  public get readonly() {
    window.console.warn(
      "'readonly' is deprecated, please use 'readOnly' instead"
    )
    return this.readOnly
  }
  public set readonly(readOnly) {
    window.console.warn(
      "'readonly' is deprecated, please use 'readOnly' instead"
    )
    this.readOnly = readOnly
  }
  protected onChangeReadOnlyInternal() {
    // nothing
  }
}
