import * as columnUtils from './columnUtils'
import {
  ColumnOption,
  DrawCallback,
  CellContext,
  GridCanvasHelperAPI,
  ListGridAPI,
  CellStyle,
} from '../../ts-types'
import { BaseColumn } from './BaseColumn'
import { Style } from '../style/Style'
import { DrawCellInfo } from '../../ts-types-internal'
import { getInlineInputEditorStateId } from '../../internal/symbolManager'
import Rect from '../../internal/Rect'

const INLINE_INPUT_EDITOR_STATE_ID = getInlineInputEditorStateId()

export class Column<T> extends BaseColumn<T, string> {
  private _draw?: DrawCallback
  private _cellStyle?: CellStyle
  public constructor(options: ColumnOption<T> = {}) {
    super(options)
    this._draw = options.draw
    this._cellStyle = options.cellStyle
  }
  public get StyleClass() {
    return Style
  }
  public clone(): Column<T> {
    return new Column(this)
  }
  public get draw() {
    return this._draw
  }
  public get cellStyle(): CellStyle | undefined {
    return this._cellStyle
  }
  public reviseAttachCellsArea(rect: Rect, row: number, grid: ListGridAPI<T>) {
    columnUtils.reviseAttachCellsArea(rect, row, grid, this._cellStyle)
  }
  public reviseAttachCellsPadding(
    padding: [number, number, number, number],
    row: number,
    grid: ListGridAPI<T>
  ) {
    super.reviseAttachCellsPadding(padding, row, grid)
    columnUtils.reviseAttachCellsPadding(padding, row, grid, this._cellStyle)
  }
  public drawInternal(
    value: string,
    context: CellContext,
    style: Style,
    helper: GridCanvasHelperAPI,
    grid: ListGridAPI<T>,
    { getIcon, getRecord }: DrawCellInfo<T>
  ): void {
    const { textAlign, textBaseline, color, font, textOverflow } = style

    helper.testFontLoad(font, value, context)
    columnUtils.loadIcons(getIcon(), context, helper, (icons, ctx) => {
      const isCustomDraw = columnUtils.customDraw<T>(
        helper,
        this.draw,
        value,
        context,
        grid,
        getRecord()
      )
      if (!isCustomDraw) {
        helper.text(value, ctx, {
          color,
          font,
          icons,
          padding: columnUtils.getActionTextPadding(context, helper, style),
          textAlign,
          textBaseline,
          textOverflow,
        })
      }
      columnUtils.drawActionButton(
        grid,
        context,
        INLINE_INPUT_EDITOR_STATE_ID,
        helper,
        style
      )
      columnUtils.drawAttachArea(
        grid,
        getRecord(),
        context,
        helper,
        this._cellStyle
      )
    })
  }
}
