import { Style } from './Style'
import { SortHeaderStyleOption, ColorDef } from '../../ts-types'

let defaultSortHeaderStyle: SortHeaderStyle
export class SortHeaderStyle extends Style {
  public static get DEFAULT(): SortHeaderStyle {
    return defaultSortHeaderStyle
      ? defaultSortHeaderStyle
      : (defaultSortHeaderStyle = new SortHeaderStyle())
  }
  private _sortArrowColor?: ColorDef
  public constructor(style: SortHeaderStyleOption = {}) {
    super(style)
    this._sortArrowColor = style.sortArrowColor
  }
  public get sortArrowColor(): ColorDef | undefined {
    return this._sortArrowColor
  }
  public set sortArrowColor(sortArrowColor) {
    this._sortArrowColor = sortArrowColor
    this.doChangeStyle()
  }
  public clone(): SortHeaderStyle {
    return new SortHeaderStyle(this)
  }
}
