import { MenuStyleOption } from '../../ts-types'
import { Style } from './Style'

let defaultMenuStyle: MenuStyle

export class MenuStyle extends Style {
  public static get DEFAULT(): MenuStyle {
    return defaultMenuStyle
      ? defaultMenuStyle
      : (defaultMenuStyle = new MenuStyle())
  }
  public constructor(style: MenuStyleOption = {}) {
    super(style)
    this.appearance = style.appearance || 'menulist-button'
  }
  public clone(): MenuStyle {
    return new MenuStyle(this)
  }
}
