import { BaseMessage } from './BaseMessage'
import { ErrorMessageElement } from './internal/ErrorMessageElement'
import { drawExclamationMarkBox } from './messageUtils'
import { DrawCellInfo } from '../../ts-types-internal'
import {
  MessageObject,
  CellContext,
  ColumnStyle,
  GridCanvasHelperAPI,
  ListGridAPI,
} from '../../ts-types'
import { cellInRange } from '../../internal/utils'

const RED_A100 = '#ff8a80'

export class ErrorMessage<T> extends BaseMessage<T> {
  public createMessageElementInternal() {
    return new ErrorMessageElement()
  }
  public drawCellMessageInternal(
    message: MessageObject,
    context: CellContext,
    style: ColumnStyle,
    helper: GridCanvasHelperAPI,
    grid: ListGridAPI<T>,
    info: DrawCellInfo<T>
  ): void {
    const bgColor = style.bgColor
    const { select } = context.getSelection()

    if (
      !cellInRange(
        grid.getCellRange(context.col, context.row),
        select.col,
        select.row
      ) ||
      !grid.hasFocusGrid()
    ) {
      helper.drawBorderWithClip(context, (ctx) => {
        drawExclamationMarkBox(
          context,
          {
            bgColor: RED_A100,
            color: bgColor,
          },
          helper
        )
      })
    }
  }
}
