import { CheckStyleOption } from '../../ts-types'
import { BaseCheckStyle } from './BaseCheckStyle'

let defaultCheckStyle: CheckStyle

export class CheckStyle extends BaseCheckStyle {
  public static get DEFAULT(): CheckStyle {
    return defaultCheckStyle
      ? defaultCheckStyle
      : (defaultCheckStyle = new CheckStyle())
  }
  public constructor(style: CheckStyleOption = {}) {
    super(style)
  }
  public clone(): CheckStyle {
    return new CheckStyle(this)
  }
}
