import Rect from '../../internal/Rect'
import { DrawLayers } from '../draw-grid/DrawLayers'
import { DrawGrid } from '../DrawGrid'
import { Selection } from './Selection'
import { CellRange, CellContext, CellSelection } from '../../ts-types'

export function createCellRange(
  startCol: number,
  startRow: number,
  endCol: number,
  endRow: number
): CellRange {
  return {
    start: { col: startCol, row: startRow },
    end: { col: endCol, row: endRow },
  }
}

/**
 * Context of cell drawing
 */
export class DrawCellContext implements CellContext {
  private _col: number
  private _row: number
  private _mode: number
  private _range: CellRange
  private _ctx: CanvasRenderingContext2D | null
  private _rect: Rect | null
  private _drawRect: Rect | null
  private _drawing: boolean
  private _selection: Selection
  private _drawLayers: DrawLayers
  private _childContexts: DrawCellContext[]
  private _cancel?: boolean
  private _grid?: DrawGrid
  private _onTerminate?: () => void
  private _rectFilter: ((base: Rect) => Rect) | null = null
  /**
   * constructor
   */
  public constructor(
    col: number,
    row: number,
    ctx: CanvasRenderingContext2D,
    rect: Rect,
    drawRect: Rect | null,
    drawing: boolean,
    selection: Selection,
    drawLayers: DrawLayers
  ) {
    this._col = col
    this._row = row
    this._mode = 0
    this._range = createCellRange(col, row, col, row)
    this._ctx = ctx
    this._rect = rect
    this._drawRect = drawRect
    this._drawing = drawing
    this._selection = selection
    this._drawLayers = drawLayers
    this._childContexts = []
  }
  public get drawing() {
    if (this._mode === 0) {
      return this._drawing
    } else {
      return true
    }
  }
  public get row() {
    return this._row
  }
  public get col() {
    return this._col
  }
  public get range() {
    return this._range
  }
  public set range(range) {
    this._range = range
  }
  public cancel() {
    this._cancel = true
    this._childContexts.forEach((ctx) => {
      ctx.cancel()
    })
  }
  /**
   * select status.
   */
  public getSelection(): CellSelection {
    return {
      border: this._selection.border(this._col, this._row),
      dragged: this._selection.drag.inDrag(this._col, this._row),
      select: this._selection.drag.select,
      range: this._selection.range,
    }
  }
  /**
   * Canvas context.
   */
  public getContext(): CanvasRenderingContext2D {
    if (this._mode === 0) {
      return this._ctx as CanvasRenderingContext2D
    } else {
      return (this._grid as DrawGrid)._getInitContext()
    }
  }
  /**
   * Rectangle of cell.
   */
  public getRect(): Rect {
    const rectFilter = this._rectFilter
    return rectFilter
      ? rectFilter(this._getRectInternal())
      : this._getRectInternal()
  }
  public setRectFilter(rectFilter: (base: Rect) => Rect): void {
    this._rectFilter = rectFilter
  }
  /**
   * Rectangle of Drawing range.
   */
  public getDrawRect(): Rect | null {
    if (this._cancel) {
      return null
    }
    if (this._mode === 0) {
      return this._drawRect
    } else {
      if (this._isOutOfRange()) {
        return null
      }
      const absoluteRect = (this._grid as DrawGrid).getCellRect(
        this._col,
        this._row
      )
      return this._toRelativeDrawRect(absoluteRect)
    }
  }
  /**
   * get Context of current state
   */
  public toCurrentContext(): DrawCellContext {
    if (this._mode === 0) {
      return this
    } else {
      const absoluteRect = (this._grid as DrawGrid).getCellRect(
        this._col,
        this._row
      )
      const rect = (this._grid as DrawGrid)._toRelativeRect(absoluteRect)
      const drawRect = this._isOutOfRange()
        ? null
        : this._toRelativeDrawRect(absoluteRect)
      const context = new DrawCellContext(
        this._col,
        this._row,
        this.getContext(),
        rect,
        drawRect,
        this.drawing,
        this._selection,
        this._drawLayers
      )
      // toCurrentContext 调用 this.toCurrentContext
      context.toCurrentContext = this.toCurrentContext.bind(this)
      this._childContexts.push(context)
      if (this._cancel) {
        context.cancel()
      }
      context._rectFilter = this._rectFilter
      return context
    }
  }
  public addLayerDraw(
    level: number,
    fn: (ctx: CanvasRenderingContext2D) => void
  ) {
    this._drawLayers.addDraw(level, fn)
  }
  public _delayMode(grid: DrawGrid, onTerminate: () => void) {
    this._mode = 1
    this._ctx = null
    this._rect = null
    this._drawRect = null
    this._grid = grid
    this._onTerminate = onTerminate
  }
  /**
   * terminate
   */
  public terminate(): void {
    if (this._mode !== 0) {
      this._onTerminate?.()
    }
  }
  private _getRectInternal(): Rect {
    if (this._mode === 0) {
      return this._rect as Rect
    } else {
      if (this._rect) {
        return this._rect
      }
      return (this._grid as DrawGrid).getCellRelativeRect(this._col, this._row)
    }
  }
  private _isOutOfRange(): boolean {
    const { colCount, rowCount } = this._grid as DrawGrid
    return colCount <= this._col || rowCount <= this._row
  }
  private _toRelativeDrawRect(absoluteRect: Rect): Rect | null {
    const grid = this._grid as DrawGrid
    const visibleRect = grid._getVisibleRect()
    let rect = absoluteRect.copy()
    if (!rect.intersection(visibleRect)) {
      return null
    }

    const isFrozenCell = grid.isFrozenCell(this._col, this._row)
    if (grid.frozenColCount >= 0 && (!isFrozenCell || !isFrozenCell.col)) {
      const fRect = grid.getCellRect(grid.frozenColCount - 1, this._row)
      rect = Rect.bounds(
        Math.max(rect.left, fRect.right),
        rect.top,
        rect.right,
        rect.bottom
      )
    }
    if (grid.frozenRowCount >= 0 && (!isFrozenCell || !isFrozenCell.row)) {
      const fRect = grid.getCellRect(this._col, grid.frozenRowCount - 1)
      rect = Rect.bounds(
        rect.left,
        Math.max(rect.top, fRect.bottom),
        rect.right,
        rect.bottom
      )
    }

    if (!rect.intersection(visibleRect)) {
      return null
    }
    rect.offsetLeft(-visibleRect.left)
    rect.offsetTop(-visibleRect.top)

    return rect
  }
}
