import * as styleContents from '../style'
import {
  GridCanvasHelperAPI,
  ListGridAPI,
  CellContext,
  HeaderStyleOption,
  EventListenerId,
  LayoutObjectId,
} from '../../ts-types'
import { DrawCellInfo } from '../../ts-types-internal'
import { BaseStyle } from '../style/BaseStyle'
import { isDef } from '../../internal/utils'

export abstract class BaseHeader<T> {
  public constructor(_options = {}) {
    this.onDrawCell = this.onDrawCell.bind(this) // 修复范围
  }
  public get StyleClass(): typeof BaseStyle {
    return BaseStyle
  }
  public onDrawCell(
    cellValue: unknown,
    info: DrawCellInfo<T>,
    context: CellContext,
    grid: ListGridAPI<T>
  ) {
    const { style, drawCellBase } = info
    const helper = grid.getGridCanvasHelper()
    drawCellBase()
    // 文字
    this.drawInternal(
      this.convertInternal(cellValue),
      context,
      styleContents.of(
        style as HeaderStyleOption,
        grid.headerValues,
        this.StyleClass
      ),
      helper,
      grid,
      info
    )
  }
  public convertInternal(value: unknown): string {
    if (typeof value === 'function') {
      value = value()
    }
    return isDef(value) ? `${value}` : ''
  }
  public abstract drawInternal(
    value: string,
    context: CellContext,
    style: BaseStyle,
    helper: GridCanvasHelperAPI,
    grid: ListGridAPI<T>,
    info: DrawCellInfo<T>
  ): void
  public bindGridEvent(
    _grid: ListGridAPI<T>,
    _cellId: LayoutObjectId
  ): EventListenerId[] {
    return []
  }
}
