  public buildCheckBoxInline(
    check: boolean,
    context: CellContext,
    option: {
      animElapsedTime?: number
      uncheckBgColor?: ColorPropertyDefine
      checkBgColor?: ColorPropertyDefine
      borderColor?: ColorPropertyDefine
      textAlign?: CanvasTextAlign
      textBaseline?: CanvasTextBaseline
    } = {}
  ): InlineDrawer {
    const self = this
    const boxWidth = canvasHelper.measureCheckbox(context.getContext()).width

    function draw(info: InlineDrawOption): void {
      const col = context.col
      const row = context.row
      gridDrawCheckbox(info.ctx, info.rect, col, row, check, self, option, {
        offset: info.offset + 1,
        padding: {
          bottom: info.offsetBottom,
          left: info.offsetLeft + 1,
          right: info.offsetRight,
          top: info.offsetTop,
        },
      })
    }
    return new InlineDrawer({
      color: undefined,
      draw,
      height: boxWidth + 1,
      width: boxWidth + 3,
    })
  }
  public buildSwitchBtnInline(
    check: boolean,
    context: CellContext,
    option: {
      animElapsedTime?: number
      uncheckBgColor?: ColorPropertyDefine
      checkBgColor?: ColorPropertyDefine
      borderColor?: ColorPropertyDefine
      textAlign?: CanvasTextAlign
      textBaseline?: CanvasTextBaseline
    } = {}
  ): InlineDrawer {
    const self = this
    const btnSize = getSwitchBtnSize(context.getRect())

    function draw(info: InlineDrawOption) {
      const col = context.col
      const row = context.row
      gridDrawSwitchBtn(info.ctx, info.rect, col, row, check, self, option, {
        offset: info.offset + 1,
        padding: {
          bottom: info.offsetBottom,
          left: info.offsetLeft + 1,
          right: info.offsetRight,
          top: info.offsetTop,
        },
      })
    }
    return new InlineDrawer({
      color: undefined,
      draw,
      height: btnSize.height + 1,
      width: btnSize.width + 3,
    })
  }
