import {
  EditorOption,
  ListGridAPI,
  EventListenerId,
  CellAddress,
  LayoutObjectId,
} from '../../ts-types'
import { KEY_ENTER, KEY_F2 } from '../../core/draw-grid/KEY_CODE'
import { event, cellEquals } from '../../internal/utils'
import { isDisabledRecord, isReadOnlyRecord } from './action-utils'
import { DG_EVENT_TYPE } from '../../core/draw-grid/DG_EVENT_TYPE'
import { Editor } from './Editor'

import Rect from '../../internal/Rect'

function _inAttachCellArea<T>(
  grid: ListGridAPI<T>,
  col: number,
  row: number,
  event: TouchEvent | MouseEvent
) {
  let bool = false
  const relativePos = grid._getMouseRelativePoint(event)
  const rect = grid.getAttachCellsArea(grid.getCellRange(col, row)).rect
  if (relativePos) {
    if ((rect as Rect).inPoint(relativePos.x, relativePos.y)) {
      bool = true
    }
  }
  return bool
}

export abstract class BaseInputEditor<T> extends Editor<T> {
  public constructor(options: EditorOption<T> = {}) {
    super(options)
  }
  public open(grid: ListGridAPI<T>, cell: CellAddress): boolean {
    const allowOpen =
      !isReadOnlyRecord(this.readOnly, grid, cell.row) &&
      !isDisabledRecord(this.disabled, grid, cell.row)
    if (allowOpen) {
      this.onOpenCellInternal(grid, cell)
    }
    return allowOpen
  }
  public abstract clone(): BaseInputEditor<T>
  abstract onInputCellInternal(
    grid: ListGridAPI<T>,
    cell: CellAddress,
    inputValue: string
  ): void
  abstract onOpenCellInternal(grid: ListGridAPI<T>, cell: CellAddress): void
  abstract onChangeSelectCellInternal(
    grid: ListGridAPI<T>,
    cell: CellAddress,
    selected: boolean
  ): void
  abstract onSetInputAttrsInternal(
    grid: ListGridAPI<T>,
    cell: CellAddress,
    input: HTMLInputElement
  ): void
  abstract onGridScrollInternal(grid: ListGridAPI<T>): void
  public onBeforeKeydownInternal(
    _grid: ListGridAPI<T>,
    _keyCode: number,
    _e: KeyboardEvent,
    _cellId: LayoutObjectId
  ) {
    return true
  }
  public bindGridEvent(
    grid: ListGridAPI<T>,
    cellId: LayoutObjectId
  ): EventListenerId[] {
    const open = (cell: CellAddress): boolean => {
      return this.open(grid, cell)
    }

    const input = (cell: CellAddress, value: any) => {
      if (
        isReadOnlyRecord(this.readOnly, grid, cell.row) ||
        isDisabledRecord(this.disabled, grid, cell.row)
      ) {
        return
      }
      this.onInputCellInternal(grid, cell, value)
    }

    function isTarget(col: number, row: number): boolean {
      return grid.getLayoutCellId(col, row) === cellId
    }
    return [
      grid.listen(DG_EVENT_TYPE.INPUT_CELL, (e) => {
        if (!isTarget(e.col, e.row)) {
          return
        }
        input(
          {
            col: e.col,
            row: e.row,
          },
          e.value
        )
      }),
      grid.listen(DG_EVENT_TYPE.PASTE_CELL, (e) => {
        if (e.multi) {
          // ignore multi cell values
          return
        }
        const selectionRange = grid.selection.range
        if (!cellEquals(selectionRange.start, selectionRange.end)) {
          // ignore multi paste values
          return
        }
        if (!isTarget(e.col, e.row)) {
          return
        }
        event.cancel(e.event)
        input(
          {
            col: e.col,
            row: e.row,
          },
          e.normalizeValue
        )
      }),
      grid.listen(DG_EVENT_TYPE.DBLCLICK_CELL, (e) => {
        if (!isTarget(e.col, e.row)) {
          return
        }
        if (!_inAttachCellArea(grid, e.col, e.row, e.event)) {
          return
        }
        open({
          col: e.col,
          row: e.row,
        })
        event.cancel(e.event)
      }),
      grid.listen(DG_EVENT_TYPE.DBLTAP_CELL, (e) => {
        if (!isTarget(e.col, e.row)) {
          return
        }
        if (!_inAttachCellArea(grid, e.col, e.row, e.event)) {
          return
        }
        open({
          col: e.col,
          row: e.row,
        })
        event.cancel(e.event)
      }),
      grid.listen(DG_EVENT_TYPE.KEYDOWN, (e) => {
        if (
          (e.keyCode !== KEY_F2 && e.keyCode !== KEY_ENTER) ||
          (e.keyCode === KEY_ENTER && e.event.shiftKey)
        ) {
          return
        }
        const sel = grid.selection.select
        if (!isTarget(sel.col, sel.row)) {
          return
        }
        if (
          this.onBeforeKeydownInternal(grid, e.keyCode, e.event, cellId) ===
          false
        ) {
          return false
        }
        if (e.event.ctrlKey || e.event.metaKey) {
          return
        }
        if (
          open({
            col: sel.col,
            row: sel.row,
          })
        ) {
          event.cancel(e.event)
          e.stopCellMoving()
        }
      }),
      grid.listen(DG_EVENT_TYPE.SELECTED_CELL, (e) => {
        this.onChangeSelectCellInternal(
          grid,
          { col: e.col, row: e.row },
          e.selected
        )
      }),
      grid.listen(DG_EVENT_TYPE.SCROLL, () => {
        this.onGridScrollInternal(grid)
      }),
      grid.listen(DG_EVENT_TYPE.EDITABLEINPUT_CELL, (cell) => {
        if (!isTarget(cell.col, cell.row)) {
          return false
        }
        if (
          isReadOnlyRecord(this.readOnly, grid, cell.row) ||
          isDisabledRecord(this.disabled, grid, cell.row)
        ) {
          return false
        }
        return true
      }),
      grid.listen(DG_EVENT_TYPE.MODIFY_STATUS_EDITABLEINPUT_CELL, (cell) => {
        if (!isTarget(cell.col, cell.row)) {
          return
        }
        if (
          isReadOnlyRecord(this.readOnly, grid, cell.row) ||
          isDisabledRecord(this.disabled, grid, cell.row)
        ) {
          return
        }
        const range = grid.getCellRange(cell.col, cell.row)
        if (
          range.start.col !== range.end.col ||
          range.start.row !== range.end.row
        ) {
          const { input } = cell
          const baseRect = grid.getCellRect(cell.col, cell.row)
          const rangeRect = grid.getCellRangeRect(range)
          input.style.top = `${(
            parseFloat(input.style.top) +
            (rangeRect.top - baseRect.top)
          ).toFixed()}px`
          input.style.left = `${(
            parseFloat(input.style.left) +
            (rangeRect.left - baseRect.left)
          ).toFixed()}px`
          input.style.width = `${rangeRect.width.toFixed()}px`
          input.style.height = `${rangeRect.height.toFixed()}px`
        }
        this.onSetInputAttrsInternal(
          grid,
          {
            col: cell.col,
            row: cell.row,
          },
          cell.input
        )
      }),
    ]
  }
  public onPasteCellRangeBox(
    grid: ListGridAPI<T>,
    cell: CellAddress,
    value: string
  ): void {
    if (
      isReadOnlyRecord(this.readOnly, grid, cell.row) ||
      isDisabledRecord(this.disabled, grid, cell.row)
    ) {
      return
    }
    grid.doChangeValue(cell.col, cell.row, () => value)
  }
  public onDeleteCellRangeBox(grid: ListGridAPI<T>, cell: CellAddress): void {
    if (
      isReadOnlyRecord(this.readOnly, grid, cell.row) ||
      isDisabledRecord(this.disabled, grid, cell.row)
    ) {
      return
    }
    grid.doChangeValue(cell.col, cell.row, () => '')
  }
}
