import * as utils from './columnUtils'
import {
  ListGridAPI,
  MaybePromise,
  CellContext,
  GridCanvasHelperAPI,
} from '../../ts-types'
import { DrawCellInfo } from '../../ts-types-internal'
import { calcStartPosition } from '../../internal/canvases'
import { getCacheOrLoad } from '../../internal/imgs'
import Rect from '../../internal/Rect'
import { ImageStyle } from '../style/ImageStyle'
import { BaseColumn } from './BaseColumn'
import { getInlineInputEditorStateId } from '../../internal/symbolManager'

const INLINE_INPUT_EDITOR_STATE_ID = getInlineInputEditorStateId()

const MAX_LRU_CACHE_SIZE = 50
function getImage(url: MaybePromise<string>): MaybePromise<HTMLImageElement> {
  return getCacheOrLoad('ImageColumn', MAX_LRU_CACHE_SIZE, url)
}

function calcKeepAspectRatioSize(
  width: number,
  height: number,
  maxWidth: number,
  maxHeight: number
): {
  width: number
  height: number
} {
  let newWidth = width
  let newHeight = height
  if (newWidth > maxWidth) {
    newWidth = maxWidth
    newHeight = (newWidth * height) / width
  }
  if (newHeight > maxHeight) {
    newHeight = maxHeight
    newWidth = (newHeight * width) / height
  }
  return {
    height: newHeight,
    width: newWidth,
  }
}

export class ImageColumn<T> extends BaseColumn<T, HTMLImageElement> {
  public get StyleClass(): typeof ImageStyle {
    return ImageStyle
  }
  public onDrawCell(
    cellValue: MaybePromise<string>,
    info: DrawCellInfo<T>,
    context: CellContext,
    grid: ListGridAPI<T>
  ) {
    const img = cellValue ? getImage(cellValue) : ''
    return super.onDrawCell(img, info, context, grid)
  }
  public clone(): ImageColumn<T> {
    return new ImageColumn(this)
  }
  public drawInternal(
    value: HTMLImageElement,
    context: CellContext,
    style: ImageStyle,
    helper: GridCanvasHelperAPI,
    grid: ListGridAPI<T>,
    _info: DrawCellInfo<T>
  ) {
    const { textAlign, textBaseline, margin } = style

    if (value) {
      helper.drawWithClip(context, (ctx) => {
        ctx.textAlign = textAlign
        ctx.textBaseline = textBaseline

        const rect = (context.getRect() as Rect).copy()
        const padding = utils.getActionTextPadding(
          context,
          helper,
          style as any
        )
        rect.width -= padding[1]
        if (rect.width < 0) {
          rect.width = 0
        }

        if (style.imageSizing === 'keep-aspect-ratio') {
          const size = calcKeepAspectRatioSize(
            value.width,
            value.height,
            rect.width - margin * 2,
            rect.height - margin * 2
          )
          const width = size.width
          const height = size.height
          const pos = calcStartPosition(ctx, rect, width, height, {
            offset: margin,
          })
          ctx.drawImage(
            value,
            0,
            0,
            value.width,
            value.height,
            pos.x,
            pos.y,
            width,
            height
          )
        } else {
          ctx.drawImage(
            value,
            0,
            0,
            value.width,
            value.height,
            rect.left + margin,
            rect.top + margin,
            rect.width - margin * 2,
            rect.height - margin * 2
          )
        }
      })
    }
    utils.drawActionButton(
      grid,
      context,
      INLINE_INPUT_EDITOR_STATE_ID,
      helper,
      style as any
    )
  }
}
