import { ListGridAPI, CellAddress } from '../../ts-types'

export const isHeaderDisabledCell = <T>(
  option: boolean | ((grid: ListGridAPI<T>, cell: CellAddress) => boolean),
  grid: ListGridAPI<T>,
  cell: CellAddress
) => {
  if (typeof option === 'function') {
    return !!option(grid, cell)
  }
  return !!option
}
