import { ImageStyleOption, Appearance } from '../../ts-types'
import { BaseStdStyle } from './BaseStdStyle'
import { defaults } from '../../internal/utils'

let defaultImageStyle: ImageStyle

export class ImageStyle extends BaseStdStyle {
  public static get DEFAULT(): ImageStyle {
    return defaultImageStyle
      ? defaultImageStyle
      : (defaultImageStyle = new ImageStyle())
  }
  private _margin: number
  private _imageSizing?: 'keep-aspect-ratio'
  private _appearance?: Appearance
  public constructor(style: ImageStyleOption = {}) {
    super(defaults(style, { textAlign: 'center' }))
    this._imageSizing = style.imageSizing
    this._margin = style.margin || 4
    this._appearance = style.appearance || 'none'
  }
  public get imageSizing(): 'keep-aspect-ratio' | undefined {
    return this._imageSizing
  }
  public set imageSizing(imageSizing) {
    this._imageSizing = imageSizing
    this.doChangeStyle()
  }
  public get margin(): number {
    return this._margin
  }
  public set margin(margin) {
    this._margin = margin
    this.doChangeStyle()
  }
  public get appearance(): Appearance | undefined {
    return this._appearance
  }
  public set appearance(appearance) {
    this._appearance = appearance
    this.doChangeStyle()
  }
  public clone(): ImageStyle {
    return new ImageStyle(this)
  }
}
