import { DateColumnOption } from '../../ts-types'
import { DateStyle } from '../style/DateStyle'
import { Column } from './Column'

let defaultDateTimeFormat: Intl.DateTimeFormat

export class DateColumn<T> extends Column<T> {
  public static get defaultFormat() {
    return (
      defaultDateTimeFormat ||
      (defaultDateTimeFormat = new Intl.DateTimeFormat())
    )
  }
  public static set defaultFormat(fmt) {
    defaultDateTimeFormat = fmt
  }
  private _format?: Intl.DateTimeFormat
  public constructor(options: DateColumnOption<T> = {}) {
    super(options)
    this._format = options.format
  }
  public get StyleClass(): typeof DateStyle {
    return DateStyle
  }
  public get format(): Intl.DateTimeFormat | undefined {
    return this._format
  }
  public clone(): DateColumn<T> {
    return new DateColumn(this)
  }
  public withFormat(format: Intl.DateTimeFormat): DateColumn<T> {
    const c = this.clone()
    c._format = format
    return c
  }
  protected convertInternal(value: Date): string {
    if (value instanceof Date) {
      const format = this._format || DateColumn.defaultFormat
      return format.format(value)
    } else {
      return ''
    }
  }
}
