import { AnyFunction, ColorDef } from '../ts-types'
import { InlineDrawOption, Inline } from './Inline'

export type InlineDrawerFunction = (options: InlineDrawOption) => void
export class InlineDrawer extends Inline {
  private _draw: InlineDrawerFunction
  private _width: number
  // private _height: number
  private _color?: ColorDef
  public constructor({
    draw,
    width,
    // height,
    color,
  }: {
    draw: InlineDrawerFunction
    width: number
    height: number
    color?: ColorDef
  }) {
    super()

    this._draw = draw
    this._width = width
    // this._height = height
    this._color = color
  }
  public width(_arg: { ctx: CanvasRenderingContext2D }): number {
    return this._width
  }
  public font(): string | null {
    return null
  }
  public color(): ColorDef | null {
    return this._color ?? null
  }
  public canDraw(): boolean {
    return true
  }
  public onReady(_callback: AnyFunction): void {}
  public draw({
    ctx,
    canvasHelper,
    rect,
    offset,
    offsetLeft,
    offsetRight,
    offsetTop,
    offsetBottom,
  }: InlineDrawOption): void {
    this._draw({
      ctx,
      canvasHelper,
      rect,
      offset,
      offsetLeft,
      offsetRight,
      offsetTop,
      offsetBottom,
    })
  }
  public canBreak(): boolean {
    return false
  }
  public toString(): string {
    return ''
  }
}
