import { BaseStdStyle } from './BaseStdStyle'
import {
  ColorDef,
  HeaderStdStyleOption,
  TextOverflow,
  LineClamp,
} from '../../ts-types'

let defaultHeaderStyle: Style

export class Style extends BaseStdStyle {
  public static get DEFAULT(): Style {
    return defaultHeaderStyle
      ? defaultHeaderStyle
      : (defaultHeaderStyle = new Style())
  }
  private _color?: ColorDef
  private _font?: string
  private _padding?: number | string | (number | string)[]
  private _textOverflow: TextOverflow
  private _lineHeight: string | number
  private _autoWrapText: boolean
  private _lineClamp: LineClamp
  public constructor(style: HeaderStdStyleOption = {}) {
    super(style)
    this._color = style.color
    this._font = style.font
    this._padding = style.padding
    this._textOverflow = style.textOverflow || 'ellipsis'
    this._lineHeight = style.lineHeight || '1em'
    this._autoWrapText = style.autoWrapText || false
    this._lineClamp = style.lineClamp || 'auto'
  }
  public get color(): ColorDef | undefined {
    return this._color
  }
  public set color(color) {
    this._color = color
    this.doChangeStyle()
  }
  public get font(): string | undefined {
    return this._font
  }
  public set font(font) {
    this._font = font
    this.doChangeStyle()
  }
  public get padding(): number | string | (number | string)[] | undefined {
    return this._padding
  }
  public set padding(padding) {
    this._padding = padding
    this.doChangeStyle()
  }
  public get textOverflow(): TextOverflow {
    return this._textOverflow
  }
  public set textOverflow(textOverflow) {
    this._textOverflow = textOverflow
    this.doChangeStyle()
  }
  public get lineHeight(): string | number {
    return this._lineHeight
  }
  public set lineHeight(lineHeight) {
    this._lineHeight = lineHeight
    this.doChangeStyle()
  }
  public get lineClamp(): LineClamp {
    return this._lineClamp
  }
  public set lineClamp(lineClamp) {
    this._lineClamp = lineClamp
    this.doChangeStyle()
  }
  public get autoWrapText(): boolean {
    return this._autoWrapText
  }
  public set autoWrapText(autoWrapText) {
    this._autoWrapText = autoWrapText
    this.doChangeStyle()
  }
  public clone(): Style {
    return new Style(this)
  }
}
