import {
  CellContext,
  GridCanvasHelperAPI,
  InlineCheckOption,
} from '../../ts-types'
import { CheckColumnState, GridInternal } from '../../ts-types-internal'
import { CheckStyle } from '../style/CheckStyle'
import { BaseCheckColumn } from './BaseCheckColumn'
import { getCheckColumnStateId } from '../../internal/symbolManager'
import { obj } from '../../internal/utils'

const CHECK_COLUMN_STATE_ID = getCheckColumnStateId()

export class CheckColumn<T> extends BaseCheckColumn<T> {
  public get StyleClass() {
    return CheckStyle
  }
  public clone(): CheckColumn<T> {
    return new CheckColumn(this)
  }
  protected getState(grid: GridInternal<T>): CheckColumnState {
    let state = grid[CHECK_COLUMN_STATE_ID]
    if (!state) {
      state = { block: {}, elapsed: {} }
      obj.setReadonly(grid, CHECK_COLUMN_STATE_ID, state)
    }
    return state
  }
  protected doDrawInternal(
    helper: GridCanvasHelperAPI,
    value: boolean,
    context: CellContext,
    opt: InlineCheckOption
  ): void {
    helper.checkbox(value, context, opt)
  }
}
