import { ColumnActionOption } from '../ts-types'
import { Action } from './action/Action'
import { BaseAction } from './action/BaseAction'
import { BaseActionInput } from './action/BaseActionInput'
import { BaseInputEditor } from './action/BaseInputEditor'
import { BaseCheckEditor } from './action/BaseCheckEditor'
import { ButtonAction } from './action/ButtonAction'
import { CheckEditor } from './action/CheckEditor'
import { Editor } from './action/Editor'
import { SwitchEditor } from './action/SwitchEditor'
import { InlineInputEditor } from './action/InlineInputEditor'
import { InlineLookupEditor } from './action/InlineLookupEditor'
import { InlineMenuEditor } from './action/InlineMenuEditor'
import { InlineTextAreaEditor } from './action/InlineTextAreaEditor'
import { RadioEditor } from './action/RadioEditor'
import { SmallDialogInputEditor } from './action/SmallDialogInputEditor'

import { ImmutableCheckEditor } from './immutable/ImmutableCheckEditor'
import { ImmutableRadioEditor } from './immutable/ImmutableRadioEditor'
import { ImmutableInputEditor } from './immutable/ImmutableInputEditor'
import { ImmutableSwitchEditor } from './immutable/ImmutableSwitchEditor'

export interface ImmutableActions {
  CHECK: ImmutableCheckEditor<any>
  RADIO: ImmutableRadioEditor<any>
  INPUT: ImmutableInputEditor<any>
  SWITCH: ImmutableSwitchEditor<any>
}

export const ACTIONS: ImmutableActions = {
  CHECK: new ImmutableCheckEditor<any>(),
  RADIO: new ImmutableRadioEditor<any>(),
  INPUT: new ImmutableInputEditor<any>(),
  SWITCH: new ImmutableSwitchEditor<any>(),
}

export {
  BaseAction,
  Editor,
  BaseCheckEditor,
  BaseInputEditor,
  BaseActionInput,
  Action,
  CheckEditor,
  RadioEditor,
  SwitchEditor,
  ButtonAction,
  SmallDialogInputEditor,
  InlineInputEditor,
  InlineTextAreaEditor,
  InlineLookupEditor,
  InlineMenuEditor,
}

export function of<T>(
  columnAction: ColumnActionOption | BaseAction<T> | null | undefined
): BaseAction<T> | undefined {
  if (!columnAction) {
    return undefined
  } else if (typeof columnAction === 'string') {
    const key = columnAction.toUpperCase() as keyof typeof ACTIONS
    return ACTIONS[key] || of(null)
  } else {
    return columnAction
  }
}
