import * as utils from './columnUtils'
import {
  DrawCallback,
  DrawColumnOptions,
  ListGridAPI,
  GridCanvasHelperAPI,
  CellContext,
} from '../../ts-types'
import { DrawCellInfo } from '../../ts-types-internal'
import { DrawStyle } from '../style/DrawStyle'
import { BaseColumn } from './BaseColumn'
import { getInlineInputEditorStateId } from '../../internal/symbolManager'

const INLINE_INPUT_EDITOR_STATE_ID = getInlineInputEditorStateId()

export class DrawColumn<T> extends BaseColumn<T, unknown> {
  private _draw?: DrawCallback
  public constructor(options: DrawColumnOptions<T> = {}) {
    super(options)
    this._draw = options.draw
  }
  public get StyleClass() {
    return DrawStyle
  }
  public clone(): DrawColumn<T> {
    return new DrawColumn(this)
  }
  public get draw() {
    return this._draw
  }
  public drawInternal(
    value: any,
    context: CellContext,
    style: DrawStyle,
    helper: GridCanvasHelperAPI,
    grid: ListGridAPI<T>,
    { getRecord }: DrawCellInfo<T>
  ): void {
    utils.customDraw(helper, this.draw, value, context, grid, getRecord())
    utils.drawActionButton(
      grid,
      context,
      INLINE_INPUT_EDITOR_STATE_ID,
      helper,
      style as any
    )
  }
}
