import { CellAddress, EventListenerId, LayoutObjectId } from '../../ts-types'
import { GridInternal, CheckHeaderState } from '../../ts-types-internal'
import { bindCellClickAction, bindCellKeyAction } from './actionBind'
import { BaseAction } from './BaseAction'
import { animate } from '../../internal/animate'
import { isHeaderDisabledCell } from './action-utils'
import { KEY_ENTER, KEY_SPACE } from '../../core/draw-grid/KEY_CODE'

export abstract class BaseCheckAction<T> extends BaseAction<T> {
  public bindGridEvent(
    grid: GridInternal<T>,
    cellId: LayoutObjectId
  ): EventListenerId[] {
    const state = this.getState(grid)

    const action = ({ col, row }: CellAddress) => {
      const range = grid.getCellRange(col, row)
      const cellKey = `${range.start.col}:${range.start.row}`
      if (
        isHeaderDisabledCell(this.disabled, grid, {
          col: range.start.col,
          row: range.start.row,
        }) ||
        state.block[cellKey]
      ) {
        return
      }
      const checked = grid.getHeaderValue(range.start.col, range.start.row)
      grid.setHeaderValue(range.start.col, range.start.row, !checked)

      const onChange = () => {
        // checkbox animation
        animate(200, (point) => {
          if (point === 1) {
            delete state.elapsed[cellKey]
          } else {
            state.elapsed[cellKey] = point
          }
          grid.invalidateCellRange(range)
        })
      }
      onChange()
    }
    return [
      ...bindCellClickAction(grid, cellId, {
        action,
        mouseOver: (e) => {
          const range = grid.getCellRange(e.col, e.row)
          if (
            isHeaderDisabledCell(this.disabled, grid, {
              col: range.start.col,
              row: range.start.row,
            })
          ) {
            return false
          }
          state.mouseActiveCell = {
            col: e.col,
            row: e.row,
          }
          grid.invalidateCellRange(range)
          return true
        },
        mouseOut: (e) => {
          delete state.mouseActiveCell
          const range = grid.getCellRange(e.col, e.row)
          grid.invalidateCellRange(range)
        },
      }),
      ...bindCellKeyAction(grid, cellId, {
        action,
        acceptKeys: [KEY_ENTER, KEY_SPACE],
      }),
    ]
  }
  protected abstract getState(grid: GridInternal<T>): CheckHeaderState
}
