import { ColumnStyleOption } from '../ts-types'
import { BaseStyle } from './style/BaseStyle'
import { BaseStdStyle } from './style/BaseStdStyle'
import { BaseCheckStyle } from './style/BaseCheckStyle'
import { ButtonStyle } from './style/ButtonStyle'
import { CheckStyle } from './style/CheckStyle'
import { SwitchStyle } from './style/SwitchStyle'
import { DateStyle } from './style/DateStyle'
import { DrawStyle } from './style/DrawStyle'
import { IconStyle } from './style/IconStyle'
import { ImageStyle } from './style/ImageStyle'
import { LookupStyle } from './style/LookupStyle'
import { MenuStyle } from './style/MenuStyle'
import { MultilineTextStyle } from './style/MultilineTextStyle'
import { NumberStyle } from './style/NumberStyle'
import { PercentCompleteBarStyle } from './style/PercentCompleteBarStyle'
import { RadioStyle } from './style/RadioStyle'
import { Style } from './style/Style'
import { TreeStyle } from './style/TreeStyle'

const { EVENT_TYPE } = BaseStyle
export {
  EVENT_TYPE,
  BaseStyle,
  BaseStdStyle,
  BaseCheckStyle,
  Style,
  NumberStyle,
  CheckStyle,
  RadioStyle,
  SwitchStyle,
  ButtonStyle,
  ImageStyle,
  IconStyle,
  PercentCompleteBarStyle,
  MultilineTextStyle,
  MenuStyle,
  DateStyle,
  LookupStyle,
  TreeStyle,
  DrawStyle,
}

export function of(
  columnStyle: ColumnStyleOption | null | undefined,
  record: any,
  StyleClassDef: typeof BaseStyle = Style
): BaseStyle {
  if (columnStyle) {
    if (columnStyle instanceof BaseStyle) {
      return columnStyle
    } else if (typeof columnStyle === 'function') {
      return of(columnStyle(record), record, StyleClassDef)
    } else if (
      typeof columnStyle === 'string' &&
      record &&
      columnStyle in record
    ) {
      return of(record[columnStyle], record, StyleClassDef)
    }
    return new StyleClassDef(columnStyle as any)
  } else {
    return StyleClassDef.DEFAULT
  }
}
