import {
  ActionListener,
  ActionOption,
  ListGridAPI,
  CellAddress,
  LayoutObjectId,
} from '../../ts-types'
import { emptyFn, isDef } from '../../internal/utils'
import { isDisabledRecord } from './action-utils'
import { bindCellClickAction, bindCellKeyAction } from './actionBind'
import { BaseAction } from './BaseAction'

export class Action<T> extends BaseAction<T> {
  private _action: ActionListener<T>
  public constructor(options: ActionOption<T> = {}) {
    super(options)
    this._action = options.action || emptyFn
  }
  get editable(): boolean {
    return false
  }
  public get action() {
    return this._action
  }
  public set action(action) {
    this._action = action
  }
  public clone(): Action<T> {
    return new Action(this)
  }
  public getState(_grid: any): any {
    return {}
  }
  public bindGridEvent(grid: ListGridAPI<T>, cellId: LayoutObjectId): any[] {
    const state = this.getState(grid)
    const action = (cell: CellAddress) => {
      if (isDisabledRecord(this.disabled, grid, cell.row)) {
        return
      }
      const record = grid.getRowRecord(cell.row)
      this._action.apply(this, [
        record,
        {
          cell,
          grid,
        },
      ])
    }
    const actionMouse = (cell: CellAddress) => {
      if (state.mouseActive) {
        action(cell)
      }
    }
    const mouseOut = (cell: CellAddress) => {
      delete state.mouseRelativePos
      delete state.mouseActiveCell
      delete state.mouseActive
      const range = grid.getCellRange(cell.col, cell.row)
      grid.invalidateCellRange(range)
    }
    const mouseMove = (cell: CellAddress, event: any) => {
      if (isDisabledRecord(this.disabled, grid, cell.row)) {
        return false
      }
      state.mouseRelativePos = grid._getMouseRelativePoint(event)
      state.mouseActiveCell = {
        col: cell.col,
        row: cell.row,
      }
      const range = grid.getCellRange(cell.col, cell.row)
      grid.invalidateCellRange(range)
      return isDef(state.mouseActive) ? state.mouseActive : true
    }

    return [
      ...bindCellClickAction(grid, cellId, {
        action: actionMouse,
        mouseMove,
        mouseOut,
        mouseOver: mouseMove,
      }),
      ...bindCellKeyAction(grid, cellId, {
        action,
      }),
    ]
  }
  public onPasteCellRangeBox(): void {
    // noop
  }
  public onDeleteCellRangeBox(): void {
    // noop
  }
}
