import { EventHandler } from '../internal/EventHandler'
import { DataSource } from './DataSource'
import { FilterData, Filter } from './filter/FilterData'
import { MaybePromiseOrUndef, FieldDef, MaybePromise } from '../ts-types'

/**
 * grid data source for filter
 */
export class FilterDataSource<T> extends DataSource<T> {
  private _dataSource: DataSource<T>
  private _handler: EventHandler
  private _filterData: FilterData<T> | null = null
  public static get EVENT_TYPE(): typeof DataSource.EVENT_TYPE {
    return DataSource.EVENT_TYPE
  }
  public constructor(dataSource: DataSource<T>, filter: Filter<T>) {
    super(dataSource)
    this._dataSource = dataSource
    this.filter = filter
    const handler = (this._handler = new EventHandler())
    handler.on(dataSource, FilterDataSource.EVENT_TYPE.UPDATED_ORDER, () => {
      // reset
      this.filter = this.filter
    })
    for (const k in FilterDataSource.EVENT_TYPE) {
      if (FilterDataSource.EVENT_TYPE.hasOwnProperty(k)) {
        const type = (FilterDataSource.EVENT_TYPE as any)[k]
        handler.on(dataSource as any, type, (...args) =>
          this.fireListeners(type, ...args)
        )
      }
    }
  }
  public get filter(): Filter<T> | null {
    return this._filterData?.filter || null
  }
  public set filter(filter) {
    if (this._filterData) {
      this._filterData.cancel()
    }
    this._filterData = filter
      ? new FilterData(this, this._dataSource, filter)
      : null
    this.setLength(this._dataSource.length)
  }
  protected getOriginal(index: number): MaybePromiseOrUndef<T> {
    if (!this._filterData) {
      return super.getOriginal(index)
    }
    return this._filterData.get(index)
  }
  public sort(field: FieldDef<T>, order: 'desc' | 'asc'): MaybePromise<void> {
    return this._dataSource.sort(field, order)
  }
  get source(): any {
    return this._dataSource.source
  }
  get dataSource(): DataSource<T> {
    return this._dataSource
  }
  public dispose(): void {
    this._handler.dispose()
    super.dispose()
  }
  protected refreshInternal() {
    this.filter = this.filter
  }
}
