import * as utils from './columnUtils'
import {
  CellContext,
  GridCanvasHelperAPI,
  InlineCheckOption,
  DrawCallback,
  BaseCheckColumnOption,
} from '../../ts-types'
import {
  GridInternal,
  DrawCellInfo,
  CheckColumnState,
} from '../../ts-types-internal'
import { isDef } from '../../internal/utils'
import { BaseColumn } from './BaseColumn'
import { CheckStyle } from '../style'
import { toBoolean } from '../utils'

export abstract class BaseCheckColumn<T> extends BaseColumn<T, boolean> {
  private _draw?: DrawCallback
  public constructor(options: BaseCheckColumnOption<T> = {}) {
    super(options)
    this._draw = options.draw
  }
  public get draw() {
    return this._draw
  }
  public drawInternal(
    value: boolean,
    context: CellContext,
    style: CheckStyle,
    helper: GridCanvasHelperAPI,
    grid: GridInternal<T>,
    { getRecord }: DrawCellInfo<T>
  ): void {
    const {
      textAlign,
      textBaseline,
      borderColor,
      checkBgColor,
      uncheckBgColor,
    } = style

    const isCustomDraw = utils.customDraw(
      helper,
      this.draw,
      value,
      context,
      grid,
      getRecord()
    )
    if (!isCustomDraw) {
      const { col, row } = context
      const range = grid.getCellRange(col, row)
      const cellKey = `${range.start.col}:${range.start.row}`
      const state = this.getState(grid)
      const elapsed = state?.elapsed[cellKey]

      const opt: InlineCheckOption = {
        borderColor,
        checkBgColor,
        textAlign,
        textBaseline,
        uncheckBgColor,
      }
      if (isDef(elapsed)) {
        opt.animElapsedTime = elapsed
      }
      this.doDrawInternal(helper, value, context, opt)
    }
  }
  protected convertInternal(value: any) {
    return toBoolean(value)
  }
  protected abstract getState(grid: GridInternal<T>): CheckColumnState
  protected abstract doDrawInternal(
    helper: GridCanvasHelperAPI,
    value: boolean,
    context: CellContext,
    opt: InlineCheckOption
  ): void
}
