import {
  GridCanvasHelperAPI,
  CellContext,
  InlineAPI,
  InlineCheckOption,
} from '../../ts-types'
import { GridInternal, CheckHeaderState } from '../../ts-types-internal'
import { SwitchHeaderStyle } from '../style/SwitchHeaderStyle'
import { BaseCheckHeader } from './BaseCheckHeader'
import { getSwitchHeaderStateId } from '../../internal/symbolManager'
import { obj } from '../../internal/utils'

const SWITCH_HEADER_STATE_ID = getSwitchHeaderStateId()

export class SwitchHeader<T> extends BaseCheckHeader<T> {
  public get StyleClass(): typeof SwitchHeaderStyle {
    return SwitchHeaderStyle
  }
  public clone(): SwitchHeader<T> {
    return new SwitchHeader(this)
  }
  protected getState(grid: GridInternal<T>): CheckHeaderState {
    let state = grid[SWITCH_HEADER_STATE_ID]
    if (!state) {
      state = { elapsed: {}, block: {} }
      obj.setReadonly(grid, SWITCH_HEADER_STATE_ID, state)
    }
    return state
  }
  protected getInlineCheck(
    helper: GridCanvasHelperAPI,
    checked: boolean,
    context: CellContext,
    opt: InlineCheckOption
  ): InlineAPI {
    return helper.buildSwitchBtnInline(checked, context, opt)
  }
}
