import { MaybePromiseOrUndef } from '../../ts-types'
import { isPromise } from '../../internal/utils'
import { DataSource } from '../DataSource'

export class DataSourceIterator<T> {
  private _dataSource: DataSource<T>
  private _curIndex: number
  private _data: MaybePromiseOrUndef<T>[]
  public constructor(dataSource: DataSource<T>) {
    this._dataSource = dataSource
    this._curIndex = -1
    this._data = []
  }
  public hasNext(): boolean {
    const next = this._curIndex + 1
    return this._dataSource.length > next
  }
  public next(): MaybePromiseOrUndef<T> {
    const next = this._curIndex + 1
    const data = this._getIndexData(next)
    this._curIndex = next
    return data
  }
  public movePrev(): void {
    this._curIndex--
  }
  private _getIndexData(index: number, nest?: boolean): MaybePromiseOrUndef<T> {
    const dataSource = this._dataSource
    const data = this._data
    if (index < data.length) {
      return data[index]
    }

    if (dataSource.length <= index) {
      return undefined
    }
    const record = this._dataSource.get(index)
    data[index] = record
    if (isPromise(record)) {
      record.then((val) => {
        data[index] = val
      })
      if (!nest) {
        for (let i = 1; i <= 100; i++) {
          this._getIndexData(index + i, true)
        }
      }
    }
    return record
  }
}
