import { EventHandler } from '../../internal/EventHandler'
import { event } from '../../internal/utils'
import { DrawGrid } from '../DrawGrid'

/**
 * managing mouse down moving
 */
export abstract class BaseMouseDownMover {
  private _handler: EventHandler
  private _events: any
  private _started: boolean
  private _moved: boolean
  private _grid: DrawGrid
  private _mouseEndPoint: { x: number; y: number } | null
  public constructor(grid: DrawGrid) {
    this._grid = grid
    this._handler = new EventHandler()
    this._events = {}
    this._started = false
    this._moved = false
    this._mouseEndPoint = null
  }
  public get grid() {
    return this._grid
  }
  public moving(e: Event) {
    return !!this._started
  }
  public lastMoving(e: Event) {
    // 必要的控制以防止点击事件在mouseup后立即作出反应
    if (this.moving(e)) {
      return true
    }
    const last = this._mouseEndPoint
    if (!last) {
      return false
    }
    const pt = this._grid._getMouseAbstractPoint(e)
    return !!pt && pt.x === last.x && pt.y === last.y
  }
  public dispose() {
    this._handler.dispose()
  }
  protected _bindMoveAndUp(e: Event) {
    const events = this._events
    const handler = this._handler
    if (!event.isTouchEvent(e)) {
      events.mousemove = handler.on(document.body, 'mousemove', (event) =>
        this._mouseMove(event)
      )
      events.mouseup = handler.on(document.body, 'mouseup', (event) =>
        this._mouseUp(event)
      )
    } else {
      events.touchmove = handler.on(
        document.body,
        'touchmove',
        (event) => this._mouseMove(event),
        {
          passive: false,
        }
      )
      events.touchend = handler.on(document.body, 'touchend', (event) =>
        this._mouseUp(event)
      )
      events.touchcancel = handler.on(document.body, 'touchcancel', (event) =>
        this._mouseUp(event)
      )
    }
    this._started = true
    this._moved = false
  }
  protected _mouseMove(e: Event) {
    if (!event.isTouchEvent(e)) {
      if (event.getMouseButtons(e as MouseEvent) !== 1) {
        this._mouseUp(e)
        return
      }
    }
    this._moved =
      this._moveInternal(e) || this._moved /* calculation on after */

    event.cancel(e)
  }
  protected abstract _moveInternal(e: Event): boolean
  protected _mouseUp(e: Event) {
    const events = this._events
    const handler = this._handler
    handler.off(events.mousemove)
    handler.off(events.touchmove)
    handler.off(events.mouseup)
    handler.off(events.touchend)
    // handler.off(events.mouseleave);
    handler.off(events.touchcancel)

    this._upInternal(e)

    // 必要的控制以防止点击事件在mouseup后立即作出反应
    if (this._moved) {
      // 如果发生了移动
      this._mouseEndPoint = this._grid._getMouseAbstractPoint(e)
      setTimeout(() => {
        this._mouseEndPoint = null
        this._started = false
      }, 10)
    } else {
      this._started = false
    }

    event.cancel(e)
  }
  protected _upInternal(e: Event) {
    // nothing
  }
  protected _vibrate(e: Event) {
    if (navigator.vibrate && event.isTouchEvent(e)) {
      navigator.vibrate(50)
    }
  }
}
