import { CellContext, GridCanvasHelperAPI, ListGridAPI } from '../../ts-types'
import { isDef } from '../../internal/utils'
import { BaseHeader } from './BaseHeader'
import headerUtils from './headerUtils'
import { DrawCellInfo } from '../../ts-types-internal'
import { Style } from '../style/Style'

export class Header<T> extends BaseHeader<T> {
  public get StyleClass(): typeof Style {
    return Style
  }
  public drawInternal(
    value: string,
    context: CellContext,
    style: Style,
    helper: GridCanvasHelperAPI,
    _grid: ListGridAPI<T>,
    { drawCellBase, getHeaderIcon }: DrawCellInfo<T>
  ): void {
    const {
      textAlign,
      textBaseline,
      color,
      font,
      bgColor,
      textOverflow,
      padding,
      lineHeight,
      autoWrapText,
      lineClamp,
    } = style

    if (bgColor) {
      drawCellBase({
        bgColor,
      })
    }
    const v = isDef(value) ? value + '' : ''
    const multiLines = v
      .replace(/\r?\n/g, '\n')
      .replace(/\r/g, '\n')
      .split('\n')
    helper.testFontLoad(font, value, context)
    headerUtils.loadIcons(
      getHeaderIcon(),
      context,
      helper,
      (icons: any, ctx: CellContext) => {
        if (multiLines.length > 1) {
          helper.multilineText(multiLines, ctx, {
            color,
            font,
            icons,
            textAlign,
            textBaseline,
            textOverflow,
            padding,
            lineHeight,
            autoWrapText,
            lineClamp,
          })
        } else {
          helper.text(value, ctx, {
            color,
            font,
            icons,
            padding,
            textAlign,
            textBaseline,
            textOverflow,
          })
        }
      }
    )
  }
}
