import { MaybePromise, AnyFunction } from '../ts-types'
import { getCacheOrLoad } from '../internal/imgs'
import { isPromise } from '../internal/utils'
import { InlineDrawOption, Inline } from './Inline'

export type InlineImageConstructorOption = {
  src: MaybePromise<string>
  width?: number
  height?: number
  imageLeft?: number
  imageTop?: number
  imageWidth?: number
  imageHeight?: number
}

export class InlineImage extends Inline {
  private _src: MaybePromise<string>
  private _width?: number
  private _height?: number
  private _imageLeft?: number
  private _imageTop?: number
  private _imageWidth?: number
  private _imageHeight?: number
  private _onloaded: AnyFunction[]
  private _inlineImgPromise: MaybePromise<HTMLImageElement> | null = null
  private _inlineImg: HTMLImageElement | null = null
  public constructor({
    src,
    width,
    height,
    imageLeft,
    imageTop,
    imageWidth,
    imageHeight,
  }: InlineImageConstructorOption) {
    super()
    this._src = src
    this._width = width
    this._height = height
    this._imageLeft = imageLeft
    this._imageTop = imageTop
    this._imageWidth = imageWidth
    this._imageHeight = imageHeight

    this._onloaded = []

    if (isPromise(src)) {
      src.then((s) => {
        this._src = s
        this._loadImage(s)
      })
    } else {
      this._loadImage(src)
    }
  }
  public width(_arg: { ctx: CanvasRenderingContext2D }): number {
    return this._width || (this._inlineImg?.width ?? 0)
  }
  public font(): string | null {
    return null
  }
  public color(): string | null {
    return null
  }
  public canDraw(): boolean {
    return !!this._inlineImg
  }
  public onReady(callback: AnyFunction): void {
    if (isPromise(this._src) || isPromise(this._inlineImgPromise)) {
      this._onloaded.push(() => callback())
    }
  }
  public draw({
    ctx,
    canvasHelper,
    rect,
    offset,
    offsetLeft,
    offsetRight,
    offsetTop,
    offsetBottom,
  }: InlineDrawOption): void {
    const img = this._inlineImg as HTMLImageElement
    canvasHelper.drawInlineImageRect(
      ctx,
      img,
      this._imageLeft || 0,
      this._imageTop || 0,
      this._imageWidth || img.width,
      this._imageHeight || img.height,
      this._width || img.width,
      this._height || img.height,
      rect.left,
      rect.top,
      rect.width,
      rect.height,
      {
        offset: offset + 1,
        padding: {
          bottom: offsetBottom,
          left: offsetLeft,
          right: offsetRight,
          top: offsetTop,
        },
      }
    )
  }
  public canBreak(): boolean {
    return false
  }
  public toString(): string {
    return ''
  }
  private _loadImage(src: string) {
    const img = (this._inlineImgPromise = getCacheOrLoad(
      'InlineImage',
      50,
      src
    ))
    if (isPromise(img)) {
      img.then((i) => {
        this._inlineImg = i

        this._onloaded.forEach((fn) => fn())
      })
    } else {
      this._inlineImg = img
    }
  }
}
