// private methods
function _arrayEqual(a: any[], b: any[]): boolean {
  if (a.length !== b.length) {
    return false
  } else {
    for (let i = 0; i < a.length; i++) {
      if (a[i] !== b[i]) {
        return false
      }
    }
    return true
  }
}
function _getCellValues<T>(
  grid: ListGrid<T>,
  startCol: number,
  endCol: number,
  row: number
): any[] {
  const values = []
  if (startCol <= endCol) {
    for (let col = startCol; col <= endCol; col++) {
      values.push(_getCellValue(grid, col, row))
    }
  } else {
    for (let col = startCol; col >= endCol; col--) {
      values.push(_getCellValue(grid, col, row))
    }
  }
  return values
}
function _getBodyCellRange<T>(
  grid: ListGrid<T>,
  col: number,
  row: number
): CellRange | null {
  let cellRange = null
  const { spanBodyOptions, layoutMap } = grid[_]
  if (spanBodyOptions && row >= layoutMap.headerRowCount) {
    let startRow = row
    let endRow = row
    const { startCol, endCol } = spanBodyOptions
    if (
      (startCol <= col && col <= endCol) ||
      (endCol <= col && col <= startCol)
    ) {
      const values = _getCellValues(grid, startCol, col, row)
      while (startRow > layoutMap.headerRowCount) {
        const pValues = _getCellValues(grid, startCol, col, startRow - 1)
        if (_arrayEqual(values, pValues)) {
          startRow--
        } else {
          break
        }
      }
      while (endRow < grid.rowCount) {
        const nValues = _getCellValues(grid, startCol, col, endRow + 1)
        if (_arrayEqual(values, nValues)) {
          endRow++
        } else {
          break
        }
      }
    }
    cellRange = {
      start: {
        col,
        row: Math.min(startRow, endRow),
      },
      end: {
        col,
        row: Math.max(startRow, endRow),
      },
    }
  }
  return cellRange
}
function _getCellRange<T>(
  grid: ListGrid<T>,
  col: number,
  row: number
): CellRange {
  const { layoutMap } = grid[_]
  let cellRange = layoutMap.getCellRange(col, row)
  const bodyCellRange = _getBodyCellRange(grid, col, row)
  if (bodyCellRange) {
    cellRange = cellMerge(cellRange, bodyCellRange)
  }
  const p = {
    get col() {
      return col
    },
    get row() {
      return row
    },
    cellRange,
  }
  grid.fireListeners(LG_EVENT_TYPE.CELL_RANGE, p)
  return p.cellRange
}
function _updateRect<T>(
  grid: ListGrid<T>,
  col: number,
  row: number,
  context: CellContext
): void {
  context.setRectFilter((rect) => {
    let { left, right, top, bottom } = rect
    const {
      start: { col: startCol, row: startRow },
      end: { col: endCol, row: endRow },
    } = _getCellRange(grid, col, row)
    for (let c = col - 1; c >= startCol; c--) {
      left -= grid.getColWidth(c)
    }
    for (let c = col + 1; c <= endCol; c++) {
      right += grid.getColWidth(c)
    }
    for (let r = row - 1; r >= startRow; r--) {
      top -= grid.getRowHeight(r)
    }
    for (let r = row + 1; r <= endRow; r++) {
      bottom += grid.getRowHeight(r)
    }
    return Rect.bounds(left, top, right, bottom)
  })
  context.range = _getCellRange(grid, col, row) // !!! TODO
}
function _getCellValue<T>(
  grid: ListGrid<T>,
  col: number,
  row: number
): FieldData {
  let cellValue
  if (row < grid[_].layoutMap.headerRowCount) {
    const { caption } = grid[_].layoutMap.getHeader(col, row)
    cellValue = typeof caption === 'function' ? caption() : caption
  } else {
    const { field } = grid[_].layoutMap.getBody(col, row)
    cellValue = _getField(grid, field, row)
  }
  const p = {
    get col() {
      return col
    },
    get row() {
      return row
    },
    cellValue,
  }
  grid.fireListeners(LG_EVENT_TYPE.CELL_VALUE, p)
  return p.cellValue
}
function _setCellValue<T>(
  grid: ListGrid<T>,
  col: number,
  row: number,
  value: any
): MaybePromise<boolean> {
  if (row < grid[_].layoutMap.headerRowCount) {
    // nop
    return false
  } else {
    const { field } = grid[_].layoutMap.getBody(col, row)
    if (field == null) {
      return false
    }
    const index = _getRecordIndexByRow(grid, row)
    return grid[_].dataSource.setField(index, field, value)
  }
}
function _getContentHidden<T>(
  grid: ListGrid<T>,
  col: number,
  row: number
): boolean {
  let contentHidden = false
  if (row >= grid[_].layoutMap.headerRowCount) {
    const column = grid[_].layoutMap.getBody(col, row)
    if (column && column.contentHidden) {
      contentHidden = getOrApply(
        column.contentHidden,
        grid.getRowRecord(row) as any
      )
    }
  }
  return contentHidden
}
function _getCellMessage<T>(
  grid: ListGrid<T>,
  col: number,
  row: number
): FieldData {
  if (row < grid[_].layoutMap.headerRowCount) {
    return null
  } else {
    const column = grid[_].layoutMap.getBody(col, row)
    if (!column || !column.message) {
      return null
    }
    return _getField(grid, column.message as FieldDef<T>, row)
  }
}
function _getHeaderField<T>(grid: ListGrid<T>, field: any) {
  if (typeof field === 'function') {
    return field(grid.headerValues)
  } else {
    return grid.headerValues.get(field)
  }
}
function _hasHeaderField<T>(grid: ListGrid<T>, field: any): boolean {
  let has = false
  grid[_].layoutMap.headerObjects.forEach((cell) => {
    if (cell.field === field) {
      has = true
    }
  })
  return has
}
function _getCellIcon1<T>(
  grid: ListGrid<T>,
  icon: ColumnIconOption<T>
): ColumnIconOption<T>
function _getCellIcon1<T>(
  grid: ListGrid<T>,
  icon: ColumnIconOption<T>[]
): ColumnIconOption<T>[]
function _getCellIcon1<T>(
  grid: ListGrid<T>,
  icon: ColumnIconOption<T> | ColumnIconOption<T>[]
): ColumnIconOption<T> | ColumnIconOption<T>[]
function _getCellIcon1<T>(
  grid: ListGrid<T>,
  icon: ColumnIconOption<T> | ColumnIconOption<T>[]
): ColumnIconOption<T> | ColumnIconOption<T>[] {
  if (Array.isArray(icon)) {
    return icon.map((i) => _getCellIcon1(grid, i))
  }
  if (!obj.isObject(icon) || typeof icon === 'function') {
    return _getHeaderField(grid, icon)
  }
  const retIcon: any = {}
  const iconOpt: any = icon
  icons.iconPropKeys.forEach((k) => {
    if (iconOpt[k]) {
      const f = _getHeaderField(grid, iconOpt[k])
      if (isDef(f)) {
        retIcon[k] = f
      } else {
        if (!_hasHeaderField(grid, iconOpt[k])) {
          retIcon[k] = iconOpt[k]
        }
      }
    }
  })
  return retIcon
}
function _getHeaderIcon<T>(
  grid: ListGrid<T>,
  col: number,
  row: number
): ColumnIconOption<T> | ColumnIconOption<T>[] | null {
  const icon = grid[_].layoutMap.getHeader(col, row).icon
  if (icon == null) {
    return null
  }
  return _getCellIcon1(grid, icon)
}
function _getCellIcon0<T>(
  grid: ListGrid<T>,
  icon: ColumnIconOption<T>,
  row: number
): ColumnIconOption<T>
function _getCellIcon0<T>(
  grid: ListGrid<T>,
  icon: ColumnIconOption<T>[],
  row: number
): ColumnIconOption<T>[]
function _getCellIcon0<T>(
  grid: ListGrid<T>,
  icon: ColumnIconOption<T> | ColumnIconOption<T>[],
  row: number
): ColumnIconOption<T> | ColumnIconOption<T>[]
function _getCellIcon0<T>(
  grid: ListGrid<T>,
  icon: ColumnIconOption<T> | ColumnIconOption<T>[],
  row: number
): ColumnIconOption<T> | ColumnIconOption<T>[] {
  if (Array.isArray(icon)) {
    return icon.map((i) => _getCellIcon0(grid, i, row))
  }
  if (!obj.isObject(icon) || typeof icon === 'function') {
    return _getField(grid, icon, row)
  }
  const retIcon: any = {}
  const iconOpt: any = icon
  icons.iconPropKeys.forEach((k) => {
    if (iconOpt[k]) {
      const f = _getField(grid, iconOpt[k], row)
      if (isDef(f)) {
        retIcon[k] = f
      } else {
        if (!_hasField(grid, iconOpt[k], row)) {
          retIcon[k] = iconOpt[k]
        }
      }
    }
  })
  return retIcon
}
function _getCellIcon<T>(
  grid: ListGrid<T>,
  col: number,
  row: number
): ColumnIconOption<T> | ColumnIconOption<T>[] | null {
  const icon = grid[_].layoutMap.getBody(col, row).icon
  if (icon == null) {
    return null
  }
  return _getCellIcon0(grid, icon, row)
}
function _getField<T>(
  grid: ListGrid<T>,
  field: FieldDef<T> | undefined,
  row: number
): FieldData {
  if (!isDef(field)) {
    return null
  }

  if (row < grid[_].layoutMap.headerRowCount) {
    return null
  } else {
    const index = _getRecordIndexByRow(grid, row)
    return grid[_].dataSource.getField(index, field)
  }
}
function _hasField<T>(
  grid: ListGrid<T>,
  field: FieldDef<T>,
  row: number
): boolean {
  if (!isDef(field)) {
    return false
  }
  if (row < grid[_].layoutMap.headerRowCount) {
    return false
  } else {
    const index = _getRecordIndexByRow(grid, row)
    return grid[_].dataSource.hasField(index, field)
  }
}
function _onDrawValue<T>(
  grid: ListGrid<T>,
  cellValue: MaybePromise<unknown>,
  context: CellContext,
  { col, row }: CellAddress,
  style: ColumnStyleOption | null | undefined,
  draw: BaseColumn<T, any>['onDrawCell']
): MaybePromise<void> {
  const helper = grid[_].gridCanvasHelper
  const isEditing = !!getInlineEditingState(grid).cellRange

  const drawCellBg = ({
    bgColor,
  }: { bgColor?: ColorPropertyDefine } = {}): void => {
    const fillOpt = {
      fillColor: bgColor,
      isEditing,
    }
    // 绘制整个cell
    helper.fillCellWithState(context, fillOpt)
  }
  const drawCellBorder = () => {
    if (context.col === grid.frozenColCount - 1) {
      // 固定列规则
      const rect = context.getRect()
      helper.drawWithClip(context, (ctx: CanvasRenderingContext2D) => {
        const borderColor =
          context.row >= grid.frozenRowCount
            ? helper.theme.borderColor
            : helper.theme.frozenRowsBorderColor

        const borderColors = helper.toBoxArray(
          helper.getColor(borderColor, context.col, context.row, ctx)
        )
        if (borderColors[1]) {
          ctx.lineWidth = 1
          ctx.strokeStyle = borderColors[1]
          ctx.beginPath()
          ctx.moveTo(rect.right - 2.5, rect.top)
          ctx.lineTo(rect.right - 2.5, rect.bottom)
          ctx.stroke()
        }
      })
    }

    helper.borderSelection(context, {
      isEditing,
      highlightBorderWidth: toPx(
        grid.highlightBorderWidth,
        grid[_].calcWidthContext
      ),
    }) // !!! TODO
    // _borderWithState(grid, helper, context)
  }
  const clearCellBase = () => {
    helper.clearCell(context)
  }
  const drawCellBase = ({
    bgColor,
  }: { bgColor?: ColorPropertyDefine } = {}): void => {
    drawCellBg({ bgColor })
    drawCellBorder()
  }
  const info: DrawCellInfo<T> = {
    getRecord: () => grid.getRowRecord(row),
    getIcon: () => _getCellIcon(grid, col, row),
    getMessage: () => _getCellMessage(grid, col, row),
    messageHandler: grid[_].messageHandler,
    style,
    clearCellBase,
    drawCellBase,
    drawCellBg,
    drawCellBorder,
    getCell: () => ({ col, row }),
    getHeaderIcon: () => _getHeaderIcon(grid, col, row),
    getContentHidden: () => _getContentHidden(grid, col, row),
  }

  return draw(cellValue, info, context, grid)
}
