import {
  GridCanvasHelperAPI,
  CellContext,
  InlineAPI,
  InlineCheckOption,
} from '../../ts-types'
import { CheckHeaderState, GridInternal } from '../../ts-types-internal'
import { CheckHeaderStyle } from '../style/CheckHeaderStyle'
import { BaseCheckHeader } from './BaseCheckHeader'
import { getCheckHeaderStateId } from '../../internal/symbolManager'
import { obj } from '../../internal/utils'

const CHECK_HEADER_STATE_ID = getCheckHeaderStateId()

export class CheckHeader<T> extends BaseCheckHeader<T> {
  public get StyleClass(): typeof CheckHeaderStyle {
    return CheckHeaderStyle
  }
  public clone(): CheckHeader<T> {
    return new CheckHeader(this)
  }
  protected getState(grid: GridInternal<T>): CheckHeaderState {
    let state = grid[CHECK_HEADER_STATE_ID]
    if (!state) {
      state = { elapsed: {}, block: {} }
      obj.setReadonly(grid, CHECK_HEADER_STATE_ID, state)
    }
    return state
  }
  protected getInlineCheck(
    helper: GridCanvasHelperAPI,
    checked: boolean,
    context: CellContext,
    opt: InlineCheckOption
  ): InlineAPI {
    return helper.buildCheckBoxInline(checked, context, opt)
  }
}
