import {
  ListGridAPI,
  CellAddress,
  LayoutObjectId,
  EventListenerId,
} from '../../ts-types'
import { event, isPromise, obj } from '../../internal/utils'
import { DG_EVENT_TYPE } from '../../core/draw-grid/DG_EVENT_TYPE'
import { KEY_ENTER, KEY_SPACE } from '../../core/draw-grid/KEY_CODE'
import { getColumnActionStateId } from '../../internal/symbolManager'

const COLUMN_ACTION_STATE_ID = getColumnActionStateId()

export function bindCellClickAction<T>(
  grid: ListGridAPI<T>,
  cellId: LayoutObjectId,
  {
    action,
    mouseOver,
    mouseMove,
    mouseOut,
  }: {
    action: (cell: CellAddress) => void
    mouseOver: (cell: CellAddress, event: any) => boolean
    mouseMove?: (cell: CellAddress, event: any) => boolean
    mouseOut: (cell: CellAddress) => void
  }
): EventListenerId[] {
  function isTarget(col: number, row: number): boolean {
    return grid.getLayoutCellId(col, row) === cellId
  }

  if (!(grid as any)[COLUMN_ACTION_STATE_ID]) {
    obj.setReadonly(grid as any, COLUMN_ACTION_STATE_ID, {})
  }

  const actionState = (grid as any)[COLUMN_ACTION_STATE_ID]

  return [
    // click
    grid.listen(DG_EVENT_TYPE.CLICK_CELL, (e) => {
      if (!isTarget(e.col, e.row)) {
        return
      }
      if (isPromise(grid.getRowRecord(e.row))) {
        return
      }
      action({
        col: e.col,
        row: e.row,
      })
    }),
    // mouse move
    grid.listen(DG_EVENT_TYPE.MOUSEMOVE_CELL, (e) => {
      if (!isTarget(e.col, e.row)) {
        return
      }
      if (isPromise(grid.getRowRecord(e.row))) {
        return
      }
      if (mouseMove) {
        if (
          !mouseMove(
            {
              col: e.col,
              row: e.row,
            },
            e.event
          )
        ) {
          grid.getElement().style.cursor = ''
          actionState.mouseActive = false
          return
        }
      }
      grid.getElement().style.cursor = 'pointer'
      actionState.mouseActive = true
    }),
    grid.listen(DG_EVENT_TYPE.MOUSEOVER_CELL, (e) => {
      if (!isTarget(e.col, e.row)) {
        return
      }
      if (isPromise(grid.getRowRecord(e.row))) {
        return
      }
      if (mouseOver) {
        if (
          !mouseOver(
            {
              col: e.col,
              row: e.row,
            },
            e.event
          )
        ) {
          grid.getElement().style.cursor = ''
          actionState.mouseActive = false
          return
        }
      }
      grid.getElement().style.cursor = 'pointer'
      actionState.mouseActive = true
    }),
    grid.listen(DG_EVENT_TYPE.MOUSEOUT_CELL, (e) => {
      if (!isTarget(e.col, e.row)) {
        return
      }
      if (mouseOut) {
        mouseOut({
          col: e.col,
          row: e.row,
        })
      }
      grid.getElement().style.cursor = ''
      actionState.mouseActive = false
    }),
  ]
}
export function bindCellKeyAction<T>(
  grid: ListGridAPI<T>,
  cellId: LayoutObjectId,
  {
    action,
    acceptKeys = [],
  }: {
    action: (option: CellAddress) => void
    acceptKeys?: number[]
  }
) {
  function isTarget(col: number, row: number): boolean {
    return grid.getLayoutCellId(col, row) === cellId
  }
  acceptKeys = [...acceptKeys, KEY_ENTER, KEY_SPACE]
  return [
    // enter key down
    grid.listen(DG_EVENT_TYPE.KEYDOWN, (e) => {
      if (acceptKeys.indexOf(e.keyCode) === -1) {
        return
      }
      if (grid.keyboardOptions?.moveCellOnEnter && e.keyCode === KEY_ENTER) {
        // When moving with the enter key, no action is taken with the enter key.
        return
      }
      const sel = grid.selection.select
      if (!isTarget(sel.col, sel.row)) {
        return
      }
      if (isPromise(grid.getRowRecord(sel.row))) {
        return
      }
      action({
        col: sel.col,
        row: sel.row,
      })
      event.cancel(e.event)
    }),
  ]
}
