function get(name?: string): '$$$$private symbol$$$$' {
  // if (name) {
  //   return (mem[name] ? mem[name] : (mem[name] = Symbol(name))) as any;
  // } else {
  //   return Symbol() as any;
  // }
  return (name || '_') as any
}

export function getEventTargetSymbol(): '$$$$event_target_symbol$$$$' {
  return get('protected.event_target') as any
}
