import { SmallDialogInputEditor } from '../action/SmallDialogInputEditor'

export class ImmutableInputEditor<T> extends SmallDialogInputEditor<T> {
  public get disabled(): boolean | ((record: T) => boolean) {
    return this._disabled
  }
  public get readOnly(): boolean | ((record: T) => boolean) {
    return this._readOnly
  }
}
