import * as styleContents from '../style'
import {
  BaseColumnOption,
  CellAddress,
  CellContext,
  ColumnTypeAPI,
  EventListenerId,
  GridCanvasHelperAPI,
  LayoutObjectId,
  ListGridAPI,
  MaybePromise,
  Message,
  Convert,
} from '../../ts-types'
import {
  DrawCellInfo,
  ColumnFadeinState,
  GridInternal,
} from '../../ts-types-internal'
import {
  obj,
  isDef,
  getOrApply,
  isPromise,
  cellInRange,
} from '../../internal/utils'
import { BaseStyle } from '../style/BaseStyle'
import { Style } from '../style/Style'
import { animate } from '../../internal/animate'
import Rect from '../../internal/Rect'
import { getColumnFadeinStateId } from '../../internal/symbolManager'
import { getInlineEditingState } from '../utils'

const COLUMN_FADEIN_STATE_ID = getColumnFadeinStateId()

function getFadinState<T>(grid: GridInternal<T>): ColumnFadeinState {
  let state = grid[COLUMN_FADEIN_STATE_ID]
  if (!state) {
    state = { cells: {} }
    obj.setReadonly(grid, COLUMN_FADEIN_STATE_ID, state)
  }
  return state
}
function _generateFadinPointAction<T>(
  grid: ListGridAPI<T>,
  col: number,
  row: number,
  context: CellContext,
  drawInternal: () => void,
  drawCellBase: () => void
): (point: number) => void {
  return (point: number) => {
    const state = getFadinState(grid)
    const stateKey = `${col}:${row}`
    if (point === 1) {
      delete state.cells[stateKey]
    } else {
      state.cells[stateKey] = {
        opacity: point,
      }
    }
    drawCellBase()

    drawInternal()

    const cellState = state.cells[stateKey]
    if (cellState) {
      // 透明背景覆盖透明度
      const ctx = context.getContext()
      ctx.globalAlpha = 1 - cellState.opacity
      try {
        drawCellBase()
      } finally {
        ctx.globalAlpha = 1
      }
    }
  }
}
const fadinMgr = {
  animate<T>(
    grid: ListGridAPI<T>,
    col: number,
    row: number,
    context: CellContext,
    drawInternal: () => void,
    drawCellBase: () => void
  ): void {
    // fadein animation
    const state = getFadinState(grid)

    const activeFadeins = [
      _generateFadinPointAction(
        grid,
        col,
        row,
        context,
        drawInternal,
        drawCellBase
      ),
    ]
    state.activeFadeins = activeFadeins

    animate(500, (point) => {
      activeFadeins.forEach((f) => f(point))
      if (point === 1) {
        delete state.activeFadeins
      }
    })
  },
  margeAnimate<T>(
    grid: ListGridAPI<T>,
    col: number,
    row: number,
    context: CellContext,
    drawInternal: () => void,
    drawCellBase: () => void
  ): void {
    const state = getFadinState(grid)
    if (state.activeFadeins) {
      state.activeFadeins.push(
        _generateFadinPointAction(
          grid,
          col,
          row,
          context,
          drawInternal,
          drawCellBase
        )
      )
    } else {
      drawInternal()
    }
  },
}

export abstract class BaseColumn<T, V> implements ColumnTypeAPI {
  private _convert?: Convert
  private _convertCopy?: Convert
  private _fadeinWhenCallbackInPromise: boolean
  private _hidden?: boolean | ((record: T) => boolean)
  public constructor(options: BaseColumnOption<T> = {}) {
    this.onDrawCell = this.onDrawCell.bind(this) // 修复范围

    this._convert = options.convert
    this._convertCopy = options.convertCopy
    this._hidden = options.hidden

    // 使用Promise的回调显示淡入
    this._fadeinWhenCallbackInPromise =
      options.fadeinWhenCallbackInPromise || false
  }
  public get StyleClass(): typeof BaseStyle {
    return BaseStyle
  }
  public get fadeinWhenCallbackInPromise(): boolean | undefined {
    return this._fadeinWhenCallbackInPromise
  }
  public get convert(): Convert | undefined {
    return this._convert
  }
  public get convertCopy(): Convert | undefined {
    return this._convertCopy
  }
  public get hidden(): boolean | ((record: T) => boolean) | undefined {
    return this._hidden
  }
  public set hidden(hidden) {
    this._hidden = hidden
  }
  public reviseAttachCellsArea(
    _rect: Rect,
    _row: number,
    _grid: ListGridAPI<T>
  ) {
    // nothing
  }
  public reviseAttachCellsPadding(
    padding: [number, number, number, number],
    row: number,
    grid: ListGridAPI<T>
  ) {
    const state = getInlineEditingState(grid)
    if (state.inputPadding) {
      padding[0] += state.inputPadding[0]
      padding[1] += state.inputPadding[1]
      padding[2] += state.inputPadding[2]
      padding[3] += state.inputPadding[3]
    }
  }
  public reviseFocusRect(_rect: Rect, _row: number, _grid: ListGridAPI<T>) {
    // nothing
  }
  public onDrawCell(
    cellValue: MaybePromise<unknown>,
    info: DrawCellInfo<T>,
    context: CellContext,
    grid: ListGridAPI<T>
  ): void | Promise<void> {
    const {
      style,
      getRecord,
      clearCellBase,
      drawCellBase,
      getCell,
      getContentHidden,
    } = info

    const record = getRecord() as T
    const helper = grid.getGridCanvasHelper()
    const state = getInlineEditingState(grid)
    if (
      state.cellRange &&
      cellInRange(state.cellRange, context.col, context.row)
    ) {
      // 被编辑单元格
      drawCellBase({ bgColor: '#FFF' })
      const actStyle = styleContents.of(style, record, this.StyleClass)
      this.drawEditingInternal(context, actStyle, helper, grid, info)
      return
    }
    if (!state.cellRange) {
      // 在绘制单元格时，在编辑前，顺便保存当前焦点单元格的 inputPadding
      const sel = context.getSelection()
      const { col, row } = context
      if (
        !sel.dragged &&
        cellInRange(context.range, sel.select.col, sel.select.row) &&
        cellInRange(sel.range, col, row)
      ) {
        // 焦点单元格
        const actStyle = styleContents.of(style, record, this.StyleClass)
        if (actStyle instanceof Style && actStyle.inputPadding) {
          state.inputPadding = helper.toBoxPixelArray(
            actStyle.inputPadding,
            context,
            actStyle.font
          )
        } else {
          delete state.inputPadding
        }
      }
    }
    clearCellBase()

    let promise
    if (!record) {
      return // 过滤过程中 record 可能没有值
    } else if (isPromise(record)) {
      promise = record.then(() => cellValue)
    } else if (isPromise(cellValue)) {
      promise = cellValue
    }
    // 文字
    if (promise) {
      const start = Date.now()
      return promise.then((val: any) => {
        const currentContext = context.toCurrentContext()
        const drawRect = currentContext.getDrawRect()
        if (!drawRect) {
          return
        }
        const time = Date.now() - start

        const drawInternal = () => {
          const _currentContext = context.toCurrentContext()
          const _drawRect = _currentContext.getDrawRect()
          if (!_drawRect) {
            return
          }
          const _record = getRecord() as T
          if (isPromise(_record)) {
            return
          }

          const actStyle = styleContents.of(style, _record, this.StyleClass)
          if (actStyle.bgColor) {
            drawCellBase({ bgColor: actStyle.bgColor })
          } else {
            drawCellBase()
          }

          if (this.isContentHidden(_record, getContentHidden)) {
            return
          }

          this.drawInternal(
            this.doConvertInternal(val, getCell(), grid),
            _currentContext,
            actStyle,
            helper,
            grid,
            info
          )
          this.drawMessageInternal(
            info.getMessage(),
            context,
            actStyle,
            helper,
            grid,
            info
          )
        }

        let isFadeinWhenCallbackInPromise = this._fadeinWhenCallbackInPromise
        if (!isDef(isFadeinWhenCallbackInPromise)) {
          isFadeinWhenCallbackInPromise = !!grid.configure(
            'fadeinWhenCallbackInPromise'
          )
        }

        if (!isFadeinWhenCallbackInPromise) {
          drawInternal() // 简单的绘图
        } else {
          const col = context.col
          const row = context.row
          if (time < 80) {
            // 80 ms内的PromiseCallback被集成到之前的动画中
            fadinMgr.margeAnimate(
              grid,
              col,
              row,
              context,
              drawInternal,
              drawCellBase
            )
          } else {
            // 动画
            fadinMgr.animate(
              grid,
              col,
              row,
              context,
              drawInternal,
              drawCellBase
            )
          }
        }
      })
    } else {
      const actStyle = styleContents.of(style, record, this.StyleClass)
      if (actStyle.bgColor) {
        drawCellBase({ bgColor: actStyle.bgColor })
      } else {
        drawCellBase()
      }
      if (this.isContentHidden(record, getContentHidden)) {
        return
      }
      this.drawInternal(
        this.doConvertInternal(cellValue, getCell(), grid),
        context,
        actStyle,
        helper,
        grid,
        info
      )
      this.drawMessageInternal(
        info.getMessage(),
        context,
        actStyle,
        helper,
        grid,
        info
      )
      // 在淡入的情况下透明背景覆盖透明度
      const col = context.col
      const row = context.row
      const stateKey = `${col}:${row}`
      const cellState =
        (grid as any)[COLUMN_FADEIN_STATE_ID] &&
        (grid as any)[COLUMN_FADEIN_STATE_ID][stateKey]
      if (cellState) {
        const ctx = context.getContext()
        ctx.globalAlpha = 1 - cellState.opacity
        try {
          drawCellBase()
        } finally {
          ctx.globalAlpha = 1
        }
      }
      return
    }
  }
  public abstract clone(): BaseColumn<T, V>
  public abstract drawInternal(
    value: V,
    context: CellContext,
    style: BaseStyle,
    helper: GridCanvasHelperAPI,
    grid: ListGridAPI<T>,
    info: DrawCellInfo<T>
  ): void
  public drawMessageInternal(
    message: Message,
    context: CellContext,
    style: BaseStyle,
    helper: GridCanvasHelperAPI,
    grid: ListGridAPI<T>,
    info: DrawCellInfo<T>
  ): void {
    info.messageHandler.drawCellMessage(
      message,
      context,
      style,
      helper,
      grid,
      info
    )
  }
  public bindGridEvent(
    _grid: ListGridAPI<T>,
    _cellId: LayoutObjectId
  ): EventListenerId[] {
    return []
  }
  public getCopyCellValue(
    value: V,
    _grid: ListGridAPI<T>,
    _cell: CellAddress
  ): string {
    return value as any
  }
  public convertCopyValue(
    value: unknown,
    cell: CellAddress,
    grid: ListGridAPI<T>
  ) {
    let displayValue = this.convertCopyInternal(value)
    if (this.convertCopy) {
      displayValue = this.convertCopy(value, displayValue, cell, grid)
    }
    return displayValue
  }
  protected doConvertInternal(
    value: unknown,
    cell: CellAddress,
    grid: ListGridAPI<T>
  ) {
    let displayValue = this.convertInternal(value)
    if (this.convert) {
      displayValue = this.convert(value, displayValue, cell, grid)
    }
    return displayValue
  }
  protected convertInternal(value: unknown): V {
    return (isDef(value) ? value : '') as V
  }
  protected convertCopyInternal(value: unknown): V {
    return this.convertInternal(value)
  }
  protected drawEditingInternal(
    _context: CellContext,
    _style: BaseStyle,
    _helper: GridCanvasHelperAPI,
    _grid: ListGridAPI<T>,
    _info: DrawCellInfo<T>
  ): void {
    // nothing
  }
  private isContentHidden(record: T, getContentHidden: () => boolean) {
    let contentHidden = getOrApply(this.hidden, record)
    if (!isDef(contentHidden)) {
      contentHidden = getContentHidden()
    }
    return contentHidden
  }
}
