const indexFirst = (ary: number[], elm: number) => {
  let low = 0
  let high = ary.length - 1
  while (low <= high) {
    const i = Math.floor((low + high) / 2)
    if (ary[i] === elm) {
      return i
    } else if (ary[i] > elm) {
      high = i - 1
    } else {
      low = i + 1
    }
  }
  return high < 0 ? 0 : high
}

export default class NumberMap<T> {
  private _keys: number[]
  private _vals: { [key: number]: T }
  private _sorted: boolean
  public constructor() {
    this._keys = []
    this._vals = {}
    this._sorted = false
  }
  public put(key: number, value: T) {
    if (!(key in this._vals)) {
      this._keys.push(key)
      this._sorted = false
    }
    this._vals[key] = value
  }
  public get(key: number) {
    return this._vals[key]
  }
  public has(key: number) {
    return this._vals[key] !== null
  }
  public each(
    keyFrom: number,
    keyTo: number,
    fn: (value: T, key: number) => void
  ) {
    const keys = this._keys
    const length = keys.length
    if (!this._sorted) {
      keys.sort((a, b) => {
        if (a < b) {
          return -1
        }
        if (a > b) {
          return 1
        }
        return 0
      })
      this._sorted = true
    }

    for (let i = indexFirst(keys, keyFrom); i < length; i++) {
      const key = keys[i]
      if (keyFrom <= key && key <= keyTo) {
        fn(this.get(key), key)
      } else if (keyTo < key) {
        return
      }
    }
  }
}
