import { icons } from '../../internal/icons'
import {
  IconColumnOption,
  ColumnIconOption,
  ListGridAPI,
  GridCanvasHelperAPI,
  CellContext,
} from '../../ts-types'
import { DrawCellInfo } from '../../ts-types-internal'
import { IconStyle } from '../style/IconStyle'
import { Column } from './Column'

function repeatArray(
  val: ColumnIconOption<unknown>,
  count: number
): ColumnIconOption<unknown>[] {
  if (count === Infinity) {
    count = 0
  }
  const a = []
  for (let i = 0; i < count; i++) {
    a.push(val)
  }
  return a
}

export class IconColumn<T> extends Column<T> {
  private _tagName: string
  private _className?: string
  private _content?: string
  private _name?: string
  private _iconWidth?: number
  public constructor(options: IconColumnOption<T> = {}) {
    super(options)
    this._tagName = options.tagName || 'i'
    this._className = options.className
    this._content = options.content
    this._name = options.name
    this._iconWidth = options.iconWidth
  }
  public get StyleClass(): typeof IconStyle {
    return IconStyle
  }
  public clone(): IconColumn<T> {
    return new IconColumn(this)
  }
  public drawInternal(
    value: string,
    context: CellContext,
    style: IconStyle,
    helper: GridCanvasHelperAPI,
    grid: ListGridAPI<T>,
    info: DrawCellInfo<T>
  ) {
    const num = Number(value)
    if (!isNaN(num)) {
      const icon: IconColumnOption<T> & { width?: number } = {}
      icons.iconPropKeys.forEach((k) => {
        ;(icon as any)[k] = (style as any)[k]
      })
      icon.className = this._className
      icon.tagName = this._tagName
      icon.content = this._content
      icon.name = this._name
      if (this._iconWidth) {
        icon.width = this._iconWidth
      }

      info.getIcon = (): ColumnIconOption<unknown>[] =>
        repeatArray(icon as ColumnIconOption<unknown>, num)
    } else {
      info.getIcon = (): null => null
    }
    super.drawInternal('', context, style, helper, grid, info)
  }
}
