import { BaseCheckStyle } from './BaseCheckStyle'
import { SwitchHeaderStyleOption } from '../../ts-types'

let defaultSwitchHeaderStyle: SwitchHeaderStyle
export class SwitchHeaderStyle extends BaseCheckStyle {
  public static get DEFAULT(): SwitchHeaderStyle {
    return defaultSwitchHeaderStyle
      ? defaultSwitchHeaderStyle
      : (defaultSwitchHeaderStyle = new SwitchHeaderStyle())
  }
  public constructor(style: SwitchHeaderStyleOption = {}) {
    super(style)
  }
  public clone(): SwitchHeaderStyle {
    return new SwitchHeaderStyle(this)
  }
}
