import { NumberStyleOption } from '../../ts-types'
import { Style } from './Style'
import { defaults } from '../../internal/utils'

let defaultNumberStyle: NumberStyle

export class NumberStyle extends Style {
  public static get DEFAULT() {
    return defaultNumberStyle
      ? defaultNumberStyle
      : (defaultNumberStyle = new NumberStyle())
  }
  public constructor(style: NumberStyleOption = {}) {
    super(defaults(style, { textAlign: 'right' }))
  }
  public clone(): NumberStyle {
    return new NumberStyle(this)
  }
}
