import { NumberColumnOption } from '../../ts-types'
import { NumberStyle } from '../style/NumberStyle'
import { Column } from './Column'

let defaultFormat: Intl.NumberFormat

export class NumberColumn<T> extends Column<T> {
  public static get defaultFormat(): Intl.NumberFormat {
    return defaultFormat || (defaultFormat = new Intl.NumberFormat())
  }
  public static set defaultFormat(fmt) {
    defaultFormat = fmt
  }
  private _format?: Intl.NumberFormat
  public constructor(options: NumberColumnOption<T> = {}) {
    super(options)
    this._format = options.format
  }
  public get StyleClass(): typeof NumberStyle {
    return NumberStyle
  }
  public get format(): Intl.NumberFormat | undefined {
    return this._format
  }
  public clone(): NumberColumn<T> {
    return new NumberColumn(this)
  }
  public withFormat(format: Intl.NumberFormat): NumberColumn<T> {
    const c = this.clone()
    c._format = format
    return c
  }
  protected convertInternal(value: unknown): string {
    let s: any = (value + '').trim()
    if (s) {
      s = s - 0
      if (isNaN(s)) {
        s = ''
      } else {
        s = (this._format || NumberColumn.defaultFormat).format(s)
      }
    }
    return s
  }
}
