import { ColorDef, TextOverflow, StyleOption, Appearance } from '../../ts-types'
import { BaseStdStyle } from './BaseStdStyle'

let defaultStyle: Style

export class Style extends BaseStdStyle {
  public static get DEFAULT(): Style {
    return defaultStyle ? defaultStyle : (defaultStyle = new Style())
  }
  private _color?: ColorDef
  private _font?: string
  private _padding?: number | string | (number | string)[]
  private _inputPadding?: number | string | (number | string)[]
  private _textOverflow: TextOverflow
  private _appearance: Appearance
  public constructor(style: StyleOption = {}) {
    super(style)
    this._color = style.color
    this._font = style.font
    this._padding = style.padding
    this._inputPadding = style.inputPadding
    this._textOverflow = style.textOverflow || 'clip'
    this._appearance = style.appearance || 'none'
  }
  public get color(): ColorDef | undefined {
    return this._color
  }
  public set color(color) {
    this._color = color
    this.doChangeStyle()
  }
  public get font(): string | undefined {
    return this._font
  }
  public set font(font) {
    this._font = font
    this.doChangeStyle()
  }
  public get padding(): number | string | (number | string)[] | undefined {
    return this._padding
  }
  public set padding(padding) {
    this._padding = padding
    this.doChangeStyle()
  }
  public get inputPadding(): number | string | (number | string)[] | undefined {
    return this._inputPadding
  }
  public set inputPadding(inputPadding) {
    this._inputPadding = inputPadding
    this.doChangeStyle()
  }
  public get textOverflow(): TextOverflow {
    return this._textOverflow
  }
  public set textOverflow(textOverflow) {
    this._textOverflow = textOverflow
    this.doChangeStyle()
  }
  public get appearance(): Appearance {
    return this._appearance
  }
  public set appearance(appearance) {
    this._appearance = appearance
    this.doChangeStyle()
  }
  public clone(): Style {
    return new Style(this)
  }
}
