import {
  ListGridAPI,
  EventListenerId,
  BaseHeaderActionOption,
  CellAddress,
  LayoutObjectId,
} from '../../ts-types'

export interface IBaseHeaderActionOptions<T> {
  disabled?: boolean | ((grid: ListGridAPI<T>, cell: CellAddress) => boolean)
}

export class BaseAction<T> {
  protected _disabled:
    | boolean
    | ((grid: ListGridAPI<T>, cell: CellAddress) => boolean)
  public constructor(option: BaseHeaderActionOption<T> = {}) {
    this._disabled = option.disabled || false
  }
  public get disabled():
    | boolean
    | ((grid: ListGridAPI<T>, cell: CellAddress) => boolean) {
    return this._disabled
  }
  public set disabled(disabled) {
    if (this._disabled !== disabled) {
      this._disabled = disabled
      this.onChangeDisabledInternal()
    }
  }
  public clone(): BaseAction<T> {
    return new BaseAction(this)
  }
  public bindGridEvent(
    _grid: ListGridAPI<T>,
    _cellId: LayoutObjectId
  ): EventListenerId[] {
    return []
  }
  public onChangeDisabledInternal(): void {
    // nothing
  }
}
