import { DateStyleOption } from '../../ts-types'
import { Style } from './Style'

function adjustDateTimeStyleOption(style: DateStyleOption): DateStyleOption {
  style.textAlign = style.textAlign || 'right'
  return style
}

let defaultDateStyle: DateStyle

export class DateStyle extends Style {
  public static get DEFAULT(): DateStyle {
    return defaultDateStyle
      ? defaultDateStyle
      : (defaultDateStyle = new DateStyle())
  }
  public constructor(style: DateStyleOption = {}) {
    super(adjustDateTimeStyleOption(style))
  }
  public clone(): DateStyle {
    return new DateStyle(this)
  }
}
