export class DrawLayer {
  private _level: number
  private _list: ((ctx: CanvasRenderingContext2D) => void)[]
  public constructor(level: number) {
    this._level = level
    this._list = []
  }
  public get level() {
    return this._level
  }
  public addDraw(fn: (ctx: CanvasRenderingContext2D) => void) {
    this._list.push(fn)
  }
  public draw(ctx: CanvasRenderingContext2D) {
    this._list.forEach((fn) => {
      ctx.save()
      try {
        fn(ctx)
      } finally {
        ctx.restore()
      }
    })
  }
}
