import { GridInternal, CheckHeaderState } from '../../ts-types-internal'
import { obj } from '../../internal/utils'
import { BaseCheckAction } from './BaseCheckAction'
import { getSwitchHeaderStateId } from '../../internal/symbolManager'

const SWITCH_HEADER_STATE_ID = getSwitchHeaderStateId()

export class SwitchHeaderAction<T> extends BaseCheckAction<T> {
  public clone(): SwitchHeaderAction<T> {
    return new SwitchHeaderAction(this)
  }
  protected getState(grid: GridInternal<T>): CheckHeaderState {
    let state = grid[SWITCH_HEADER_STATE_ID]
    if (!state) {
      state = { elapsed: {}, block: {} }
      obj.setReadonly(grid, SWITCH_HEADER_STATE_ID, state)
    }
    return state
  }
}
