import { ButtonActionOption } from '../../ts-types'
import { obj } from '../../internal/utils'
import { Action } from './Action'
import { getButtonColumnStateId } from '../../internal/symbolManager'

const BUTTON_COLUMN_STATE_ID = getButtonColumnStateId()

export class ButtonAction<T> extends Action<T> {
  public constructor(options: ButtonActionOption<T> = {}) {
    super(options)
  }
  public getState(grid: any): any {
    if (!grid[BUTTON_COLUMN_STATE_ID]) {
      obj.setReadonly(grid, BUTTON_COLUMN_STATE_ID, {})
    }
    return grid[BUTTON_COLUMN_STATE_ID]
  }
}
