  function vmove(this: DrawGrid, vDir: 'N' | 'S'): boolean {
    let row: number
    if (vDir === 'S') {
      row = this.getMoveDownRowByKeyDownInternal(focusCell)
      if (this.rowCount <= row) {
        return false
      }
    } else {
      row = this.getMoveUpRowByKeyDownInternal(focusCell)
      if (row < 0) {
        return false
      }
    }
    const { col } = focusCell
    this._moveFocusCell(col, row, shiftKey)
    return true
  }

