      const abstractPos = this._getMouseAbstractPoint(e as TouchEvent)
      if (!abstractPos) {
        return
      }

      if (_getResizeSelectionAt.call(this, abstractPos.x, abstractPos.y)) {
        this[_].selectionResizer.start(e as TouchEvent)
      } else {
        const resizeCol = _getResizeColAt.call(
          this,
          abstractPos.x,
          abstractPos.y,
          15
        )
        if (resizeCol >= 0 && canResizeColumn(resizeCol)) {
          // 改变宽度
          this[_].columnResizer.start(resizeCol, e)
        } else {
          // 选择
          this[_].cellSelector.start(e as TouchEvent)
        }
      }
