import { DrawLayer } from './DrawLayer'

/**
 * This class manages the drawing process for each layer
 */
export class DrawLayers {
  private _layers: {
    [key: number]: DrawLayer
  }
  public constructor() {
    this._layers = {}
  }
  public addDraw(level: number, fn: (ctx: CanvasRenderingContext2D) => void) {
    const l =
      this._layers[level] || (this._layers[level] = new DrawLayer(level))
    l.addDraw(fn)
  }
  public draw(ctx: CanvasRenderingContext2D) {
    const list = []
    for (const k in this._layers) {
      if (this._layers.hasOwnProperty(k)) {
        list.push(this._layers[k])
      }
    }
    list.sort((a, b) => a.level - b.level)
    list.forEach((l) => l.draw(ctx))
  }
}
