import { BaseCheckStyle } from './BaseCheckStyle'
import { CheckHeaderStyleOption } from '../../ts-types'

let defaultCheckHeaderStyle: CheckHeaderStyle
export class CheckHeaderStyle extends BaseCheckStyle {
  public static get DEFAULT(): CheckHeaderStyle {
    return defaultCheckHeaderStyle
      ? defaultCheckHeaderStyle
      : (defaultCheckHeaderStyle = new CheckHeaderStyle())
  }
  public constructor(style: CheckHeaderStyleOption = {}) {
    super(style)
  }
  public clone(): CheckHeaderStyle {
    return new CheckHeaderStyle(this)
  }
}
