import { drawExclamationMarkBox } from './messageUtils'
import {
  MessageObject,
  CellContext,
  ColumnStyle,
  GridCanvasHelperAPI,
  ListGridAPI,
} from '../../ts-types'
import { BaseMessage } from './BaseMessage'
import { DrawCellInfo } from '../../ts-types-internal'
import { WarningMessageElement } from './internal/WarningMessageElement'
import { cellInRange } from '../../internal/utils'

const DEEP_ORANGE_A100 = '#ff9e80'

export class WarningMessage<T> extends BaseMessage<T> {
  public createMessageElementInternal(): WarningMessageElement {
    return new WarningMessageElement()
  }
  public drawCellMessageInternal(
    _message: MessageObject,
    context: CellContext,
    style: ColumnStyle,
    helper: GridCanvasHelperAPI,
    grid: ListGridAPI<T>,
    _info: DrawCellInfo<T>
  ): void {
    const { bgColor } = style
    const { select } = context.getSelection()
    if (
      !cellInRange(
        grid.getCellRange(context.col, context.row),
        select.col,
        select.row
      ) ||
      !grid.hasFocusGrid()
    ) {
      helper.drawBorderWithClip(context, (ctx) => {
        drawExclamationMarkBox(
          context,
          {
            bgColor: DEEP_ORANGE_A100,
            color: bgColor,
          },
          helper
        )
      })
    }
  }
}
