import { TreeStyleOption, ColorDef, LineClamp } from '../../ts-types'
import { Style } from './Style'

function adjustTreeStyleOption(style: TreeStyleOption) {
  style.textAlign = style.textAlign || 'left'
  return style
}

let defaultTreeStyle: TreeStyle

export class TreeStyle extends Style {
  public static get DEFAULT(): TreeStyle {
    return defaultTreeStyle
      ? defaultTreeStyle
      : (defaultTreeStyle = new TreeStyle())
  }
  private _lineColor?: ColorDef
  private _buttonColor?: ColorDef
  private _buttonBgColor?: ColorDef
  private _buttonBorderColor?: ColorDef
  private _linkColor?: ColorDef
  private _lineHeight: string | number
  private _autoWrapText: boolean
  private _lineClamp: LineClamp
  public constructor(style: TreeStyleOption = {}) {
    super(adjustTreeStyleOption(style))
    this._lineColor = style.lineColor
    this._buttonColor = style.buttonColor
    this._buttonBgColor = style.buttonBgColor
    this._buttonBorderColor = style.buttonBorderColor
    this._linkColor = style.linkColor
    this._lineHeight = style.lineHeight || '1em'
    this._autoWrapText = style.autoWrapText || false
    this._lineClamp = style.lineClamp || 'auto'
  }
  public get lineColor(): ColorDef | undefined {
    return this._lineColor
  }
  public set lineColor(lineColor) {
    this._lineColor = lineColor
    this.doChangeStyle()
  }
  public get buttonColor(): ColorDef | undefined {
    return this._buttonColor
  }
  public set buttonColor(buttonColor) {
    this._buttonColor = buttonColor
    this.doChangeStyle()
  }
  public get buttonBgColor(): ColorDef | undefined {
    return this._buttonBgColor
  }
  public set buttonBgColor(buttonBgColor) {
    this._buttonBgColor = buttonBgColor
    this.doChangeStyle()
  }
  public get buttonBorderColor(): ColorDef | undefined {
    return this._buttonBorderColor
  }
  public set buttonBorderColor(buttonBorderColor) {
    this._buttonBorderColor = buttonBorderColor
    this.doChangeStyle()
  }
  public get linkColor(): ColorDef | undefined {
    return this._linkColor
  }
  public set linkColor(linkColor) {
    this._linkColor = linkColor
    this.doChangeStyle()
  }
  public get lineHeight(): string | number {
    return this._lineHeight
  }
  public set lineHeight(lineHeight) {
    this._lineHeight = lineHeight
    this.doChangeStyle()
  }
  public get lineClamp(): LineClamp {
    return this._lineClamp
  }
  public set lineClamp(lineClamp) {
    this._lineClamp = lineClamp
    this.doChangeStyle()
  }
  public get autoWrapText(): boolean {
    return this._autoWrapText
  }
  public set autoWrapText(autoWrapText) {
    this._autoWrapText = autoWrapText
    this.doChangeStyle()
  }
  public clone(): TreeStyle {
    return new TreeStyle(this)
  }
}
