import { BaseStyleOption, ColorDef, ColumnStyle } from '../../ts-types'
import { EventTarget } from '../../core/EventTarget'

const HEADER_BASE_STYLE_EVENT_TYPE = {
  CHANGE_STYLE: 'change_style',
}

let defaultHeaderBaseStyle: BaseStyle

export class BaseStyle extends EventTarget implements ColumnStyle {
  public static get EVENT_TYPE(): { CHANGE_STYLE: string } {
    return HEADER_BASE_STYLE_EVENT_TYPE
  }
  public static get DEFAULT(): BaseStyle {
    return defaultHeaderBaseStyle
      ? defaultHeaderBaseStyle
      : (defaultHeaderBaseStyle = new BaseStyle())
  }
  private _bgColor?: ColorDef
  public constructor({ bgColor }: BaseStyleOption = {}) {
    super()
    this._bgColor = bgColor
  }
  public get bgColor(): ColorDef | undefined {
    return this._bgColor
  }
  public set bgColor(bgColor) {
    this._bgColor = bgColor
    this.doChangeStyle()
  }
  public doChangeStyle(): void {
    this.fireListeners(HEADER_BASE_STYLE_EVENT_TYPE.CHANGE_STYLE)
  }
  public clone(): BaseStyle {
    return new BaseStyle(this)
  }
}
