import {
  PercentCompleteBarColumnOption,
  GridCanvasHelperAPI,
  CellContext,
  ListGridAPI,
} from '../../ts-types'
import { DrawCellInfo } from '../../ts-types-internal'
import { str, getOrApply } from '../../internal/utils'
import { PercentCompleteBarStyle } from '../style/PercentCompleteBarStyle'
import { Column } from './Column'

const MARGIN = 2

export class PercentCompleteBarColumn<T> extends Column<T> {
  private _min: number
  private _max: number
  private _formatter: (value: string) => string
  public constructor(options: PercentCompleteBarColumnOption<T> = {}) {
    super(options)
    this._min = options.min || 0
    this._max = options.max || this._min + 100
    this._formatter = options.formatter || ((v) => v)
  }
  public get StyleClass(): typeof PercentCompleteBarStyle {
    return PercentCompleteBarStyle
  }
  public clone(): PercentCompleteBarColumn<T> {
    return new PercentCompleteBarColumn(this)
  }
  public drawInternal(
    value: string,
    context: CellContext,
    style: PercentCompleteBarStyle,
    helper: GridCanvasHelperAPI,
    grid: ListGridAPI<T>,
    info: DrawCellInfo<T>
  ): void {
    super.drawInternal(
      this._formatter(value),
      context,
      style,
      helper,
      grid,
      info
    )
    const barColor = style.barColor
    const barBgColor = style.barBgColor
    const barHeight = style.barHeight

    let sValue = `${value}`
    if (str.endsWith(sValue, '%')) {
      sValue = sValue.substr(0, sValue.length - 1)
    }
    const num = Number(sValue)
    const rate =
      num < this._min
        ? 0
        : num > this._max
        ? 1
        : (num - this._min) / (this._max - this._min)

    helper.drawWithClip(context, (ctx) => {
      const rect = context.getRect()

      const barMaxWidth = rect.width - MARGIN * 2 - 1 /*边线*/
      const barTop = rect.bottom - MARGIN - barHeight - 1 /*边线*/
      const barLeft = rect.left + MARGIN
      ctx.fillStyle = getOrApply(barBgColor, rate * 100) || '#f0f3f5'
      ctx.beginPath()
      ctx.rect(barLeft, barTop, barMaxWidth, barHeight)
      ctx.fill()

      const barSize = Math.min(barMaxWidth * rate, barMaxWidth)
      ctx.fillStyle = getOrApply(barColor, rate * 100) || '#20a8d8'
      ctx.beginPath()
      ctx.rect(barLeft, barTop, barSize, barHeight)
      ctx.fill()
    })
  }
}
