import { HeaderActionOption, SortOption } from '../ts-types'
import { BaseAction } from './action/BaseAction'
import { BaseCheckAction } from './action/BaseCheckAction'
import { CheckHeaderAction } from './action/CheckHeaderAction'
import { SwitchHeaderAction } from './action/SwitchHeaderAction'
import { SortHeaderAction } from './action/SortHeaderAction'
import { ImmutableCheckHeaderAction } from './immutable/ImmutableCheckHeaderAction'
import { ImmutableSortHeaderAction } from './immutable/ImmutableSortHeaderAction'
import { ImmutableSwitchHeaderAction } from './immutable/ImmutableSwitchHeaderAction'
import { BaseHeaderDefine } from '../list-grid/layout-map/api'

export interface ImmutableHeaderActions {
  SORT: ImmutableSortHeaderAction<any>
  CHECK: ImmutableCheckHeaderAction<any>
  SWITCH: ImmutableSwitchHeaderAction<any>
}

export const ACTIONS: ImmutableHeaderActions = {
  SORT: new ImmutableSortHeaderAction<any>(),
  CHECK: new ImmutableCheckHeaderAction<any>(),
  SWITCH: new ImmutableSwitchHeaderAction<any>(),
}

export {
  BaseAction,
  BaseCheckAction,
  SortHeaderAction,
  CheckHeaderAction,
  SwitchHeaderAction,
}

export function of<T>(
  headerAction: HeaderActionOption | BaseAction<T> | null | undefined
): BaseAction<T> | undefined {
  if (!headerAction) {
    return undefined
  } else if (typeof headerAction === 'string') {
    const key = headerAction.toUpperCase() as keyof typeof ACTIONS
    return ACTIONS[key] || of(null)
  } else {
    return headerAction
  }
}

export function ofCell<T>(
  headerCell: BaseHeaderDefine<T>
): BaseAction<T> | undefined {
  if (headerCell.sort) {
    if (typeof headerCell.sort === 'function') {
      const sortMethod = headerCell.sort
      // 0.9.0 Backward compatibility
      const sort: SortOption<T> = ({ order, col, grid }): void =>
        sortMethod.call(headerCell, order, col, grid)
      return new ImmutableSortHeaderAction({ sort } as any)
    }
    return ACTIONS.SORT
  }
  return of(headerCell.headerAction)
}
