import { CheckHeaderState, GridInternal } from '../../ts-types-internal'
import { getSwitchColumnStateId } from '../../internal/symbolManager'
import { obj } from '../../internal/utils'
import { BaseCheckEditor } from './BaseCheckEditor'

const SWITCH_COLUMN_STATE_ID = getSwitchColumnStateId()

export class SwitchEditor<T> extends BaseCheckEditor<T> {
  public clone(): SwitchEditor<T> {
    return new SwitchEditor(this)
  }
  protected getState(grid: GridInternal<T>): CheckHeaderState {
    let state = grid[SWITCH_COLUMN_STATE_ID]
    if (!state) {
      state = { block: {}, elapsed: {} }
      obj.setReadonly(grid, SWITCH_COLUMN_STATE_ID, state)
    }
    return state
  }
}
