import { ButtonStyleOption, ColorDef } from '../../ts-types'
import { Style } from './Style'
import { defaults } from '../../internal/utils'

let defaultButtonStyle: ButtonStyle

export class ButtonStyle extends Style {
  public static get DEFAULT() {
    return defaultButtonStyle
      ? defaultButtonStyle
      : (defaultButtonStyle = new ButtonStyle())
  }
  private _buttonBgColor?: ColorDef
  public constructor(style: ButtonStyleOption = {}) {
    super(defaults(style, { textAlign: 'center' }))
    this._buttonBgColor = style.buttonBgColor
  }
  public get buttonBgColor(): ColorDef | undefined {
    return this._buttonBgColor
  }
  public set buttonBgColor(buttonBgColor) {
    this._buttonBgColor = buttonBgColor
    this.doChangeStyle()
  }
  public clone(): ButtonStyle {
    return new ButtonStyle(this)
  }
}
